const { ccclass, property } = cc._decorator;

/** */
enum LabelFixedType {
    NONE, // 
    WIDTH, //
    BYTE, //  1,2
    FONT_SIZE, //  shrink 
}
/** cc.Label  */
@ccclass
export default class ALabelFixed extends cc.Label {
    @property({
        type: cc.Enum(LabelFixedType),
        serializable: true,
        displayName: "",
        tooltip: "WIDTH\nBYTE  1,2  ",
    })
    fixedType: LabelFixedType = LabelFixedType.NONE; //

    @property({ serializable: true, displayName: "" })
    fixedValue: number = 10; // width   byte

    @property({
        serializable: true,
        displayName: " ...",
        tooltip: " 123456   123...",
    })
    fixedString: string = "..."; // 123456   123...

    private aLabel: string = "";
    public oldLabel: string = "";
    private isRefresh: boolean;

    private set _string(label: string) {
        this.aLabel = label;
        this.refreshView();
    }
    private get _string(): string {
        return this.aLabel;
    }

    protected start(): void {
        this.refreshView();
    }

    private refreshView(): void {
        if (this.isRefresh || !this.node) {
            return;
        }
        this.isRefresh = true;
        switch (this.fixedType) {
            case LabelFixedType.WIDTH:
                this.fixedWidthLengthModel();
                break;
            case LabelFixedType.BYTE:
                this.fixedByteLengthModel();
                break;
            case LabelFixedType.FONT_SIZE:
                this.fixedFontSizeModel();
                break;
        }
        this.isRefresh = false;
    }

    /** */
    private fixedWidthLengthModel() {
        let str = (this.oldLabel = this.aLabel);
        let len;

        if (this.fixedValue < this.fontSize) {
            //
            this.fixedValue = this.fontSize;
        }
        this["_forceUpdateRenderData"]();
        len = this.node.width;
        len > this.fixedValue && this.labelWidth(str);
    }

    /** */
    private fixedByteLengthModel() {
        let str = (this.oldLabel = this.aLabel);
        let len;

        if (this.fixedValue < 2) {
            //
            this.fixedValue = 2;
        }
        len = this.getLabelByteLength(str);
        len > this.fixedValue && this.labelByte(str);
    }

    /**  shrink  */
    private fixedFontSizeModel() {
        let str = (this.oldLabel = this.aLabel);
        //SHRINK
        if (this.overflow != cc.Label.Overflow.SHRINK) {
            this.overflow = cc.Label.Overflow.SHRINK;
        }

        this["_forceUpdateRenderData"]();
        this.actualFontSize < this.fixedValue && this.labelFontSize(str + this.fixedString);
    }

    /** */
    private labelFontSize(label: string): void {
        let str: string;
        let orgStr = label.split(this.fixedString)[0];
        let len = orgStr.length - 1;
        for (var i = len; i > 0; i--) {
            str = orgStr.substr(0, i);
            this.string = str + this.fixedString;
            this["_forceUpdateRenderData"]();
            if (this.actualFontSize >= this.fixedValue) return;
        }
    }

    /** */
    private labelWidth(label: string): void {
        let len = label.length - 1;
        let str: string;
        for (var i = len; i > 0; i--) {
            str = label.substr(0, i);
            this.string = str;
            this["_forceUpdateRenderData"]();
            if (this.node.width < this.fixedValue) {
                this.string = str + this.fixedString;
                return;
            }
        }
    }

    /** */
    private labelByte(label: string): void {
        let len = label.length - 1;
        let str: string;
        for (var i = len; i > 0; i--) {
            str = label.substr(0, i);
            if (this.getLabelByteLength(str) <= this.fixedValue) {
                this.string = str + this.fixedString;
                return;
            }
        }
    }

    /**  */
    private getLabelByteLength(label: string, replaceStr: string = "**"): number {
        return label.replace(/[^\x00-\xff]/g, replaceStr).length;
    }
}
