import Listener from "../../../../main/core/utils/Listener";
import CommonEventName from "./CommonEventName";

const DEFALUT_NAVITEM_INDEX: string = "0";

export class DataCacheMgr {
    private static _instance: DataCacheMgr = null;

    private loginBonusData: ILoginBonusData = null;
    /** item "0"*/
    private navItemIndex: string = DEFALUT_NAVITEM_INDEX;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new DataCacheMgr();
        }

        return this._instance;
    }

    private constructor() {}

    public setLoginBonusData(loginBonusData: ILoginBonusData) {
        this.loginBonusData = loginBonusData;
        Listener.send(CommonEventName.LOGIN_BONUS_MSG, this.loginBonusData);
    }

    public getLoginBonusData() {
        return this.loginBonusData;
    }

    public clearLoginBonusData() {
        this.loginBonusData = null;
    }

    public setNavItemIndex(index: string) {
        this.navItemIndex = index;
    }

    public getNavItemIndex(): string {
        return this.navItemIndex;
    }

    public initDataCache() {
        this.setNavItemIndex(DEFALUT_NAVITEM_INDEX);
    }
}
