import { GameType } from "../../../../../../main/core/conf/GameType";
import { UserManager } from "../../../../../../main/core/user/UserManager";
import { Instance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { SceneControl } from "../../SceneControl";
import Switch from "../../Switch";
import UniverseSocketMgr from "../../universe/UniverseSocketMgr";
import { BlackIpUtil } from "../../utils/BlackIpUtil";
import CommonUserLastLoginObserver from "../obs/CommonUserLastLoginObserver";

export namespace CommonUserLoginUiUtil {
    export function launchHall() {
        Switch.needTriggerHallStartupChain = true;
        Instance(CommonUserLastLoginObserver).login(UserManager.getInstance().user_id);
        //;
        if (BlackIpUtil.chcekIsInBlackList(false)) return;
        UniverseSocketMgr.onAuthReq();
        return SceneControl.getInstance().launchScene(GameType.LOGIN);
    }
}
