import Listener from "../../../../../../main/core/utils/Listener";
import CommonEventName from "../../CommonEventName";

const { ccclass, property } = cc._decorator;

@ccclass
export default class PageItem extends cc.Component {
    @property({ type: cc.Label, tooltip: "lab" })
    lab_pageNum: cc.Label = null;

    private pageNun: number = 0;
    private posIndex: number = 0;

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_END, this.onClick, this);
    }

    start() {}

    public init(pageNum: number, posIndex: number) {
        this.pageNun = pageNum;
        this.posIndex = posIndex;
        this.lab_pageNum.string = String(pageNum);
    }

    public getPageNum() {
        return this.pageNun;
    }

    public setPosIndex(posIndex: number) {
        this.posIndex = posIndex;
    }

    public getPosIndex() {
        return this.posIndex;
    }

    private onClick() {
        Listener.emit(CommonEventName.CLICK_PAGE_ITEM, this.posIndex);
    }
}
