import { LocalStorageMgr } from "../../../../../../main/core/storage/LocalStorageMgr";
import EnumActive from "../../../../../../main/core/ui/component/EnumActive";
import ObjectLabel from "../../../../../../main/core/ui/component/ObjectLabel";
import UIMgr from "../../Component/base/UIMgr";
import CommonLanguage from "../../conf/lang/CommonLanguage";
import ApiMgr from "../../service/api/ApiMgr";
import FormatConstUtil from "../../utils/FormatConstUtil";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CommonSmsCode extends cc.Component {
    @property({ type: cc.EditBox, displayName: "" })
    codeEd: cc.EditBox = null;

    private _phone: string = "";
    public get phone(): string {
        return this._phone;
    }
    public set phone(v: string) {
        this._phone = v.replace(/(\D+)/g, "");
    }

    public get code() {
        return this.codeEd.string;
    }

    public setGetSmsBtnClick(callfunc: () => void) {
        cc.find("layout/getCodeBtn", this.node)?.on("click", callfunc, this);
    }

    @UIMgr.applyLoadingUi
    public requestCode(phone: string) {
        this.phone = phone;
        if (this.phone.length < FormatConstUtil.MIN_PHONE_LEN + 2) {
            UIMgr.showToast(CommonLanguage.phoneNumberError);
            return;
        }
        return ApiMgr.getInstance()
            .smsCodeReq({
                phone: this.phone,
            })
            .then((res) => {
                this.toCodeCd();
                LocalStorageMgr.getInstance().saveFormalUserAccount(phone);
            });
    }

    private toCodeCd() {
        this.getComponent(EnumActive).show4Name("codeCdBtn");
        let object = {
            value: 60,
        };
        this.getComponentInChildren(ObjectLabel).object = object;
        this.schedule(
            () => {
                object.value -= 1;
                if (object.value < 0) {
                    this.getComponent(EnumActive).show4Name("getCodeBtn");
                    return;
                }
                this.getComponentInChildren(ObjectLabel).object = object;
            },
            1,
            object.value
        );
    }
}
