import { GameType } from "../../../../../main/core/conf/GameType";
import UIMgr from "../Component/base/UIMgr";
import CommonSkin from "../conf/skin/CommonSkin";
import { SceneControl } from "../SceneControl";
import { ApiProto } from "../service/api/ApiProto";
import { CommonResultType } from "./define/CommonResultType";
import CommonUserLoginAuto from "./login/CommonUserLoginAuto";
import { ILogincHandler } from "./login/CommonUserLoginBase";
import CommonUserLoginGuest from "./login/CommonUserLoginGuest";
import CommonUserLoginPassword from "./login/CommonUserLoginPassword";
import { CommonUserInfoUtil } from "./utils/CommonUserInfoUtil";
import ApiMgr from "../service/api/ApiMgr";
export namespace CommonUserUtil {
    let _passwordLogin: CommonUserLoginPassword = new CommonUserLoginPassword();
    let _guestLogin: CommonUserLoginGuest = new CommonUserLoginGuest();
    let _autoLogin: CommonUserLoginAuto = new CommonUserLoginAuto();

    interface ILoginOption<T> {
        success?: (data: T) => void;
        error?: (data: RespHeader) => boolean;
    }

    export async function checkReferId(id: number) {
        try {
            return await ApiMgr.getInstance().promoterVerifyReferId(id);
        } catch {
            return false;
        }
    }

    //
    export function updateUserBalance() {
        return CommonUserInfoUtil.updateUserBalance().then((data) => {
            if (data.result === CommonResultType.Success) {
                return true;
            } else {
                return false;
            }
        });
    }

    let updateTime = 0;

    //
    export function updateUserInfo() {
        let nowTime = Date.now();
        if (nowTime - updateTime > 1000) {
            updateTime = nowTime;
            return CommonUserInfoUtil.updateUserInfo(false).then((data) => {
                if (data.result === CommonResultType.Success) {
                    return true;
                } else {
                    return false;
                }
            });
        }
    }

    export function guestLogin(options?: ILoginOption<ProtoObject<ApiProto.LoginInfo>>) {
        let handler: ILogincHandler = {
            onLoginSuccess: options?.success,
            onEnterLoginFailure: options?.error,
        };

        _guestLogin.applyHandler(handler);
        return _guestLogin.login().then((ret) => {
            _guestLogin.removeHandler(handler);
            if (ret.result === CommonResultType.Success) {
                return ret.content;
            } else {
                throw ret.content;
            }
        });
    }

    export function passwordLogin(
        options?: CommonUser.IPasswordLoginOptions & ILoginOption<ProtoObject<ApiProto.LoginInfo>>
    ) {
        let handler: ILogincHandler = {
            onLoginSuccess: options?.success,
            onEnterLoginFailure: options?.error,
        };

        _passwordLogin.applyHandler(handler);
        return _passwordLogin.login(options).then((ret) => {
            _passwordLogin.removeHandler(handler);
            if (ret.result === CommonResultType.Success) {
                return ret.content;
            } else {
                throw ret.content;
            }
        });
    }

    export function autoLogin(options: ILoginOption<ApiProto.UserInfo> = {}) {
        let handler: ILogincHandler = {
            onLoginSuccess: options?.success,
            onEnterLoginFailure: options?.error,
        };

        _autoLogin.applyHandler(handler);
        return _autoLogin.login().then((ret) => {
            _autoLogin.removeHandler(handler);
            if (ret.result === CommonResultType.Success) {
                return ret.content;
            } else {
                throw ret.content;
            }
        });
    }

    export function logout() {
        CommonDepend.UserManager.clearAccount();
        CommonDepend.UserManager.clearAvailableAvatar();
        CommonDepend.LocalStorageMgr.clearNewAvatarId();
        CommonDepend.LocalStorageMgr.clearUserLoginData();
        SceneControl.getInstance().launchScene(GameType.LOGIN);
    }
}
