import { UserManager } from "../../../../../../main/core/user/UserManager";
import ValueObserver from "../../../../../../main/core/utils/class/ValueObserver";
import { Injector } from "../../../../../../main/core/utils/decorators/Injector";
import { Tools } from "../../../../../../main/core/utils/Tools";
import { UniverseProto } from "../../universe/proto/UniverseProto";
import CommonUserBroadCastObserver from "./CommonUserBroadCastObserver";

type IData = {
    self: CommonUserBroadCastObserver;
    other: CommonUserBroadCastObserver;
};

@Injector.Injectable()
export default class CommonBroadCastObserver extends ValueObserver<IData> {
    private readonly MAX_COUNT = 20;

    constructor() {
        super();
    }

    protected onInit(): IData {
        return {
            self: new CommonUserBroadCastObserver(),
            other: new CommonUserBroadCastObserver(),
        };
    }
    async push(
        value: ProtoObject<UniverseProto.BroadCastNT>[] | ProtoObject<UniverseProto.BroadCastNT>
    ) {
        if (value instanceof Array) {
            value.forEach((value) => {
                this.pushOnce(value);
            });
        } else {
            this.pushOnce(value);
        }
    }

    async pushOnce(value: ProtoObject<UniverseProto.BroadCastNT>) {
        const isSelf = UserManager.getInstance().isSelf(value.UserID.toString());

        const target = isSelf ? this.get("self") : this.get("other");

        target.push(value);
        //
        this.modify();
    }

    pop() {
        const selfTotal = this.get("self").totalLength();
        const otherTotal = this.get("other").totalLength();
        if (selfTotal >= this.MAX_COUNT) {
            this.get("other").set({});
        } else if (selfTotal + otherTotal > this.MAX_COUNT) {
            this.get("other").getOld(true, selfTotal + otherTotal - this.MAX_COUNT);
        }
        return Tools.evenyEmpty([this.get("self"), this.get("other")], (obs) => {
            return obs.popMessage();
        });
    }
}
