import { GameType } from "../../../../../../../main/core/conf/GameType";
import { lodash } from "../../../../../../../main/core/utils/NpmExport";
import BaseSocketAttachment from "./BaseSocketAttachment";

export default class DebugLogAttachment extends BaseSocketAttachment {
    public tag: string = "log";

    public onMessage(data: Socket.TMessageObject<number, any>): void {
        let date = new Date();
        CC_DEBUG && cc.log(
            `%c[${date.toLocaleString()}:${date.getMilliseconds()}] <socket> gameId: >${
                GameType[this.options.gameId]
            }< on=> Id:${data.msgId}->${data?.ctor?.name ?? "Unknown"} \n`,
            "color:#a5a",
            lodash.cloneDeep(data.msgData)
        );
    }

    public onSendMessage(msgType: number, data: Socket.IMessageClass): void {
        if (!msgType) {
            return;
        }
        let date = new Date();
        CC_DEBUG && cc.log(
            `%c[${date.toLocaleString()}:${date.getMilliseconds()}] <socket> gameId:>${
                GameType[this.options.gameId]
            }< send=> Id:${msgType}->${data?.["constructor"]?.name ?? "Unknown"} \n`,
            "color:#f0f",
            lodash.cloneDeep(data?.toObject?.())
        );
    }
}
