import MathUtils from "../../../../../../main/core/utils/MathUtils";
import CommonSkin from "../../conf/skin/CommonSkin";
import PropConfMgr from "../../PropConfMgr";
import { TipsInfoDir } from "../../TipsInfo";
import UIMgr from "../base/UIMgr";
import GetAwardTipsCtrl from "./GetAwardTipsCtrl";
import { AwardItem, AwardTips } from "./GetAwardType";

const { ccclass, property } = cc._decorator;

@ccclass
export default class GetAwardItem extends cc.Component {
    @property(cc.Label)
    nameLabel: cc.Label = null;

    @property(cc.Sprite)
    iconSprite: cc.Sprite = null;

    @property(cc.Label)
    countLabel: cc.Label = null;

    @property(cc.Node)
    tipsNode: cc.Node = null;

    private _vo: AwardItem = null;

    setUIByVO(vo: AwardItem) {
        this._vo = vo;
        this.nameLabel.string = vo.name ?? "";
        this.iconSprite.spriteFrame = PropConfMgr.getInstance().getPropSpriteFrameById(vo.id);
        this.countLabel.string = MathUtils.saveTowDecimal(vo.count) + "";
    }

    protected onClickAwardItem() {
        if (!this._vo) return;
        let worldPos = this.node.convertToWorldSpaceAR(cc.v2(0, 0)); // 
        let iconScale = this.node.scale;
        let offsetHight = (this.node.height / 2) * iconScale;
        worldPos = worldPos.add(cc.v2(0, offsetHight + 10));

        let poopConf: IPropsConf = PropConfMgr.getInstance().getPropConfById(this._vo.id);
        if (poopConf) {
            let parm: ITipsInfoParm = {
                title: poopConf.name,
                content: poopConf.desc,
                width: 376,
                worldPos: worldPos,
                direction: TipsInfoDir.UP,
            };
            UIMgr.showUISync(CommonSkin.Priority.tipsInfo, {
                args: [parm],
            });
        }
    }
}
