import { MoneyUtil } from "../../../../../../main/core/utils/MoneyUtil";

/**
 * 
 */
export namespace CurrencyVND {
    /**
     * 999,999M/999K
     * @param money
     */
    export function formatMoney(money: number): string {
        if (money >= 1000000) {
            money = MoneyUtil.getInstance().roundDown(money);
            return MoneyUtil.getInstance().toThousandthsPass999999WithMWithD(money);
        } else if (money >= 1000) {
            money = MoneyUtil.getInstance().roundDown(money);
            return MoneyUtil.getInstance().toThousandthsPass999WithKWithD(money);
        } else {
            return money.toString();
        }
    }

    /**
     * 999,999,999B,999999M, 
     * @param money
     */
    export function formatMoneyPass999999999WithB(money) {
        if (money > 999999999) {
            money = MoneyUtil.getInstance().roundDown(money);
            return MoneyUtil.getInstance().toThousandthsPass999999999WithB(money);
        } else if (money > 999999) {
            money = MoneyUtil.getInstance().roundDown(money);
            return MoneyUtil.getInstance().toThousandthsPass999999WithMWithD(money);
        } else {
            return this.formatShortMoney(money);
        }
    }

    /**
     * 999,999M, 
     * @param money
     */
    export function formatMoneyPass999999WithM(money) {
        if (money > 999999) {
            money = MoneyUtil.getInstance().roundDown(money);
            return MoneyUtil.getInstance().toThousandthsPass999999WithMWithD(money);
        } else {
            return this.formatShortMoney(money);
        }
    }

    /**
     * 999,999,999M, 
     * 999,999,999K
     * @param money
     */
    export function formatMoneyForCount(money) {
        if (money > 999999999) {
            money = MoneyUtil.getInstance().roundDown(money);
            return MoneyUtil.getInstance().toThousandthsPass999999999WithM(money);
        } else {
            return this.formatWithoutK(money);
        }
    }

    /**
     * 999,999,999M, 
     * * 999,999K
     * @param money
     */
    export function formatMoneyForAmount(money) {
        if (money > 999999999) {
            money = MoneyUtil.getInstance().roundDown(money);
            return MoneyUtil.getInstance().toThousandthsPass999999999WithM(money);
        } else {
            return this.formatLongMoney(money);
        }
    }

    /**
     * Slots---BigWin
     *      |  --
     *      Master &
     * 999,999KMaster Master 
     */
    export function formatLongMoney(money: number) {
        money = MoneyUtil.getInstance().roundDown(money);
        return MoneyUtil.getInstance().toThousandthsPass999999WithK(money);
    }

    /**
     * 
     * K
     */
    export function toThousandthsPass999(money) {
        money = MoneyUtil.getInstance().roundDown(money);
        return MoneyUtil.getInstance().toThousandthsPass999(money);
    }

    /**
     * -
     * 999K
     */
    export function formatShortMoney(money) {
        money = MoneyUtil.getInstance().roundDown(money);
        return MoneyUtil.getInstance().toThousandthsPass999WithK(money);
    }

    /**
     * -
     * 999K
     */
    export function formatChipMoney(money) {
        money = MoneyUtil.getInstance().roundDown(money);
        return MoneyUtil.getInstance().to999WithK(money);
    }

    /**
     * -
     * 999K
     */
    export function formatRoundUpMoney(money) {
        money = MoneyUtil.getInstance().roundUp(money);
        return MoneyUtil.getInstance().toRoundUpThousandthsPass999WithK(money);
    }

    /**
     * Slot-Slot-Slot-Slot-FGSlot-BigWin-
     * K
     */
    export function formatWithoutK(money) {
        money = MoneyUtil.getInstance().roundDown(money);
        return MoneyUtil.getInstance().toThousandths(money);
    }

    /**
     * 
     */
    export function toMoney(thousandths) {
        return MoneyUtil.getInstance().toMoney(thousandths);
    }

    /**
     * |
     */
    export function showGoldForBrcBalance(amount) {
        return this.formatLongMoney(amount);
    }
}
