import { ServiceOperateType } from "../define/ServiceOperateType";
export default abstract class BaseServiceErrorCenter {
    private readonly defaultHandler = Symbol("defaultHandler");

    private _operateMap: Map<
        ServiceOperateType,
        Map<ServiceError.TCode, ServiceError.IErrorHandler>
    > = new Map();

    protected abstract onInitialize(): void;

    constructor() {
        this.initialize();
    }

    public initialize() {
        this.onInitialize();
    }

    protected getHandlerMap(type: ServiceOperateType = ServiceOperateType.None) {
        return this._operateMap.get(type) || this._operateMap.get(ServiceOperateType.None);
    }

    protected applyDeflaultHandler(handler: ServiceError.IErrorHandler): void {
        this.applyDeflaultHandlerWithType(ServiceOperateType.None, handler);
    }

    public applyHandler(code: ServiceError.TCode, handler: ServiceError.IErrorHandler): void {
        this.applyHandlerWithType(ServiceOperateType.None, code, handler);
    }

    protected applyDeflaultHandlerWithType(
        type: ServiceOperateType,
        handler: ServiceError.IErrorHandler
    ): void {
        this.applyHandlerWithType(type, this.defaultHandler, handler);
    }

    public applyHandlerWithType(
        type: ServiceOperateType,
        code: ServiceError.TCode,
        handler: ServiceError.IErrorHandler
    ): void {
        if (!this._operateMap.has(type)) {
            this._operateMap.set(type, new Map());
        }
        if (this.getHandlerMap(type).has(code)) {
            CC_DEBUG &&
                CC_DEBUG &&
                cc.warn(
                    `[BaseServiceErrorCenter] applyHandlerWithType is exists for type:${type} code:${code}, cover exists handler!`
                );
        }

        this.getHandlerMap(type).set(code, handler);
    }

    public catchError(data: ServiceError.IError) {
        let handler = this.getHandler(ServiceOperateType.None, data.code);
        throw handler.catchError(data) || data;
    }

    public catchErrorWithType(type: ServiceOperateType, data: ServiceError.IError) {
        let handler = this.getHandler(type, data.code);
        if (handler) {
            throw handler.catchError(data) || data;
        } else {
            return this.catchError(data);
        }
    }

    protected getHandler(type: ServiceOperateType, code: ServiceError.TCode) {
        return (
            this.getHandlerMap(type).get(code) || this.getHandlerMap(type).get(this.defaultHandler)
        );
    }
}
