import { CurrencyUtil } from "../../utils/CurrencyUtil";
import { ChipUtil } from "./ChipUtil";

const { ccclass, property } = cc._decorator;

@ccclass
export default class BetChip extends cc.Component {
    @property(cc.Node)
    private numItemListArr: Array<cc.Node> = [];

    @property(cc.SpriteAtlas)
    private atlas: cc.SpriteAtlas = null;

    public initChip(chipIndex: number, faceValue: number): void {
        if (chipIndex <= 0 || chipIndex >= 6) {
            chipIndex = chipIndex <= 0 ? 0 : 5;
        }

        this.node.attr({ chipIndex: chipIndex, faceValue: faceValue });

        let chipFrame = this.atlas?.getSpriteFrame?.(`common_chip_0${chipIndex + 1}`);
        // let chipFrame = ChipUtil.getInstance().getChipTypeSpriteFrame(
        //     `common_chip_0${chipIndex + 1}`
        // );
        this.node.getComponent(cc.Sprite).spriteFrame = chipFrame;

        let chipFrameName = chipFrame.name;
        let arr = chipFrameName.split("");
        let chipsNumType = arr[arr.length - 1] + "_";
        this.initChipNumFrame(faceValue, chipsNumType);
    }

    private initNumItem(): void {
        for (let i = 0, len = this.numItemListArr.length; i < len; i++) {
            let numItem = this.numItemListArr[i];
            if (numItem && numItem.active) {
                numItem.active = false;
            }
        }
    }

    private initChipNumFrame(value: number, chipsNumType: string): void {
        if (value != null && value > 0) {
            this.initNumItem();

            let numSpriteFrame: cc.SpriteFrame = null;
            let numStr = CurrencyUtil.formatChipMoney(value).replace("Cr", "C");
            for (let i = 0, len = numStr.length; i < len; i++) {
                let numCode = numStr[i];
                if (chipsNumType) {
                    numSpriteFrame = this.atlas?.getSpriteFrame?.(chipsNumType + numCode);
                    //numSpriteFrame = ChipUtil.getInstance().getChipVauleSpriteFrame(chipsNumType + numCode);
                }
                let numItem = this.numItemListArr[i];
                if (numItem && numSpriteFrame) {
                    numItem.active = true;
                    numItem.getComponent(cc.Sprite).spriteFrame = numSpriteFrame;
                }
            }
        }
    }
}
