import FormatConstUtil from "./FormatConstUtil";

export namespace FormatTemplateUtil {
    export function cpf(str: string) {
        return str.replace(/^(\d{3})(\d{3})(\d{3})(\d{2})$/, "$1.$2.$3-$4");
    }
    export function cnpj(str: string) {
        return str.replace(/^(\d{2})(\d{3})(\d{3})(\d{4})(\d+)$/, "$1.$2.$3/$4-$5");
    }
    export function cep(str: string) {
        return str.replace(/(\d{5})(.+)/, "$1-$2");
    }
    export function phone(str: string) {
        const config = FormatConstUtil.PHONE_FORMAT;
        return str.replace(config.reg, config.template);
    }
    export function evp(str: string) {
        return str.replace(
            /^([A-Za-z0-9]{8})([A-Za-z0-9]{4})([A-Za-z0-9]{4})([A-Za-z0-9]{4})([A-Za-z0-9]+)$/,
            "$1-$2-$3-$4-$5"
        );
    }
    export function upi(upiStr: string, lengthCount = 27): string {
        // let reg = /^[A-Za-z0-9]+(?=@)/;
        // let str:RegExpExecArray = reg.exec(upiStr);
        // let frontStr:string = "";
        // if (str && str[0]) {
        //     frontStr = str[0];
        // } else {
        //     return upiStr;
        // }

        // if (frontStr.length > 4) {
        //     frontStr = frontStr.slice(0, 4);
        //     frontStr += "..."
        // }

        let str: string[] = upiStr.split("@");
        if (str.length <= 1) {
            return upiStr;
        }

        let frontStr = str[0];
        let tailStr = str[1];

        if (frontStr.length > 4) {
            frontStr = frontStr.slice(0, 4);
            frontStr += "***";
        }

        let retStr = frontStr + "@" + tailStr;

        return retStr.limtLength(lengthCount);
    }
    export function bank(bankStr: string): string {
        return bankStr.replace(/^(\d{4})(\d+)(\d{4})$/, "$1***$3");
    }

    export function emailMosaic(emailStr: string, lengthCount = 40): string {
        let str: string[] = emailStr.split("@");
        if (str.length <= 1) {
            return emailStr;
        }

        let frontStr = str[0];
        let tailStr = str[1].limtLength(23);

        if (frontStr.length > 4) {
            frontStr = frontStr.slice(0, 4);
            frontStr += "***";
        }

        let retStr = frontStr + "@" + tailStr;

        return retStr.limtLength(lengthCount);
    }

    export function phoneMosaic(str: string) {
        return str.replace(/(\d{2})(.+)(\d{4})$/, "($1)*****-$3");
    }

    export function cpfMosaic(str: string) {
        return str.replace(/^(\d{2})(\d{1})(\d{3})(\d{3})(\d{2})$/, "$1*.***.***-$5");
    }

    export function cnpjMosaic(str: string) {
        return str.replace(/^(\d{2})(\d{3})(\d{3})(\d{4})(\d+)$/, "$1.***.***/****-$5");
    }

    /**
     * 
     * @param str
     * @returns
     */
    export function removeEmoji(str: string): string {
        let emojiReg =
            /([\u00A9\u00AE\u203C\u2049\u2122\u2139\u2194-\u2199\u21A9-\u21AA\u231A-\u231B\u2328\u23CF\u23E9-\u23F3\u23F8-\u23FA\u24C2\u25AA-\u25AB\u25B6\u25C0\u25FB-\u25FE\u2600-\u2604\u260E\u2611\u2614-\u2615\u2618\u261D\u2620\u2622-\u2623\u2626\u262A\u262E-\u262F\u2638-\u263A\u2640\u2642\u2648-\u2653\u2660\u2663\u2665-\u2666\u2668\u267B\u267F\u2692-\u2697\u2699\u269B-\u269C\u26A0-\u26A1\u26AA-\u26AB\u26B0-\u26B1\u26BD-\u26BE\u26C4-\u26C5\u26C8\u26CE-\u26CF\u26D1\u26D3-\u26D4\u26E9-\u26EA\u26F0-\u26F5\u26F7-\u26FA\u26FD\u2702\u2705\u2708-\u270D\u270F\u2712\u2714\u2716\u271D\u2721\u2728\u2733-\u2734\u2744\u2747\u274C\u274E\u2753-\u2755\u2757\u2763-\u2764\u2795-\u2797\u27A1\u27B0\u27BF\u2934-\u2935\u2B05-\u2B07\u2B1B-\u2B1C\u2B50\u2B55\u3030\u303D\u3297\u3299]|\uD83C[\uDC04\uDCCF\uDD70-\uDD71\uDD7E-\uDD7F\uDD8E\uDD91-\uDD9A\uDDE6-\uDDFF\uDE01-\uDE02\uDE1A\uDE2F\uDE32-\uDE3A\uDE50-\uDE51\uDF00-\uDF21\uDF24-\uDF93\uDF96-\uDF97\uDF99-\uDF9B\uDF9E-\uDFF0\uDFF3-\uDFF5\uDFF7-\uDFFF])|(\uD83D[\uDC00-\uDCFD\uDCFF-\uDD3D\uDD49-\uDD4E\uDD50-\uDD67\uDD6F-\uDD70\uDD73-\uDD7A\uDD87\uDD8A-\uDD8D\uDD90\uDD95-\uDD96\uDDA4-\uDDA5\uDDA8\uDDB1-\uDDB2\uDDBC\uDDC2-\uDDC4\uDDD1-\uDDD3\uDDDC-\uDDDE\uDDE1\uDDE3\uDDE8\uDDEF\uDDF3\uDDFA-\uDE4F\uDE80-\uDEC5\uDECB-\uDED2\uDEE0-\uDEE5\uDEE9\uDEEB-\uDEEC\uDEF0\uDEF3-\uDEF6])|(\uD83E[\uDD10-\uDD1E\uDD20-\uDD27\uDD30\uDD33-\uDD3A\uDD3C-\uDD3E\uDD40-\uDD45\uDD47-\uDD4B\uDD50-\uDD5E\uDD80-\uDD91\uDDC0])/g;
        return str.replace(emojiReg, "");
    }

    /**
     *   
     * @param str
     * @param maxLen
     * @returns
     */
    export function removeRedundantCharacter(str: string, maxLen: number): string {
        let len = 0;
        let retStr = "";
        let reg = /[\u4e00-\u9fa5]/;
        for (let i = 0, strLen = str.length; i < strLen; i++) {
            let character = str.charAt(i);
            if (reg.test(character)) {
                len += 2;
            } else {
                len += 1;
            }

            if (len <= maxLen) {
                retStr += character;
            } else {
                break;
            }
        }

        return retStr;
    }
}
