import { UniverseProto } from "./universe/proto/UniverseProto";

type GameTag = ProtoObject<UniverseProto.FeatureConfResp.GameTag>[];

class GameNavigation {
    public static navigation: ProtoObject<UniverseProto.FeatureConfResp.GameNavigation>[];
    public static gameTag: GameTag;
    private static gameTagTypeMap: Map<number, UniverseProto.GameTagType> = new Map();

    public static gameSocket: {};

    public static initGameTagTypeMap(gameTag: GameTag) {
        this.gameTag = gameTag;

        for (let i = 0; i < this.gameTag.length; i++) {
            let gameIds = this.gameTag[i].game_ids;
            let tagType = this.gameTag[i].type;
            if (tagType == UniverseProto.GameTagType.TAG_TYPE_NEWBIE_GUIDE) {
                //tag
                continue;
            }
            for (let j = 0; j < gameIds.length; j++) {
                this.gameTagTypeMap.set(gameIds[j], tagType);
            }
        }
    }

    public static getGameTagWithId(gameId: number): UniverseProto.GameTagType {
        return this.gameTagTypeMap.get(gameId);
    }
}

export default GameNavigation;
