export default class CommonObjectPool {
    private Pool: cc.NodePool = new cc.NodePool();
    private prefab: cc.Prefab = null;

    public create(count: number, prefab: cc.Prefab) {
        if (this.Pool == null) {
            this.Pool = new cc.NodePool();
        }
        if (count > 0 && prefab != null && prefab != undefined) {
            this.prefab = prefab;
            for (let i = 0; i < count; ++i) {
                let obj = cc.instantiate(prefab);
                if (obj) {
                    if (this.Pool) {
                        this.Pool.put(obj);
                    }
                }
            }
        }
    }

    public setObj(parentNode: cc.Node, maxChildrenCount: number) {
        let self = this;
        if (self.Pool && self.Pool.size() <= 0) {
            if (self.prefab != null) {
                let node = cc.instantiate(self.prefab);
                if (node) {
                    self.Pool.put(node);
                }
            }
        }

        if (maxChildrenCount !== undefined && parentNode.childrenCount >= maxChildrenCount) {
            let firstChip = parentNode.children[0];
            self.Pool.put(firstChip);
            parentNode.removeChild(firstChip);
        }

        let node = self.Pool.get();
        if (node == null && self.prefab != null) {
            node = cc.instantiate(self.prefab);
        }

        if (node != null) {
            node.parent = parentNode;
        }

        return node;
    }

    public reuse(obj: cc.Node) {
        let self = this;
        if (self.Pool != null && self.Pool != undefined && obj != null && obj != undefined) {
            self.Pool.put(obj);
        }
    }

    public size() {
        return this.Pool.size();
    }

    public clear() {
        let self = this;
        if (self.Pool != null && self.Pool != undefined) {
            self.Pool.clear();
            self.Pool = null;
        }
    }
}
