import Listener from "../../../../../../main/core/utils/Listener";
import { AwardTips, FrameEvent } from "./GetAwardType";

const { ccclass, property } = cc._decorator;

@ccclass
export default class GetAwardTipsCtrl extends cc.Component {
    @property(cc.Node)
    container: cc.Node = null;

    @property(cc.Label)
    titleLabel: cc.Label = null;

    @property(cc.Label)
    contentLabel: cc.Label = null;

    onEnable() {
        Listener.on(FrameEvent.FRAME_TOUCH, this.onOtherAwardItemTouch, this);
    }

    onDisable(): void {
        Listener.off(FrameEvent.FRAME_TOUCH, this.onOtherAwardItemTouch, this);
    }

    setUiByVO(vo: AwardTips) {
        this.titleLabel.string = vo.title;
        this.contentLabel.string = vo.content;
    }

    onOtherAwardItemTouch() {
        this.node && (this.node.active = false);
    }
}
