import { Instance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { CommonProto } from "../ws/proto/CommonProto";
import ApiServiceErrorCenter from "./ApiServiceErrorCenter";
import SocketServiceErrorCenter from "./SocketServiceErrorCenter";
import { ServiceOperateType } from "./define/ServiceOperateType";
export namespace ServiceErrorUtils {
    export function catchError<T>(promise: Promise<T>): Promise<T> {
        return promise.catch((ret) => {
            return applyApiError(ret) as T;
        });
    }

    export function catchErrorWithType<T>(
        type: ServiceOperateType,
        promise: Promise<T>
    ): Promise<T> {
        return promise.catch((ret) => {
            return applyApiErrorWitchType(type, ret) as T;
        });
    }

    export function applyApiErrorWitchType(type: ServiceOperateType, data: RespHeader) {
        if (!data.code) {
            return data;
        }
        return onApiErrorWitchType(type, data);
    }

    export function applyApiError(data: RespHeader) {
        if (!data.code) {
            return data;
        }
        return onApiError(data);
    }

    export function applySocketError(data: ProtoObject<CommonProto.ErrorST>) {
        return onSocketError(data);
    }

    export function onApiErrorWitchType(type: ServiceOperateType, data: RespHeader) {
        return Instance(ApiServiceErrorCenter).catchErrorWithType(type, {
            code: data.code,
            message: data.msg,
        });
    }

    export function onApiError(data: RespHeader) {
        return Instance(ApiServiceErrorCenter).catchError({
            code: data.code,
            message: data.msg,
        });
    }

    export function onSocketError(data: ProtoObject<CommonProto.ErrorST>) {
        return Instance(SocketServiceErrorCenter).catchError({
            code: data.Status,
            message: data.Msg,
        });
    }
}
