import { GameType } from "../../../../../../../main/core/conf/GameType";

const INTERVAL_HEART_CHECK: number = 5000;
const INTERVAL_RECONNECT: number = 5000; // reconnect timeout5s
/**
 * responsepongrequest/response
 * ping/pongping5spongtimeout5spong10s
 */
const TIMEOUT_SECS_PONG: number = 10;

interface IHeartCheckOptions {
    gameId: number;
    connect(): void;
    ping(): void;
}

export default class HeartCheck {
    private needCheck: boolean = false;
    private lastTime: number = 0;
    private connectCount: number = 0;
    private checkIntervalTimeId: NodeJS.Timer = null;
    private reconnectIntervalTimeId: NodeJS.Timer = null;
    private options: IHeartCheckOptions = null;

    public init(options: IHeartCheckOptions) {
        this.options = options;
        this.log("init");
        this.needCheck = true;
        this.updateLastTime();
        this.connectCount = 0;
        return this;
    }

    public play() {
        this.log("play");
        this.stop();
        this.needCheck = true;
        this.checkIntervalTimeId = setInterval(() => {
            this.tryReconnect();
        }, INTERVAL_HEART_CHECK);
    }

    public updateLastTime() {
        this.lastTime = this.getNow();
    }

    public stop() {
        if (this.checkIntervalTimeId) {
            this.log("stop");
            clearInterval(this.checkIntervalTimeId);
            this.checkIntervalTimeId = null;
        }
    }

    public close() {
        this.log("close");
        this.needCheck = false;
        this.stop();
    }

    private getNow() {
        return Date.parse(new Date().toString()) / 1000;
    }

    private tryReconnect() {
        if (!this.needCheck) {
            return;
        }
        let isPongTimeout = this.getNow() - this.lastTime > TIMEOUT_SECS_PONG;
        if (isPongTimeout) {
            if (CC_DEBUG) {
                this.log("timeout -> reconnect");
            }
            this.reconnect();
        }
        this.options.ping();
    }

    public reconnect() {
        if (!this.needCheck) {
            return;
        }
        this.stop();
        if (CC_DEBUG) {
            this.log("reconnect");
        }
        this.options.connect();
    }

    protected log(str: string) {
        if (!this.options) {
            return;
        }
        CC_DEBUG && cc.log(
            `%c[${new Date().toLocaleString()}] <socket> heartCheck heartCheck gameId:${
                GameType[this.options.gameId]
            } ${str}`,
            "color:#a00"
        );
    }

    protected onReConnect() {}
}
