import Listener from "../../../../../main/core/utils/Listener";
import { CommonBroadCastEvents } from "./define/CommonBroadCastEvents";

class CommonBroadCastUtil {
    /**
     * 
     */
    public setPostion(world: cc.Vec3) {
        Listener.send(CommonBroadCastEvents.position, world);
    }

    /**
     * 
     */
    public reset() {
        Listener.send(CommonBroadCastEvents.reset);
    }
}
export default new CommonBroadCastUtil();
