import CryptMgr from "../../../../../../main/core/crypt/CryptMgr";
import { Tools } from "../../../../../../main/core/utils/Tools";
import CommonSocket from "../../../../../../main/core/ws/CommonSocket";
import { CommonSocketUtil } from "./CommonSocketUtil";

export default abstract class BaseSocket<MsgType extends number> {
    protected abstract gameId: number;
    protected abstract host: string | string[];
    protected abstract isEncrypt: boolean;
    protected abstract cryptId: number;

    protected curentHost: string = "";

    protected aesKey: string = undefined;
    private socket: CommonSocket = null;

    protected connect() {
        if (CC_DEBUG) {
            CC_DEBUG && cc.log(`BaseSocket: connect`);
        }
        console.log(this.host, "this.host")
        this.curentHost = this.host[0];
        this.socket = new CommonSocket(
            this.gameId,
            this.curentHost,
            this.onMessageHandler.bind(this),
            this.onOpenHandler.bind(this),
            this.onCloseHandler.bind(this)
        );
    }

    protected getSocket() {
        return this.socket;
    }

    protected disconnect() {
        CC_DEBUG && cc.log(`BaseSocket: disconnect`);
        this.socket?.close();
        this.socket = null;
    }

    protected abstract getDecodeClass(msgType: MsgType);

    protected abstract onMessage(data: Socket.TMessageObject<MsgType, any>): void;
    protected abstract onOpenHandler(): void;

    protected abstract onCloseHandler(): void;

    protected getState() {
        return this.socket?.state();
    }

    private onMessageHandler(data: Socket.TMessageData<MsgType, any>) {
        data = this.decryptData(data);
        if (data instanceof Uint8Array) {
            let msgType: MsgType = CommonSocketUtil.getInstance().getMessageId(data.buffer) as any;
            let msgData = data.slice(2);

            //proto 
            let ctor = this.getDecodeClass(msgType);
            if (!ctor) {
                if (CC_DEBUG) {
                    CC_DEBUG && cc.error("[socket] getDecodeClass failed: " + msgType);
                }
                return;
            }
            let messageData = this.decode(ctor, msgData);
            if (!messageData) {
                if (CC_DEBUG) {
                    // CC_DEBUG && cc.error("[socket] decode failed: " + msgType, messageData);
                }
                return;
            }
            this.onMessage({
                msgId: msgType,
                msgData: messageData,
                ctor: ctor,
            });
        } else {
            this.onMessage(data);
        }
    }

    private decode(
        ctor: Socket.IMessageCtor,
        msgData: ArrayBuffer
    ): Socket.TMessageObject<MsgType, any> {
        try {
            let deserialize = ctor.deserialize(new Uint8Array(msgData));
            return deserialize?.toObject() ?? null;
        } catch (e) {
            if (CC_DEBUG) {
                //CC_DEBUG && cc.error("[socket] decode failed!", ctor, msgData, e);
            }
        }
    }

    protected encode(msgType: MsgType, msgData: Socket.IMessageClass) {
        let buffer = msgData.serialize();
        buffer = CommonSocketUtil.getInstance().addMessageId(msgType as number, buffer);
        return buffer;
    }

    protected send(msgType: MsgType, msgData: Socket.IMessageClass) {
        let buffer = this.encode(msgType, msgData);
        buffer = this.encryptData(buffer);
        this.socket?.send(buffer);
    }

    protected decryptData(data: Socket.TMessageData<MsgType, any>) {
        if (!this.isEncrypt) {
            return new Uint8Array(data as ArrayBuffer);
        }
        data = CryptMgr.getInstance().aes_decrypt(data);
        return data;
    }

    protected encryptData(buffer: Uint8Array) {
        if (!this.isEncrypt) {
            return buffer;
        }
        buffer = CryptMgr.getInstance().aes_encrypt(buffer, this.aesKey);
        buffer = CommonSocketUtil.getInstance().addCryptId(this.cryptId, buffer);
        return buffer;
    }
}