import { BundleMgr } from "../../../../../../main/core/bd/BundleMgr";
import { Tools } from "../../../../../../main/core/utils/Tools";
import CommonSkin from "../../conf/skin/CommonSkin";
import UserAvatar from "./UserAvatar";
import UserAvatarModel from "./UserAvatarModel";

export default class UserAvatarMgr {
    public static getSpriteFrame(spriteName: string): cc.SpriteFrame {
        let res = CommonSkin.Priority.avatar[spriteName];
        if (!res) {
            return null;
        }
        return BundleMgr.getInstance().getResFromBundle(res);
    }

    //ApiProto.GENDER
    static getAvatar(
        avatar: number,
        gender: UserAvatarModel.GENDER
    ): cc.SpriteFrame | sp.SkeletonData {
        let self = this;
        let result = null;
        switch (Number(avatar)) {
            case UserAvatarModel.AVATAR_ID.NORMAL_1:
            case UserAvatarModel.AVATAR_ID.NORMAL_2:
            case UserAvatarModel.AVATAR_ID.NORMAL_3:
            case UserAvatarModel.AVATAR_ID.NORMAL_4:
            case UserAvatarModel.AVATAR_ID.NORMAL_5:
            case UserAvatarModel.AVATAR_ID.NORMAL_6:
            case UserAvatarModel.AVATAR_ID.NORMAL_7:
            case UserAvatarModel.AVATAR_ID.NORMAL_8:
                result = self.getNormalAvatar(avatar, gender==0 ? 1 : gender);
                break;
            case UserAvatarModel.AVATAR_ID.DYNAMIC_0:
                result = BundleMgr.getInstance().getResFromBundle(
                    CommonSkin.Priority.avatar[avatar]
                );
                break;
            default:
                result = self.getNormalAvatar(
                    UserAvatarModel.AVATAR_ID.NORMAL_1,
                    UserAvatarModel.GENDER.FEMALE
                );
                break;
        }
        return result;
    }

    static isDynamicAvatar(avatar: number) {
        return [UserAvatarModel.AVATAR_ID.DYNAMIC_0].includes(Number(avatar));
    }

    /**
     * 
     * DWC:  avatar="head_boy1.png"
     * PACK: avatar=1
     * @param avatar
     * @param gender
     */
    static getNormalAvatar(avatar: number, gender: UserAvatarModel.GENDER) {
        let self = this;
        let frame;
        if (avatar) {
            frame = this.getSpriteFrame(`head_${gender}_${Tools.limit(Number(avatar), 1, 8)}`);
        }
        return frame;
    }

    static switchPointRed() {
        let self = this;
        let data = CommonDepend.LocalStorageMgr.getNewAvatarId();
        if (data) {
            let newAvatar = data.newAvatar;
            let available_avatar = CommonDepend.UserManager.available_avatar;
            if (available_avatar && available_avatar.length > 0) {
                for (let i = 0; i < available_avatar.length; i++) {
                    if (newAvatar) {
                        let result = newAvatar.indexOf(available_avatar[i]);
                        if (result == -1) {
                            return true;
                        }
                    }
                }
            } else {
                return false;
            }
        } else {
            return false;
        }
    }

    /**
     *
     * @param node 
     * @param avatar id
     * @param gender 
     * @param options 
     * @param pointRed 
     * @param buttonEnabled Button
     *
     * 
     */
    static async loadUserAvatarPrefab(
        node: cc.Node | cc.Component,
        avatar: number,
        gender: UserAvatarModel.GENDER
    ) {
        let ccNode = null;
        if (cc.isValid(node)) {
            if (node instanceof cc.Node) {
                ccNode = node;
            } else {
                ccNode = node.node;
            }

            return UIDepend.PrefabManager.loadPrefabBundle(CommonSkin.UnPriority.UserAvatar).then(
                (res: cc.Prefab) => {
                    if (res && cc.isValid(ccNode)) {
                        let prefabIns = cc.instantiate(res);
                        ccNode.removeAllChildren();
                        ccNode.addChild(prefabIns);
                        prefabIns.getComponent(UserAvatar)?.init?.(Number(avatar), gender);
                        return prefabIns;
                    }
                }
            );
        }
        return null;
    }
}
