import ValueObserver from "../../../../../main/core/utils/class/ValueObserver";
import { setInstance } from "../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { UniverseProto } from "../universe/proto/UniverseProto";
type ISettingData = ProtoObject<UniverseProto.FeatureConfResp.FeatureSetting>

@setInstance
export default class CommonFeatureSettingObserver extends ValueObserver<ISettingData> {
    protected onInit(): ISettingData {
        return {};
    }

    public getUpgradeAmount() {
        return this.get("upgrade", 0);
    }

    public getUserBox() {
        return this.get("anonymous", 0);
    }
}
