import ValueObserver from "../../../../../main/core/utils/class/ValueObserver";
import { BundleNames } from "../../../../../main/core/conf/BundleNames";
import { Injector } from "../../../../../main/core/utils/decorators/Injector";

export interface IBundleLoadingOptions {
    bundleName: BundleNames;
    progress: number;
    success: boolean;
    has: boolean;
    error: boolean;
}

@Injector.Injectable()
export default class LoadingSceneStateObserver extends ValueObserver<IBundleLoadingOptions> {
    private enterUi: boolean = false;

    protected onInit(): IBundleLoadingOptions {
        return {
            has: false,
            bundleName: BundleNames.UNKNOWN,
            progress: 0,
            success: false,
            error: false,
        };
    }

    public tryEnterGame() {
        this.enterUi = true;
    }

    public enterGameBack() {
        this.enterUi = false;
    }

    public onLoadingStart(bundleName: BundleNames) {
        if (
            [
                BundleNames.FIRE_JOKER,
                BundleNames.WOLFGOLD,
                BundleNames.BIG_SMALL,
                BundleNames.HALL,
            ].includes(bundleName)
        ) {
            return;
        }
        this.modify({
            has: this.enterUi,
            bundleName: bundleName,
            progress: 0,
            success: false,
            error: false,
        });
    }

    public onLoadingSuccess(bundleName: BundleNames) {
        switch (bundleName) {
            case BundleNames.HALL: {
                //has false
                this.set("has", false, false);
                break;
            }
        }
        this.modify({
            bundleName: bundleName,
            progress: 0,
            success: true,
        });
    }
    public onError(bundleName: BundleNames) {
        if ([BundleNames.FIRE_JOKER, BundleNames.WOLFGOLD].includes(bundleName)) {
            return;
        }
        this.modify({
            error: true,
        });
    }
}
