import PopUpViewBase from "../../../../../main/core/popUpViewUtils/PopUpViewBase";
import { LocalStorageMgr } from "../../../../../main/core/storage/LocalStorageMgr";
import { AdjustTrack } from "../../../../../main/core/track/AdjustTrack";
import { Instance } from "../../../../../main/core/utils/decorators/InstanceClassDecorator";
import Listener from "../../../../../main/core/utils/Listener";
import { lodash } from "../../../../../main/core/utils/NpmExport";
import CommonEventName from "../CommonEventName";
import UIMgr from "../Component/base/UIMgr";
import CommonLanguage from "../conf/lang/CommonLanguage";
import { DataCacheMgr } from "../DataCacheMgr";
import CommonListenUiEvent from "../event/CommonListenUiEvent";
import GameNavigation from "../GameNavigation";
import H5Customer from "../h5_customer/H5Customer";
import CommonFeatureSettingObserver from "../obs/CommonFeatureSettingObserver";
import CommonHostListObserver from "../obs/CommonHostListObserver";
import CommonLaunchConfigObserver from "../obs/CommonLaunchConfigObserver";
import CommonNetworkSwitchObserver from "../obs/CommonNetworkSwitchObserver";
import CommonUpdateNewObserver from "../obs/CommonUpdateNewObserver";
import RedPointObserver from "../obs/RedPointObserver";
import PropConfMgr from "../PropConfMgr";
import CommonStorageMgr from "../Storage/CommonStorageMgr";
import { CommonUserUtil } from "../user/CommonUserUtil";
import { UniverseProto } from "./proto/UniverseProto";
import { UniverseProtoMap } from "./proto/UniverseProtoMap";
import UniverseSocketMgr from "./UniverseSocketMgr";
import CommonGameListObserver from "../obs/CommonGameListObserver";
import { BlackIpUtil } from "../utils/BlackIpUtil";

const { MessageID } = UniverseProto;

type TCacheOption = {
    [K in keyof typeof UniverseProto.MessageID]?: ProtoObject<
        Pick<typeof UniverseProtoMap, typeof UniverseProto.MessageID[K]>
    >;
};

class UniverseServiceMgr {
    private cacheOption: TCacheOption = {};

    public startUniverse() {
        UniverseSocketMgr.init({
            onMessage: this.onMessage.bind(this),
        });
    }

    public reconnect() {
        UniverseSocketMgr.reconnect();
    }

    private async onMessage(
        message: ProtoObject<typeof UniverseProtoMap[UniverseProto.MessageID]>
    ) {
        this.cacheOption[message.msgId] = message.msgData as any;
        this?.[`on${MessageID[message.msgId]}`]?.(message);
        switch (message.msgId) {
            case MessageID.NAMING_ST: {
                this.parseNamingMessage(message.msgData);
                UniverseSocketMgr.onPropsConfReq();
                Listener.send(CommonEventName.AUTH_SUCCESS);
                break;
            }

            case MessageID.LAUNCH_CONF_RESP: {
                this.parseLaunchConfigResp(message.msgData);
                break;
            }

            case MessageID.FEATURE_CONF_RESP: {
                this.parseFeatureConfResp(message.msgData);
                break;
            }

            case MessageID.UNREAD_MSG_NT: {
                this.parseUnreadMsgNT(message.msgData);
                break;
            }

            case MessageID.BROADCAST_NT: {
                Listener.send(CommonEventName.EVENT_IM_NEW_MESSAGE, message.msgData);
                break;
            }

            case MessageID.PROPS_CONF_RESP: {
                this.parsePropsConfResp(message.msgData);
                break;
            }

            case MessageID.POPUPS_CONF_RESP: {
                //
                this.parseUpsConfResp(message.msgData);
                break;
            }

            case MessageID.LOGIN_AWARD_NT: {
                let data: ILoginBonusData = { AwardAmount: message.msgData.AwardAmount };
                DataCacheMgr.getInstance().setLoginBonusData(data);
                break;
            }

            case MessageID.CUSTOMER_CONF_RESP: {
                let data: ProtoObject<UniverseProto.CustomerConfResp> = message.msgData;
                H5Customer.getInstance().setCustomerInfo(data);
                break;
            }

            case MessageID.MAIL_DELETE_NT: {
                let data: ProtoObject<UniverseProto.MailDeleteNT> = message.msgData;
                Listener.emit(CommonEventName.MailRomoteDelList, data.MailList);
                break;
            }

            case MessageID.IP_BLACK_LIST_NT:
                let data: ProtoObject<UniverseProto.IPBlacklistNT> = message.msgData;
                BlackIpUtil.blackListFlag = data.ErrorMsg;
                let result = await UIMgr.showAlertAwait({
                    contentText: data.ErrorMsg,
                    contextAlign: cc.macro.TextAlignment.CENTER,
                    style: UIMgr.ALERT_STYLE.POSITIVE,
                });

                result.onClose(() => {
                    CommonUserUtil.logout();
                });
                break;
            case MessageID.UPDATE_NEW_NT: {
                Instance(CommonUpdateNewObserver).tryUpdate(message.msgData);
                break;
            }
            default:
                break;
        }
    }

    private parseUpsConfResp(msg: ProtoObject<UniverseProto.PopUpsConfResp>) {
        if (!msg?.PopUps) return;

        let storageMgr = CommonStorageMgr.getInstance();

        // 
        let topupUps = msg.PopUps["1"];
        if (topupUps) {
            storageMgr.writeTopupUps(topupUps);
        } else {
            //
            storageMgr.clearTopupUps();
        }

        // 
        let fishTopUpUps = msg.PopUps["2"];
        if (fishTopUpUps) {
            storageMgr.writeFishTopUpUps(fishTopUpUps);
        } else {
            //
            storageMgr.clearFishTopUpUps();
        }

        // 
        let topupFailUps = msg.PopUps["3"];
        if (topupFailUps) {
            storageMgr.writeTopupFailUps(topupFailUps);
        } else {
            //
            storageMgr.clearTopupFailUps();
        }

        // 
        let WithdrawUps = msg.PopUps["4"];
        if (WithdrawUps) {
            storageMgr.writeWithdrawUps(WithdrawUps);
        } else {
            //
            storageMgr.clearWithdrawUps();
        }
    }

    private parseUnreadMsgNT(msg: ProtoObject<UniverseProto.UnreadMsgNT>) {
        let msgList = msg.UnreadMsgList;
        for (let i = 0, len = msgList.length; i < len; i++) {
            switch (msgList[i].MsgType) {
                case UniverseProto.UnreadMsgType.PAY_MSG:
                    CommonUserUtil.updateUserBalance();
                    Listener.send(CommonEventName.PAY_SUCCESS);

                    // 
                    let payResultAlert = UIMgr.checkDialogExits("PayResultAlert");
                    payResultAlert && payResultAlert.getComponent(PopUpViewBase).close();

                    //
                    const ALERT_NAME = "PaySuccessAlert";
                    let result = UIMgr.showAlert({
                        contentText: CommonLanguage.paySuccess,
                        style: UIMgr.ALERT_STYLE.POSITIVE,
                    });
                    result.popupNode.name = ALERT_NAME;

                case UniverseProto.UnreadMsgType.MAIL_MSG:
                    Listener.emit(CommonEventName.MailShowRedDot);
                    break;
                case UniverseProto.UnreadMsgType.KF_MSG:
                    CommonStorageMgr.getInstance().writeCustomerUnreadMsg(true);
                    Listener.send(CommonEventName.CUSTOMER_RED_DOT_REFRESH);
                    break;

                default:
            }
        }
    }

    private parsePropsConfResp(msg: ProtoObject<UniverseProto.PropsConfResp>) {
        // to do iconprops_idicon
        let propConf: Map<number, IPropsConf> = new Map();
        let props_list = msg.props_list;
        for (let key in props_list) {
            let props_id = props_list[key].props_id;
            let name = props_list[key].name;
            let kind = props_list[key].kind;
            let sub_kind = props_list[key].sub_kind;
            let icon = props_list[key].icon;
            let desc = props_list[key].desc;
            propConf.set(props_id, {
                props_id: props_id,
                name: name,
                kind: kind,
                sub_kind: sub_kind,
                icon: icon,
                desc: desc,
            });
        }
        PropConfMgr.getInstance().setPropConf(propConf);
    }

    private parseLaunchConfigResp(msg: ProtoObject<UniverseProto.LaunchConfResp>) {
        Instance(CommonLaunchConfigObserver).set(msg);
        LocalStorageMgr.getInstance().saveLauncherConfig(msg);
        AdjustTrack.getInstance().initAdjustConf();
    }

    private parseFeatureConfResp(msg: ProtoObject<UniverseProto.FeatureConfResp>) {
        let game_settings = msg.game_settings;
        let navigation = game_settings.navigation;
        let game_list = game_settings.game_list;
        let game_tag = game_settings.game_tag;
        let bar = msg.bar;

        GameNavigation.navigation = navigation;
        GameNavigation.initGameTagTypeMap(game_tag);

        Instance(CommonHostListObserver).loadGameInfo(game_list);

        Listener.send(CommonListenUiEvent.LISTEN_UI_HALL_VIEW_SHOW);
        Instance(CommonFeatureSettingObserver).modify(msg?.feature_settings ?? {});
        Instance(CommonNetworkSwitchObserver).modify(msg?.switch_conf ?? {});
        Instance(RedPointObserver).initGameGuide(game_settings?.game_tag ?? []);
        Instance(RedPointObserver).initBar(bar);
        Instance(CommonGameListObserver).set(game_list);
    }

    private parseNamingMessage(msg: ProtoObject<UniverseProto.NamingST>) {
        if (!msg) {
            return;
        }
        /** ws host list **/
        let hostList = [];
        let remoteHostList = msg.HostList;
        Instance(CommonHostListObserver).loadServerHostList(msg.HostList);
        if (!lodash.isEmpty(remoteHostList)) {
            hostList = Array.from(new Set(remoteHostList));
        }
        let localHostList = CommonDepend.LocalStorageMgr.readHostList();
        if (!lodash.isEmpty(localHostList)) {
            let arr = lodash.difference(hostList, localHostList);
            hostList = arr.concat(localHostList);
        }
        CommonDepend.LocalStorageMgr.saveHostList(hostList);
        Instance(CommonHostListObserver).reset();
    }
}

export default new UniverseServiceMgr();
