import { AudioManager } from "../../../../../main/core/audio/AudioManager";
import CommonSkin from "../conf/skin/CommonSkin";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CountDownTimer extends cc.Component {
    @property(cc.Label)
    timerLabel: cc.Label = null;

    @property([cc.String])
    animName: string[] = ["clock_bg", "loop"];

    @property(cc.String)
    boneName: string = "text";

    private _skeleton: sp.Skeleton = null;
    private _currBone = null;
    private _duration: number = 0;
    private _endCb: Function = null;

    protected onLoad(): void {
        this._skeleton = this.node.getComponent(sp.Skeleton);
        this._currBone = this._skeleton.findBone(this.boneName);
        this.node && (this.node.opacity = 0);
    }

    public startTimer(duration, endCountDownCallBack): void {
        if (duration <= 0) {
            if (endCountDownCallBack && typeof endCountDownCallBack == "function") {
                endCountDownCallBack();
            }
            return;
        }

        this.node && (this.node.opacity = 255);
        this._duration = duration;
        this._endCb = endCountDownCallBack;

        if (this._duration <= 5) {
            this._skeleton?.setAnimation(0, this.animName[1], false);
            AudioManager.getInstance().playEffect(CommonSkin.LoadOnDemand.CountDown, false, 1);
        } else {
            if (this.animName[0] == "") {
                let track = this._skeleton?.setAnimation(0, this.animName[1], false);
                track && (track.trackTime = track.animationEnd);
            } else {
                this._skeleton?.setAnimation(0, this.animName[0], false);
            }
        }
        this.timerLabel && (this.timerLabel.string = this._duration.toFixed());
        this.unschedule(this.running_process);
        this.schedule(this.running_process, 1, this._duration);
    }

    public stopTimer(): void {
        this._endCb = null;
        this._duration = 0;
        this.node && (this.node.opacity = 0);
        this.unschedule(this.running_process);
    }

    private running_process() {
        this._duration--;
        if (this._duration <= 0) {
            this.unschedule(this.running_process);
            if (this._endCb && typeof this._endCb == "function") {
                this._endCb();
            }
            return;
        }
        this.timerLabel && (this.timerLabel.string = this._duration.toFixed());
        if (this._duration <= 5) {
            this._skeleton?.setAnimation(0, this.animName[1], false);
            AudioManager.getInstance().playEffect(CommonSkin.LoadOnDemand.CountDown, false, 1);
        }
    }

    protected update(dt: number): void {
        if (this._duration <= 0) {
            return;
        }
        if (!this._currBone || !this.timerLabel) {
            return;
        }
        this.timerLabel.node.setPosition(this._currBone.worldX, this._currBone.worldY);
        this.timerLabel.node.scaleX = this._currBone.scaleX;
        this.timerLabel.node.scaleY = this._currBone.scaleY;
    }
}
