import ValueObserver from "../../../../../../main/core/utils/class/ValueObserver";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { UniverseProto } from "../../universe/proto/UniverseProto";
import { CommonBroadCastType } from "../define/CommonBroadCastType";

interface TDataItem extends ProtoObject<UniverseProto.BroadCastNT> {
    vipLevel?: number;
}
type TData = {
    [K in CommonBroadCastType]?: TDataItem[];
};
export default class CommonUserBroadCastObserver extends ValueObserver<TData> {
    private readonly MSG_TYPE_MAX_COUNT: number = 300; //300

    protected onInit(): TData {
        return {};
    }

    public totalLength() {
        return lodash.sum(
            [
                CommonBroadCastType.VipLevelUp,
                CommonBroadCastType.CashSuccess,
                CommonBroadCastType.OnlineReward,
                CommonBroadCastType.Game,
            ].map((v) => {
                return this.get(v, []).length;
            })
        );
    }

    public getOld(del: boolean = false, count: number = 0) {
        const otherCount = lodash.sumBy(
            [
                CommonBroadCastType.CashSuccess,
                CommonBroadCastType.OnlineReward,
                CommonBroadCastType.Game,
            ],
            (value) => {
                return this.get(value, []).length;
            }
        );
        if (otherCount <= count) {
            [
                CommonBroadCastType.CashSuccess,
                CommonBroadCastType.OnlineReward,
                CommonBroadCastType.Game,
            ].forEach((v) => {
                this.set(v, []);
            });
            if (this.get(CommonBroadCastType.VipLevelUp, []).length <= count - otherCount) {
                this.set(CommonBroadCastType.VipLevelUp, []);
            } else {
                this.set(
                    CommonBroadCastType.VipLevelUp,
                    this.get(CommonBroadCastType.VipLevelUp, []).splice(0, count - otherCount)
                );
            }
        } else {
            lodash
                .chain([
                    CommonBroadCastType.CashSuccess,
                    CommonBroadCastType.OnlineReward,
                    CommonBroadCastType.Game,
                ])
                .reduce((pre, type) => {
                    return pre.concat(this.get(type, []));
                }, [])
                .sortBy("Timestamp")
                .splice(0, count)
                .forEach((v: TDataItem) => {
                    let list = this.get(v.BroadCastType as CommonBroadCastType);
                    lodash.remove(list, v);
                    this.set(v.BroadCastType as CommonBroadCastType, list);
                })
                .value();
        }
    }

    public push(value: TDataItem) {
        this.parseData(value);
        const type = value.BroadCastType as CommonBroadCastType;
        const list = this.get(type, []);
        if (lodash.isEmpty(list)) {
            this.set(type, list);
        }
        if (list.length >= 20) {
            list.shift();
        }
        list.push(value);
    }

    private parseData(value: TDataItem) {
        try {
            let desc = value.Desc;
            if (!desc) {
                return;
            }
            let data: {
                kind: string;
                level: number;
            } = JSON.parse(desc);

            if (data.kind === "vip") {
                value.BroadCastType = CommonBroadCastType.VipLevelUp;
                value.vipLevel = data.level;
            }
        } catch {}
    }

    public popMessage(): TDataItem | undefined {
        let type = [
            CommonBroadCastType.VipLevelUp,
            CommonBroadCastType.CashSuccess,
            CommonBroadCastType.OnlineReward,
            CommonBroadCastType.Game,
        ].find((v) => {
            return !lodash.isEmpty(this.get(v, null));
        });
        let array = lodash.sortBy(this.get(type, []), "Timestamp");
        this.set(type, array);
        return array?.pop?.() ?? null;
    }
}
