import { ApiErrorCode } from "../../../../../../main/core/conf/ApiErrorCode";
import { setInstance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import BaseServiceErrorCenter from "./base/BaseServiceErrorCenter";
import { ServiceOperateType } from "./define/ServiceOperateType";
import ServiceErrorNetwordError from "./handler/ServiceErrorNetwordError";
import ServiceErrorNetwordTimeout from "./handler/ServiceErrorNetwordTimeout";
import ServiceEnterHallFailedAlert from "./handler/ServiceEnterHallFailedAlert";
import { ServiceErrorShowToast } from "./handler/ServiceErrorShowToast";
import { ServiceErrorToLoginScene } from "./handler/ServiceErrorToLoginScene";
import { ServiceErrorUpgradeAccount } from "./handler/ServiceErrorUpgradeAccount";
import { ServiceErrorWithdrawAlert } from "./handler/ServiceErrorWithdrawAlert";
import ServiceErrorNetwordErrorAlert from "./handler/ServiceErrorNetwordErrorAlert";

@setInstance
export default class ApiServiceErrorCenter extends BaseServiceErrorCenter {
    protected onInitialize(): void {
        this.applyDeflaultHandler(new ServiceErrorShowToast());

        /**  */
        this.applyHandler(100, new ServiceErrorToLoginScene());
        /**  */
        this.applyHandler(102, new ServiceErrorToLoginScene());
        /**  */
        this.applyHandler(105, new ServiceErrorToLoginScene());
        /** */
        this.applyHandler(106, new ServiceErrorToLoginScene());
        /** 	 */
        this.applyHandler(803, new ServiceErrorUpgradeAccount());
        /** {account_no} */
        this.applyHandler(806, new ServiceErrorWithdrawAlert());
        /**  */
        this.applyHandler(1101, new ServiceErrorToLoginScene());
        /**  */
        this.applyHandler(ApiErrorCode.TIMEOUT, new ServiceErrorNetwordTimeout());
        /**  400 500 */
        this.applyHandler(ApiErrorCode.HTTP_ERROR, new ServiceErrorNetwordError());
        /**  */
        this.applyHandler(ApiErrorCode.OFFLINE, new ServiceErrorNetwordErrorAlert());

        this.initializeLoginHandler();
    }

    private initializeLoginHandler() {
        /**  */
        this.applyHandlerWithType(
            ServiceOperateType.Login,
            ApiErrorCode.TIMEOUT,
            new ServiceEnterHallFailedAlert()
        );
        /**  400 500 */
        this.applyHandlerWithType(
            ServiceOperateType.Login,
            ApiErrorCode.HTTP_ERROR,
            new ServiceEnterHallFailedAlert()
        );
    }
}
