import PopUpViewBase from "../../../../../../../main/core/popUpViewUtils/PopUpViewBase";
import CommonRuleLanguage from "../lang/CommonRuleLanguage";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CachetaRuleDialog extends PopUpViewBase {
    @property({ type: cc.Node, displayName: "" })
    closeN: cc.Node = null;

    @property({ type: cc.Node, displayName: "" })
    left: cc.Node = null;

    @property({ type: cc.ScrollView, displayName: "" })
    right: cc.ScrollView = null;

    @property([cc.Node])
    pages: cc.Node[] = [];

    onLoad() {
        this.left.children.forEach((node, index) => {
            node.on("click", () => {
                this.right.stopAutoScroll();
                this.pages.forEach((node) => {
                    node.active = false;
                });
                this.pages[index].active = true;
                this.pages[index].opacity = 0;
                this.scheduleOnce(() => {
                    this.right.scrollToTop();
                });
                cc.tween(this.pages[index]).to(0.5, { opacity: 255 }).start();
            });
        });

        this.closeN.on("click", () => {
            this.onCloseClick();
        });
        this.initText();
    }

    initText() {
        // cc.find("content/title", this.node).getComponent(cc.Label).string =
        //     CommonRuleLanguage.title;
        let texts = CommonRuleLanguage.rule.cacheta;
        this.left.children.forEach((node, idx) => {
            node.getChildByName("lab").getComponent(cc.Label).string = texts[idx].pageTitle;
            cc.find("selected/lab", node).getComponent(cc.Label).string = texts[idx].pageTitle;
        });

        let content = cc.find("content/right/view/content", this.node);
        content.children.forEach((node, idx) => {
            if (idx == 0) {
                node.getChildByName("tip").getComponent(cc.Label).string = texts[idx].content;
            } else if (idx == 1 || idx == 2) {
                node.getChildByName("title1").getComponent(cc.Label).string = texts[idx].title1;
                node.getChildByName("tip1").getComponent(cc.Label).string = texts[idx].content1;

                node.getChildByName("title2").getComponent(cc.Label).string = texts[idx].title2;
                node.getChildByName("tip2").getComponent(cc.Label).string = texts[idx].content2;

                node.getChildByName("title3").getComponent(cc.Label).string = texts[idx].title3;
                node.getChildByName("tip3").getComponent(cc.Label).string = texts[idx].content3;
                if (idx == 1) {
                    node.getChildByName("tip4").getComponent(cc.Label).string = texts[idx].content4;
                    node.getChildByName("tip5").getComponent(cc.Label).string = texts[idx].content5;
                }
            }
        });
    }

    private onCloseClick(): void {
        this.close();
    }
}
