import { CalendarDayType } from "./CalendarEnum";
import { ICalendarItemInfo } from "./CalendarInterface";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CalendarItem extends cc.Component {
    @property(cc.Label)
    lbDay: cc.Label = null;

    @property(cc.Node)
    spSel: cc.Node = null;

    @property({ tooltip: "" })
    curSelectDayColor: cc.Color = cc.color();

    @property({ tooltip: "" })
    preOrNextMonthColor: cc.Color = cc.color();

    @property({ tooltip: "" })
    nextMonthColor: cc.Color = cc.color();

    @property({ tooltip: "" })
    greaterThanTodayColor: cc.Color = cc.color();

    @property({ tooltip: "" })
    curMonthColor: cc.Color = cc.color();

    private _dayItemInfo: ICalendarItemInfo = null;
    private selectCallFunc: (_dayItemInfo: ICalendarItemInfo) => void = null;
    private selectMinTimestampLimit:number = 0;

     /**
     * 
     * @param func 
     * @param selectMinTimestampLimit  
     */
    public init(func: (_dayItemInfo: ICalendarItemInfo) => void, selectMinTimestampLimit:number) {
        this.selectCallFunc = func;
        this.selectMinTimestampLimit = selectMinTimestampLimit;
    }

    setDay(dayItemInfo: ICalendarItemInfo) {
        this._dayItemInfo = dayItemInfo;
        this.lbDay.string = String(this._dayItemInfo.day);

        let dayTimestamp = new Date(
            this._dayItemInfo.inYear,
            this._dayItemInfo.inMonth,
            this._dayItemInfo.day
        ).getTime();
        let curTimestamp = new Date(
            this._dayItemInfo.curYear,
            this._dayItemInfo.curMonth,
            this._dayItemInfo.curDay
        ).getTime();
        
        this.setDayLabColor(dayTimestamp, curTimestamp, dayItemInfo.isSelect);
        this.setDayBG(dayItemInfo.isSelect);
        this.setInteractable(dayTimestamp, curTimestamp);
    }

    private setInteractable(dayTimestamp: number, curTimestamp: number) {
        if (dayTimestamp > curTimestamp || dayTimestamp < this.selectMinTimestampLimit) {
            this.node.getComponent(cc.Button).interactable = false;
        } else {
            this.node.getComponent(cc.Button).interactable = true;
        }
    }

    private setDayBG(isCurSelectDay:Boolean) {
        if (isCurSelectDay) {
            this.spSel.active = true;
        } else {
            this.spSel.active = false;
        }
    }

    private setDayLabColor(dayTimestamp: number, curTimestamp: number, isCurSelectDay:Boolean) {
        if (isCurSelectDay) {
            this.lbDay.node.color = this.curSelectDayColor; // 
        } else {
            if (dayTimestamp > curTimestamp) {
                if (this._dayItemInfo.belongTo == CalendarDayType.IN_NEXT_MONTH) {
                    //  
                    this.lbDay.node.color = this.nextMonthColor;
                } else {
                    this.lbDay.node.color = this.greaterThanTodayColor; //  
                }
            } else {
                if (
                    this._dayItemInfo.belongTo == CalendarDayType.IN_PRE_MONTH ||
                    this._dayItemInfo.belongTo == CalendarDayType.IN_NEXT_MONTH
                ) {
                    // 
                    this.lbDay.node.color = this.preOrNextMonthColor;
                } else {
                    if (dayTimestamp < this.selectMinTimestampLimit) {
                        this.lbDay.node.color = this.greaterThanTodayColor; //  
                    } else {
                        this.lbDay.node.color = this.curMonthColor; // 
                    }
                }
            }
        }
    }

    onClickItem() {
        if (this.selectCallFunc) {
            this.selectCallFunc(this._dayItemInfo);
        }
    }
}
