import { GameType } from "../../../../../../main/core/conf/GameType";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { Tools } from "../../../../../../main/core/utils/Tools";

interface IGameCode {
    [index: number]: string;
}

export default class HostListLogic {
    constructor(private _template: string, private _gameCode: IGameCode = {}) {}

    public mergeGameCode(gameCode: IGameCode) {
        lodash.merge(this._gameCode, gameCode);
    }

    public getHosts(hosts: string[], gameType: GameType) {
        return lodash
            .chain(hosts)
            .map((host) =>
                Tools.formatString(this._template, {
                    game_code: this._gameCode[gameType],
                    host,
                })
            )
            .value();
    }
}
