import { GameType } from "../../../../../../../main/core/conf/GameType";
import PopUpViewBase from "../../../../../../../main/core/popUpViewUtils/PopUpViewBase";
import { IntervalsDecorator } from "../../../../../../../main/core/utils/decorators/IntervalsDecorator";
import { SceneControl } from "../../../SceneControl";
import GameReplayControl from "../GameReplayControl";
// Learn TypeScript:
//  - https://docs.cocos.com/creator/manual/en/scripting/typescript.html
// Learn Attribute:
//  - https://docs.cocos.com/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

const { ccclass, property } = cc._decorator;

@ccclass
export default class CommonGameReplay extends PopUpViewBase {
    @property(cc.Label)
    protected speedBtnLb: cc.Label = null;

    @property(cc.Node)
    playBtn: cc.Node = null;

    @property(cc.Node)
    pause: cc.Node = null;

    protected ctrl: GameReplayControl = null;

    public init(ctrl: GameReplayControl): void {
        this.ctrl = ctrl;
        ctrl.play();
    }
    @IntervalsDecorator
    public onBackBtnClick() {
        this.ctrl.destroy();
        SceneControl.getInstance().launchScene(GameType.GAME_HALL);
    }
    @IntervalsDecorator
    public onPlayBtnClick() {
        this.ctrl.resume();
        this.playBtn.active = !(this.pause.active = true);
    }
    @IntervalsDecorator
    public onPauseBtnClick() {
        this.ctrl.pause();
        this.playBtn.active = !(this.pause.active = false);
    }

    @IntervalsDecorator
    public onRePlayBtnClick() {
        this.ctrl.stop();
        this.ctrl.play();
    }

    private _speedIndex = 0;
    public onSpeedBtnClick() {
        let speeds = [1, 2, 3];
        this._speedIndex = (this._speedIndex + 1) % speeds.length;
        this.ctrl.timeScale = speeds[this._speedIndex];
        this.speedBtnLb.string = `x${speeds[this._speedIndex]}`;
    }
}
