import CacheObserver from "../../../../../../main/core/utils/class/CacheObserver";
import { setInstance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { Tools } from "../../../../../../main/core/utils/Tools";

interface IUser {
    previous: number;
    current: number;
    count: number;
}

interface IData {
    [userId: string]: Partial<IUser>;
}

@setInstance
export default class CommonUserLastLoginObserver extends CacheObserver<IData> {
    protected getCacheKey(): string {
        return "_user_last_login_time";
    }

    private toUserId(userId: number) {
        return `uid:${userId}`;
    }

    public loginCount(userId: number) {
        return lodash.get(this.get(this.toUserId(userId), {}), "count", 0);
    }

    public login(userId: number) {
        let userData = this.get(this.toUserId(userId), { previous: 0, current: 0, count: 0 });

        let nowTime = Date.now();
        [userData.previous, userData.current] = [userData.current, nowTime];

        if (userData.previous != 0 && Tools.isToday(userData.previous, userData.current)) {
            userData.count++;
        } else {
            userData.count = 0;
        }
        this.set(this.toUserId(userId), userData);
    }
}
