import CommonLanguage from "../../conf/lang/CommonLanguage";

class BrcGraphicsMgr {
    static drawTrendTableView(
        graphics,
        itemSize,
        maxLine,
        maxItem,
        tableBgColor: string = CommonLanguage.tableBgColor,
        tableLineFillColor: string = CommonLanguage.tableLineFillColor,
        tableLineStrokeColor: string = CommonLanguage.tableLineStrokeColor
    ): void {
        if (graphics) {
            graphics.clear();

            if (tableBgColor) {
                graphics.fillColor = cc.color().fromHEX(tableBgColor);
                let rectX = 0;
                let rectY = 0;
                let rectWidth = itemSize * maxItem;
                let rectHeight = itemSize * maxLine;
                graphics.rect(rectX, rectY, rectWidth, rectHeight);
                graphics.fill();
            }

            graphics.fillColor = cc.color().fromHEX(tableLineFillColor);
            graphics.strokeColor = cc.color().fromHEX(tableLineStrokeColor);
            graphics.lineWidth = 2;
            graphics.lineJoin = cc.Graphics.LineJoin.ROUND;
            graphics.lineCap = cc.Graphics.LineCap.ROUND;
            let x = 0;
            let y = itemSize * maxLine;
            for (let line = 0; line < maxLine + 1; line++) {
                graphics.moveTo(x, y - itemSize * line);
                graphics.lineTo(itemSize * maxItem + x, y - itemSize * line);
            }
            x = 0;
            y = 0;
            for (let row = 0; row < maxItem + 1; row++) {
                graphics.moveTo(x + itemSize * row, y);
                graphics.lineTo(x + itemSize * row, y + itemSize * 6);
            }
            graphics.stroke();
        }
    }
}

export default BrcGraphicsMgr;
