import Flavor from "../../../../../../../main/core/conf/Flavor";
import Listener from "../../../../../../../main/core/utils/Listener";
import CommonEventName from "../../../CommonEventName";
import UIMgr from "../../../Component/base/UIMgr";
import CommonLanguage from "../../../conf/lang/CommonLanguage";
import BaseServiceErrorHandler from "./base/BaseServiceErrorHandler";

export class ServiceErrorWithdrawAlert extends BaseServiceErrorHandler {
    protected onCatchError(data: ServiceError.IError): void {
        let accountColor = "#ffecba";
        if (Flavor.Skin.isWorldCup()) {
            accountColor = "#01ecff";
        } else if (Flavor.Skin.isLight()) {
            accountColor = "#ffecba";
        }
        UIMgr.showAlert({
            contentText: data.message.format(accountColor) || ``,
            negativeBtnText: CommonLanguage.iKonw,
            positiveBtnText: CommonLanguage.bindOther,
        }).onClose((result) => {
            if (result) {
                this.onConfirmBtnClick();
            } else {
                this.onCancalBtnClick();
            }
        });
    }

    protected onConfirmBtnClick(): void {
        Listener.emit(CommonEventName.WITHDRAW_SELECT_TAB_INDEX, 1);
    }

    protected onCancalBtnClick(): void {}
}
