import { GameType } from "../../../../../../main/core/conf/GameType";
import CommonSkin from "../../conf/skin/CommonSkin";

export default class CardTypeResultConfig {
    public static getResultSpriteBg(level) {
        if (level == "1") {
            return CommonSkin.LoadOnDemand.level_1;
        } else if (level == "2") {
            return CommonSkin.LoadOnDemand.level_2;
        } else if (level == "3") {
            return CommonSkin.LoadOnDemand.level_3;
        } else {
            return CommonSkin.LoadOnDemand.level_4;
        }
    }

    public static getSkeletonAnimAndBone(level) {
        if (level == "1") {
            return {
                skeletonAnim: "animation_4",
                bone: "text_4",
            };
        } else if (level == "2") {
            return {
                skeletonAnim: "animation_3",
                bone: "text_3",
            };
        } else if (level == "3") {
            return {
                skeletonAnim: "animation_2",
                bone: "text_2",
            };
        } else {
            return {
                skeletonAnim: "animation_1",
                bone: "text_1",
            };
        }
    }

    public static getRootNodeScale(gameID: number) {
        return 1;
    }
}
