import { Tools } from "../../../../../main/core/utils/Tools";

export namespace TimerUtil {
    type TimeKey = string | symbol;

    const timerMap: Map<TimeKey, NodeJS.Timer> = new Map();

    export async function steup<T>(
        key: TimeKey,
        callfunc: (count: number, option?: T) => T,
        time: number,
        option: T = null
    ) {
        stop(key);
        let count = 0;
        option = callfunc(count++, option);

        timerMap.set(
            key,
            setInterval(() => {
                option = callfunc(count++, option);
            }, time)
        );
    }

    export function steupOnce<T>(
        key: TimeKey,
        callfunc: (count: number, option?: T) => T,
        time: number,
        option: T = null
    ) {
        stop(key);
        let count = 0;

        timerMap.set(
            key,
            setInterval(() => {
                option = callfunc(count++, option);
                stop(key);
            }, time)
        );
    }

    export function stop(key: TimeKey) {
        let timeId = timerMap.get(key);
        if (timeId) {
            clearInterval(timeId);
        }
        timerMap.delete(key);
        if (timeId) {
            return true;
        }
        return false;
    }
}
