import { AccountType } from "../../../../../../main/core/conf/GameEnum";
import PopUpViewBase from "../../../../../../main/core/popUpViewUtils/PopUpViewBase";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { Tools } from "../../../../../../main/core/utils/Tools";
import UIMgr from "../../Component/base/UIMgr";
import CommonLanguage from "../../conf/lang/CommonLanguage";
import ApiMgr from "../../service/api/ApiMgr";
import { ApiProto } from "../../service/api/ApiProto";
import { CommonUserUtil } from "../CommonUserUtil";
import { CommonAccountUtil } from "../utils/CommonAccountUtil";
import { CommonUserLoginCheckUtil } from "../utils/CommonUserLoginCheckUtil";
import { CommonUserLoginUiUtil } from "../utils/CommonUserLoginUiUtil";
import EnumController from "../../../../../../main/core/ui/component/EnumController";
import { Instance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import CommonPromoterVerifyBrdObserver from "../../obs/CommonPromoterVerifyBrdObserver";
import EnumActive from "../../../../../../main/core/ui/component/EnumActive";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Register extends PopUpViewBase {
    @property({ type: cc.EditBox })
    accountNode: cc.EditBox = null;

    @property({ type: cc.EditBox })
    pwdNode: cc.EditBox = null;

    @property({ type: cc.EditBox })
    confirmPwdNode: cc.EditBox = null;

    @property({ type: cc.EditBox })
    inviteEdit: cc.EditBox = null;

    @property(EnumActive)
    inviteNode: EnumActive = null;
    public init() {
        this.getComponent(EnumController)?.show4Name(
            Instance(CommonPromoterVerifyBrdObserver).get() ? "invite" : "normal"
        );
    }

    protected onHasInviteCodeClicked() {
        this.inviteNode.show4Name("editBox");
    }

    protected onCloseClick(event: { target: cc.Node }) {
        Tools.clickManager(event.target);
        this.close(true);
    }

    private verifyData(accountStr: string, pwdStr: string, confirmPwd: string) {
        if (!CommonUserLoginCheckUtil.checkAccount(accountStr)) {
            return false;
        }
        if (!CommonUserLoginCheckUtil.checkPassword(pwdStr)) {
            return false;
        }
        if (!CommonUserLoginCheckUtil.checkPassword(confirmPwd)) {
            return false;
        }

        if (pwdStr != confirmPwd) {
            UIMgr.showToast(CommonLanguage.passwordDifferent);
            return false;
        }

        return true;
    }

    @UIMgr.applyLoadingUi
    protected async onRegisterBtnClick(event: { target: cc.Node }) {
        let self = this;

        let accountStr = lodash.toLower(this.accountNode.string);
        let pwdStr = this.pwdNode.string;
        let confirmPwd = this.confirmPwdNode.string;

        if (!self.verifyData(accountStr, pwdStr, confirmPwd)) {
            return;
        }

        const inviteCode = Number(this.inviteEdit?.string || 0);

        if (!!inviteCode && !(await CommonUserUtil.checkReferId(inviteCode))) {
            UIMgr.showToast(CommonLanguage.invalidReferId);
            return;
        }

        return ApiMgr.getInstance()
            .registerRequest(accountStr, pwdStr, inviteCode)
            .then((data) => {
                self.updateUserInfo(data, accountStr, pwdStr);
                this.close();
                UIMgr.showToast(CommonLanguage.registerLogin);
                return CommonUserUtil.passwordLogin({
                    account: accountStr,
                    password: pwdStr,
                }).then(() => {
                    return CommonUserLoginUiUtil.launchHall();
                });
            });
    }

    private updateUserInfo(data: ProtoObject<ApiProto.LoginInfo>, accountStr, pwdStr) {
        if (data.newbie_type === ApiProto.NewbieType.NB_REGISTER) {
            CommonDepend.LocalStorageMgr.saveUserDataEvent(data);
            CommonDepend.AdjustTrack.trackEvent(CommonDepend.AdjustTrack.user_active);
        }
        CommonDepend.AdjustTrack.trackEvent(CommonDepend.AdjustTrack.register_account);

        CommonAccountUtil.addAccount(
            CommonDepend.UserManager.uid,
            CommonDepend.UserManager.token,
            accountStr,
            pwdStr,
            null,
            AccountType.TYPE_ACCOUNT
        );
    }

    protected formatPasswordEditbox(event: cc.EditBox): void {
        let editbox = event;
        let str = editbox.string;
        editbox.textLabel.string = "";
        for (let i = 0; i < str.length; i++) {
            editbox.textLabel.string += "";
        }
    }
}
