import { AppParamManager } from "../../../../../main/core/app/AppParamManager";
import Flavor from "../../../../../main/core/conf/Flavor";
import { ProjectInfo } from "../../../../../main/core/conf/ProjectInfo";
import { PlatformUtil } from "../../../../../main/core/platform/PlatformUtil";
import { UserManager } from "../../../../../main/core/user/UserManager";
import { lodash } from "../../../../../main/core/utils/NpmExport";
import UIMgr from "../Component/base/UIMgr";
import CommonLanguage from "../conf/lang/CommonLanguage";
import { UniverseProto } from "../universe/proto/UniverseProto";
import { BrowserUtil } from "../utils/BrowserUtil";

class H5Customer {
    private static _instance: H5Customer = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new H5Customer();
        }

        return this._instance;
    }

    private constructor() {}

    private customerInfo: ProtoObject<UniverseProto.CustomerConfResp>;

    public setCustomerInfo(customerInfo: ProtoObject<UniverseProto.CustomerConfResp>) {
        this.customerInfo = customerInfo;
    }

    public getCustomerInfo(): ProtoObject<UniverseProto.CustomerConfResp> {
        return this.customerInfo;
    }

    private h5CustomerBase(pathAndFixedParams: string, direction?: BrowserUtil.DirectionType) {
        let customerInfo = this.getCustomerInfo();
        if (customerInfo) {
            let token = customerInfo.token;
            let domain = customerInfo.domain;
            let browser_type = customerInfo.browser_type;

            if (lodash.isEmpty(domain)) {
                return;
            }

            // scheme
            let prefixIndex = domain.indexOf("https");
            if (prefixIndex < 0) {
                domain = "https://" + domain;
            }
            // host
            let arr = domain.split("?");
            let host = arr[0];
            if (host.charAt(host.length - 1) != "/") {
                host += "/";
            }
            // token
            if (pathAndFixedParams.includes("token={0}")) {
                pathAndFixedParams = pathAndFixedParams.format(token);
            }
            let fullUrl = host + pathAndFixedParams;
            // dynamicParams()
            if (arr.length > 1) {
                let dynamicParams = arr[1];
                fullUrl += "&" + dynamicParams;
            }

            BrowserUtil.open({
                url: fullUrl,
                type: browser_type,
                direction,
                param: {
                    code: Flavor.Country.code,
                    aid: CommonDepend.AppParamManager.getDeviceID(),
                    level: UserManager.getInstance().vip,
                    brand: AppParamManager.getInstance().getBrandCode(),
                    chn: AppParamManager.getInstance().getChn(),
                    game_version: ProjectInfo.getInstance().GAME_VERSION,
                    platform: PlatformUtil.getInstance().getSvn(),
                },
            });
        } else {
            UIMgr.showToast(CommonLanguage.abnormalLaterRetry);
        }
    }

    public toCustomerPage(direction?) {
        let userId = UserManager.getInstance().user_id;
        let url: string = `customerServicePage/?token={0}&user_id=${userId}`;
        this.h5CustomerBase(url, direction);
    }

    public toCustomerWorkOrder(direction?: BrowserUtil.DirectionType) {
        let userId = UserManager.getInstance().user_id;
        let url: string = `withDraw/?token={0}&problem_id=2&template_code=204&user_id=${userId}&second_id=5`;
        this.h5CustomerBase(url, direction);
    }

    public toRechargeAppealPage(orderid?, direction?: BrowserUtil.DirectionType) {
        let userId = UserManager.getInstance().user_id;
        let url: string = `rechargeStatement/?token={0}&user_id=${userId}&order_id=${orderid}&problem_id=1&template_code=501&second_id=1`;
        this.h5CustomerBase(url, direction);
    }

    public toWithdrawAppealPage(orderid?, direction?: BrowserUtil.DirectionType) {
        let userId = UserManager.getInstance().user_id;
        let url: string = `withDraw/?token={0}&user_id=${userId}&order_id=${orderid}&problem_id=2&template_code=201&second_id=2`;
        this.h5CustomerBase(url, direction);
    }

    public toWithdrawPermissionPage(direction?: BrowserUtil.DirectionType) {
        let userId = UserManager.getInstance().user_id;
        let url: string = `withDraw/?token={0}&user_id=${userId}&problem_id=2&template_code=204&second_id=5`;
        this.h5CustomerBase(url, direction);
    }

    public toLogin() {
        let url: string = `accountFeedback/?problem_id=9&template_code=302&second_id=32`;
        this.h5CustomerBase(url);
    }
}

export default H5Customer;
