import CacheObserver from "../../../../../main/core/utils/class/CacheObserver";
import { setInstance } from "../../../../../main/core/utils/decorators/InstanceClassDecorator";

type TData = {
    [key: string]: number;
};

@setInstance
export default class CommonHostListSwitchCountObserver extends CacheObserver<TData> {
    private readonly max_count = 5;

    protected getCacheKey(): string {
        return "_host_list_switch_count";
    }

    public addSwitchCount(host: string) {
        this.set(host, (value = 0) => {
            return value + 1;
        });

        return this.get(host) < this.max_count;
    }
}
