import { BundleCommonMgr } from "./bd/BundleCommonMgr";
import CommonSkin from "./conf/skin/CommonSkin";

export class CommonPrefabMgr {
    private static _instance: CommonPrefabMgr = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new CommonPrefabMgr();
        }

        return this._instance;
    }

    private constructor() {}

    public createCommonLoading(message: string): void {
        let canvas = CommonDepend.CocosUtils.getSceneCanvas();
        UIDepend.PrefabManager.loadPrefabInsBundle(
            BundleCommonMgr.getInstance().getBundleNameDescForLoadingUI(),
            (prefab: cc.Node) => {
                if (cc.isValid(canvas) && prefab) {
                    let oldLoading = canvas.getChildByName("Loading");
                    if (oldLoading) {
                        canvas.removeChild(oldLoading);
                    }
                    canvas.addChild(prefab, canvas.childrenCount + 1);
                    if (prefab.getComponent("Loading")) {
                        prefab.getComponent("Loading").init(message);
                    }
                    prefab.x = 0;
                    prefab.y = 0;
                }
            }
        );
    }

    public removeCommonLoading() {
        let isRemoved = false;

        let timeRemoveLoadingFirst = setTimeout(function () {
            removeLogic();
            clearTimeout(timeRemoveLoadingFirst);
        }, 150);

        if (isRemoved == false) {
            let timeRemoveLoading = setTimeout(function () {
                removeLogic();
                clearTimeout(timeRemoveLoading);
            }, 500);
        }

        function removeLogic() {
            let canvas = CommonDepend.CocosUtils.getSceneCanvas();
            let oldLoading = canvas.getChildByName("LoadingUI");
            if (oldLoading && cc.isValid(canvas)) {
                canvas.removeChild(oldLoading);
                isRemoved = true;
            }
        }
    }

    public removeCommonLoadingWithBrc() {
        let canvas = CommonDepend.CocosUtils.getSceneCanvas();
        if (cc.isValid(canvas) && canvas.children) {
            for (let i = canvas.children.length - 1; i >= 0; i--) {
                let child = canvas.children[i];
                if (child && child.name == "LoadingWithFont") {
                    canvas.removeChild(child);
                }
            }
        }
    }

    public loadRuleContentToNode(parent, gameId, completeCallback) {
        let prefabPath = BundleCommonMgr.getInstance().getBundleNameDescForGameRuleContent();
        UIDepend.PrefabManager.loadPrefabInsBundle(prefabPath, function (prefab) {
            if (prefab && cc.isValid(parent)) {
                if (prefab.getComponent("RuleContent")) {
                    prefab.getComponent("RuleContent").loadContent(gameId);
                }
                parent.addChild(prefab);
                if (typeof completeCallback === "function") {
                    completeCallback();
                }
            }
        });
    }

    public createCommonLoadingWithBrc(options?) {
        let self = this;
        let canvas = CommonDepend.CocosUtils.getSceneCanvas();
        self.removeCommonLoadingWithBrc();

        let bundleDesc: IResDescribe =
            BundleCommonMgr.getInstance().getBundleNameDescForLoadingWithFont();
        UIDepend.PrefabManager.loadPrefabInsBundle(bundleDesc, (node: cc.Node) => {
            if (cc.isValid(canvas) && node) {
                canvas.addChild(node, 50);
                let posX = 0;
                let posY = 0;
                if (options) {
                    if (options.x) {
                        posX = options.x;
                    }
                    if (options.y) {
                        posY = options.y;
                    }
                }
                node.x = posX;
                node.y = posY;
            }
        });
    }
}
