import UIMgr from "../Component/base/UIMgr";

export namespace BlackIpUtil {
    export let blackListFlag: boolean | string = false;

    /**
     * 
     */
    export function chcekIsInBlackList(showAlert = true) {
        let isInBlackList = !!BlackIpUtil.blackListFlag;
        isInBlackList &&
            showAlert &&
            UIMgr.showAlert({
                contentText: BlackIpUtil.blackListFlag as string,
                contextAlign: cc.macro.TextAlignment.CENTER,
                style: UIMgr.ALERT_STYLE.POSITIVE,
            });
        return isInBlackList;
    }
}
