import { BundleMgr } from "../../../../../main/core/bd/BundleMgr";
import { H5Util } from "../../../../../main/core/platform/H5Util";
import { PlatformType } from "../../../../../main/core/platform/PlatformType";
import SkeletonHelper from "../../../../../main/core/utils/class/SkeletonHelper";
import { lodash } from "../../../../../main/core/utils/NpmExport";
import { Tools } from "../../../../../main/core/utils/Tools";
import CommonSkin from "../conf/skin/CommonSkin";

export namespace BrowserUtil {
    type ServerStringType = "sys_url" | "app_url" | string;
    type ServerNumberType = BrowserType;
    type OpenType = ServerNumberType | ServerStringType;
    export type QueryParamsType = { [key: string]: string };

    interface OpenOption {
        type: OpenType;
        url: string;
        direction?: DirectionType;
        param?: any;
        withUserInfo?: boolean;
        hoverMenu?: boolean;
    }

    export enum BrowserType {
        None,
        WebView,
        Browser,
    }

    export enum DirectionType {
        None,
        Landscape,
        Portrait,
    }

    export function transateBrowserType(type: string | number): BrowserType {
        // BrowserType.Browser
        let isH5 = H5Util.getInstance().platform === PlatformType.H5;
        if (isH5) return BrowserType.Browser;

        switch (typeof type) {
            case "number": {
                return type;
            }
            case "string": {
                return {
                    ["sys_url"]: BrowserType.Browser,
                    ["app_url"]: BrowserType.WebView,
                }[type];
            }
        }
    }

    export function open(option: OpenOption) {
        const {
            type,
            url,
            direction = DirectionType.None,
            withUserInfo = false,
            hoverMenu = false,
            param = {},
        } = option;
        const transateType = transateBrowserType(type);

        if (withUserInfo) {
            lodash.merge(param, {
                userid: CommonDepend.UserManager.uid,
                token: CommonDepend.UserManager.token,
                brand_code: CommonDepend.AppParamManager.getBrandCode(),
                aid: CommonDepend.AppParamManager.getDeviceID(),
            });
        }

        lodash.merge(param, {
            orientation: [undefined, "landscape", "portrait"][direction],
            hoverMenu,
        });

        const newUrl = Tools.transateUrlParams(url, param);

        return openBrowser(transateType, newUrl, direction);
    }

    export async function openBrowser(type: BrowserType, url: string, direction: DirectionType) {
        switch (type) {
            case BrowserType.WebView: {
                await playPortraitAnim(direction);
                CommonDepend.PlatformUtil.openUrlByWebView(url);
                break;
            }
            case BrowserType.Browser: {
                let isSafari = H5Util.getInstance().isSafari;
                !isSafari && (await playPortraitAnim(direction));
                CommonDepend.PlatformUtil.openUrlByBrowser(Promise.resolve(url));
                break;
            }
        }
    }

    export async function playPortraitAnim(direction: DirectionType) {
        if (direction !== DirectionType.Portrait) {
            return;
        }
        //
        let canvas: cc.Node = CommonDepend.CocosUtils.getSceneCanvas();
        const helper = SkeletonHelper.createInParent(canvas, {
            position: cc.v3(), //
        });
        try {
            await helper.load(CommonSkin.Priority.JumpToAppealAnim);
            await helper.play(helper.getAnimationNames()[0]);
            helper.getNode().removeFromParent();
            return true;
        } catch (e) {
            return true;
        }
    }

    export function queryStringEncoding(obj: QueryParamsType) {
        let str = [];
        for (let p in obj)
            if (Object.prototype.hasOwnProperty.call(obj, p)) {
                str.push(encodeURIComponent(p) + "=" + encodeURIComponent(obj[p]));
            }
        return str.join("&");
    }
}
