import { GameType } from "../../../../../main/core/conf/GameType";
import { PlatformType } from "../../../../../main/core/platform/PlatformType";
import { PlatformUtil } from "../../../../../main/core/platform/PlatformUtil";
import { lodash } from "../../../../../main/core/utils/NpmExport";
import BaseSocketMgr from "../service/ws/BaseSocketMgr";
import { UniverseProto } from "./proto/UniverseProto";
import { UniverseProtoMap } from "./proto/UniverseProtoMap";

const { MessageID } = UniverseProto;

type TMessage = UniverseProto.MessageID;

class UniverseSocketMgr extends BaseSocketMgr<TMessage> {
    protected gameId: number = GameType.UNIVERSE;
    protected host: string;
    protected isEncrypt: boolean = true;
    protected cryptId: number = 2000;
    protected aesKey: string = "SEWkGIc2RiqcASstuR4kuw==";

    constructor() {
        super();
        this.applyConfig({
            log: {
                blacklist: [MessageID.BROADCAST_NT],
            },
        });
    }

    public getMessageCtor<T extends TMessage>(msgType: T) {
        return UniverseProtoMap[msgType];
    }

    public onAuthReq() {
        let data: ProtoObject<UniverseProto.AuthReq> = {
            UserID: CommonDepend.UserManager.uid,
            Token: CommonDepend.UserManager.token,
            DeviceID: CommonDepend.AppParamManager.getDeviceID(),
            Package: CommonDepend.AppParamManager.getPackageName(),
            Channel: CommonDepend.AppParamManager.getChn(),
            VersionName: CommonDepend.ProjectInfo.GAME_VERSION,
            VersionCode: CommonDepend.AppParamManager.getAppVersionCode(),
            BrandCode: CommonDepend.CommonSocketModel.getBrandCode(),
            Platform: PlatformUtil.getInstance().getSvn(),
            PackageType: {
                [PlatformType.Unknow]: UniverseProto.PackageTypeEnu.PACKAGE_NOT_USED,
                [PlatformType.H5]: UniverseProto.PackageTypeEnu.H5,
                [PlatformType.H5Native]: UniverseProto.PackageTypeEnu.H5,
            }[PlatformUtil.getInstance().type],
        };
        this.request(MessageID.AUTH_REQ, data);
    }

    public onPropsConfReq() {
        let now = new Date().getTime();
        let curr_time = now + CommonDepend.DateUtils.getTimeDiff();
        let data: ProtoObject<UniverseProto.PropsConfReq> = {
            Timestamp: curr_time,
        };
        this.request(MessageID.PROPS_CONF_REQ, data);
    }

    public onCustomerConfReq() {
        let now = new Date().getTime();
        let curr_time = now + CommonDepend.DateUtils.getTimeDiff();
        let data: ProtoObject<UniverseProto.CustomerConfReq> = {
            Timestamp: curr_time,
        };
        this.request(MessageID.CUSTOMER_CONF_REQ, data);
    }

    public onLaunchConfReq() {
        this.request(MessageID.LAUNCH_CONF_REQ, {});
    }

    public request<T extends TMessage>(msgType: T, data: ProtoObject<typeof UniverseProtoMap[T]>) {
        const ctor = this.getMessageCtor(msgType);
        lodash.set(data, "timestamp", new Date().getTime());
        this.send(msgType, new ctor(data));
    }
}
export default new UniverseSocketMgr();
