import { SpriteManager } from "../../../../../../main/core/ui/base/SpriteManager";
import CardTypeResultConfig from "./CardTypeResultConfig";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CardTypeResult extends cc.Component {
    @property(cc.Node)
    typeNode: cc.Node = null;

    @property(sp.Skeleton)
    animBgSp: sp.Skeleton = null;

    @property(cc.Node)
    spriteBgNode: cc.Node = null;

    private _playingAnim = false;
    private _currBone = null;

    public showResult(gameID, resultSpriteFrame, level, showAnim, endCallback) {
        let self = this;
        self.node.scale = CardTypeResultConfig.getRootNodeScale(gameID);
        if (showAnim) {
            self.showAnim(gameID, resultSpriteFrame, level, endCallback);
        } else {
            self.showSpriteFrame(resultSpriteFrame, level, endCallback);
        }
    }

    private showSpriteFrame(resultSpriteFrame, level, endCallback) {
        let self = this;
        if (cc.isValid(self.typeNode)) {
            self.typeNode.active = true;
            if (self.typeNode.getComponent(cc.Sprite)) {
                self.typeNode.getComponent(cc.Sprite).spriteFrame = resultSpriteFrame;
                self.typeNode.y = 0;
            }
        }
        if (cc.isValid(self.animBgSp)) {
            self.animBgSp.node.active = false;
        }
        if (cc.isValid(self.spriteBgNode)) {
            self.spriteBgNode.active = true;
            SpriteManager.getInstance().loadSpriteForNodeBundle(
                self.spriteBgNode,
                CardTypeResultConfig.getResultSpriteBg(level)
            );
        }
        if (endCallback) {
            endCallback();
        }
    }

    private showAnim(gameID, resultSpriteFrame, level, endCallback) {
        let self = this;
        if (cc.isValid(self.typeNode)) {
            self.typeNode.active = true;
            self.typeNode.scale = 0;
            if (self.typeNode.getComponent(cc.Sprite)) {
                self.typeNode.getComponent(cc.Sprite).spriteFrame = resultSpriteFrame;
            }
        }
        if (cc.isValid(self.spriteBgNode)) {
            self.spriteBgNode.active = false;
        }
        if (cc.isValid(self.animBgSp)) {
            self.animBgSp.node.active = true;
            let config = CardTypeResultConfig.getSkeletonAnimAndBone(level);
            self._currBone = this.animBgSp.findBone(config.bone);
            self._playingAnim = true;
            self.animBgSp.setAnimation(0, config.skeletonAnim, false);
            self.animBgSp.setCompleteListener(() => {
                self._playingAnim = false;
                if (endCallback) {
                    endCallback();
                }
            });
        }
    }

    protected update(dt: number): void {
        if (!this._currBone || !this._playingAnim) {
            return;
        }
        this.typeNode.setPosition(this._currBone.worldX, this._currBone.worldY);
        this.typeNode.scaleX = this._currBone.scaleX;
        this.typeNode.scaleY = this._currBone.scaleY;
    }
}
