import { AccountType } from "../../../../../../main/core/conf/GameEnum";
import CommonEventName from "../../CommonEventName";
import { FormatTestUtil } from "../../utils/FormatTestUtil";
import { PlatformUtil } from "../../../../../../main/core/platform/PlatformUtil";
import { AppParamManager } from "../../../../../../main/core/app/AppParamManager";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { UserManager } from "../../../../../../main/core/user/UserManager";

export namespace CommonAccountUtil {
    /**
     * phone
     * @param uid
     * @param token
     * @param phone 
     * @param pwd
     * @param nickName
     * @param accountType
     */
    export function addAccount(uid, token, phone, pwd, nickName, accountType): void {
        if (!phone) {
            phone = CommonDepend.AppParamManager.getDeviceID();
        }
        uid += "";
        if (phone && phone.length >= FormatTestUtil.MIN_ACCOUNT_LENGTH) {
            let newAccounts = [];
            let auto = 0;
            if (uid && uid.length >= 1 && token && token.length >= 10) {
                auto = 1;
            }

            let historyAccounts = getAllHistoryAccounts();
            let foundItem = null;
            if (historyAccounts && historyAccounts.length > 0) {
                let count = historyAccounts.length;
                for (let index = 0; index < count; index++) {
                    let item = historyAccounts[index];
                    if (item) {
                        if (
                            ((uid && item[0] == uid) || (phone && item[2] == phone)) &&
                            item[7] === AppParamManager.getInstance().getBrandCode()
                        ) {
                            foundItem = item;
                            let foundNickName = foundItem[5] || "";
                            let newNickName = nickName || foundNickName;
                            newAccounts.unshift([
                                uid,
                                token,
                                phone,
                                pwd,
                                auto,
                                newNickName,
                                accountType,
                                AppParamManager.getInstance().getBrandCode(),
                            ]);
                        } else {
                            item[4] = 0;
                            newAccounts.push(item);
                        }
                    }
                }
            }

            if (foundItem == null) {
                newAccounts.unshift([
                    uid,
                    token,
                    phone,
                    pwd,
                    auto,
                    nickName,
                    accountType,
                    AppParamManager.getInstance().getBrandCode(),
                ]);
            }
            UserManager.getInstance().initAccount(uid, token);
            saveHistoryAccounts(newAccounts);
        }
        cc.director.emit(CommonEventName.LOGIN_SUCCESS);
    }

    export function deleteAccount(account): void {
        let historyAccounts = getAllHistoryAccounts();
        let newAccounts = [];
        if (historyAccounts) {
            let count = historyAccounts.length;
            for (let index = 0; index < count; index++) {
                let item = historyAccounts[index];
                if (item) {
                    if (
                        item[2] == account &&
                        item[7] == AppParamManager.getInstance().getBrandCode()
                    ) {
                        continue;
                    }
                    newAccounts.push(item);
                }
            }
        }
        saveHistoryAccounts(newAccounts);
    }

    export function clearGuestAccount() {
        let historyAccounts = getAllHistoryAccounts();
        let newAccounts = [];
        if (historyAccounts) {
            let count = historyAccounts.length;
            for (let index = 0; index < count; index++) {
                let account = historyAccounts[index];
                if (isGuestAccount(account)) {
                    continue;
                }
                newAccounts.push(account);
            }
        }
        saveHistoryAccounts(newAccounts);
    }

    /**
     * 
     * @returns {*}
     */
    export function getLastAccount() {
        let historyAccounts = getHistoryAccounts();
        if (historyAccounts) {
            return historyAccounts[0];
        }
        return null;
    }

    export function getAllLastAccountObject() {
        return account2Object(getAllLastAccount());
    }

    export function account2Object(account: (string | number)[]) {
        const [uid, token, phone, pwd, auto, nickName, accountType, brand] = account || [];
        if (!uid) {
            return null;
        }
        return {
            uid,
            token,
            phone,
            pwd,
            auto,
            nickName,
            accountType,
            brand,
        };
    }

    export function updateLastAccount(token): void {
        let historyAccounts = getAllHistoryAccounts();
        if (historyAccounts && historyAccounts[0]) {
            historyAccounts[0][1] = token;
        }
        saveHistoryAccounts(historyAccounts);
    }

    export function updateAccountToLast() {
        let lastAccount = getAllLastAccountObject();
        let historyAccounts = getAllHistoryAccounts();
        historyAccounts.unshift(
            ...lodash
                .chain(historyAccounts)
                .remove((account: string[]) => {
                    return account[0] == lastAccount.uid;
                })
                .thru((array: string[][]) => {
                    if (
                        account2Object(array[0]).brand !=
                        AppParamManager.getInstance().getBrandCode()
                    ) {
                        let newAccount = lodash.cloneDeep(array[0]);
                        newAccount[7] = AppParamManager.getInstance().getBrandCode();
                        array.unshift(newAccount);
                    }
                    return array;
                })
                .value()
        );
        saveHistoryAccounts(historyAccounts);
    }

    export function getAccountByPhone(phone) {
        let historyAccounts = getHistoryAccounts();
        let foundAccount = null;
        if (historyAccounts) {
            for (let i = 0; i < historyAccounts.length; i++) {
                let account = historyAccounts[i];
                if (account) {
                    if (phone && account[2] == phone) {
                        foundAccount = account;
                        break;
                    }
                }
            }
        }
        return foundAccount;
    }

    export function getAllAccountByUserId(userId) {
        let historyAccounts = getAllHistoryAccounts();
        let foundAccount = null;
        if (historyAccounts) {
            for (let i = 0; i < historyAccounts.length; i++) {
                let account = historyAccounts[i];
                if (account) {
                    if (userId && account[0] == userId) {
                        foundAccount = account;
                        break;
                    }
                }
            }
        }
        return foundAccount;
    }

    export function getAccountByUserId(userId) {
        let historyAccounts = getHistoryAccounts();
        let foundAccount = null;
        if (historyAccounts) {
            for (let i = 0; i < historyAccounts.length; i++) {
                let account = historyAccounts[i];
                if (account) {
                    if (userId && account[0] == userId) {
                        foundAccount = account;
                        break;
                    }
                }
            }
        }
        return foundAccount;
    }

    /**
     * 
     */
    export function loadOwnAccounts() {
        let historyAccounts = getHistoryAccounts();
        let newAccounts = [];
        if (historyAccounts) {
            let count = historyAccounts.length;
            for (let index = 0; index < count; index++) {
                let account = historyAccounts[index];
                if (!isGuestAccount(account) && !isFacebookAccount(account)) {
                    newAccounts.push(account);
                }
            }
        }
        return newAccounts;
    }

    export function hasGuestAccount(): boolean {
        let hasGuest = false;
        let historyAccounts = getHistoryAccounts();

        if (historyAccounts && historyAccounts.length > 0) {
            for (let i = 0; i < historyAccounts.length; i++) {
                let account = historyAccounts[i];
                if (isGuestAccount(account)) {
                    hasGuest = true;
                    break;
                }
            }
        }
        return hasGuest;
    }

    export function isGuestAccount(account) {
        let isGuest = false;
        if (account) {
            let phone = account[2];
            let pwd = account[3];
            let accountType = account[6];
            if (accountType) {
                isGuest = accountType == AccountType.TYPE_GUEST;
            } else {
                isGuest = !FormatTestUtil.testPhoneNumber(phone) && !pwd;
            }
        }
        return isGuest;
    }

    export function isFacebookAccount(account) {
        let isFacebook = false;
        if (account) {
            let phone = account[2];
            let accountType = account[6];
            if (accountType) {
                isFacebook = accountType == AccountType.TYPE_FACEBOOK;
            } else {
                isFacebook = phone && phone.startsWith("fb:");
            }
        }
        return isFacebook;
    }

    /**
     * 
     */
    export function getHistoryAccountCount() {
        let historyArray = getHistoryAccounts();
        if (historyArray) {
            return historyArray.length;
        }
        return 0;
    }

    /**  */
    export function getAllLastAccount() {
        let account = getLastAccount();
        if (!account) {
            account = getAllHistoryAccounts()?.[0] ?? null;
            if (isGuestAccount(account)) {
                account = null;
            }
        }
        return account;
    }

    export function getHistoryAccounts() {
        return lodash
            .chain(getAllHistoryAccounts())
            ?.map((item) => {
                if (item[7] === undefined) {
                    item[7] = AppParamManager.getInstance().getBrandCode();
                }
                return item;
            })
            .tap((array) => {
                saveHistoryAccounts(array);
            })
            .filter((item) => item[7] === AppParamManager.getInstance().getBrandCode())
            .value();
    }
    /**
     * 
     * 
     * 
     * 
     * 
     *
     * sdcard
     */
    export function getAllHistoryAccounts(): any[][] {
        let historyAccounts = null;

        let historyAccountsText = PlatformUtil.getInstance().getAccountInfo();
        if (!historyAccountsText) {
            historyAccounts = CommonDepend.LocalStorageMgr.loadKeyChainAccount();
            if (historyAccounts) {
                PlatformUtil.getInstance().saveAccountInfo(JSON.stringify(historyAccounts));
            }
        } else {
            try {
                historyAccounts = JSON.parse(historyAccountsText);
                CommonDepend.LocalStorageMgr.saveKeyChainAccount(historyAccounts);
            } catch (e) {}
        }
        return historyAccounts;
    }

    /**
     * localstoragenative
     * @param historyAccounts
     */
    export function saveHistoryAccounts(historyAccounts) {
        CommonDepend.LocalStorageMgr.saveKeyChainAccount(historyAccounts);
        PlatformUtil.getInstance().saveAccountInfo(JSON.stringify(historyAccounts));
    }
}
