import PopUpViewBase from "../../../../main/core/popUpViewUtils/PopUpViewBase";

const { ccclass, property } = cc._decorator;

/**  */
export enum TipsInfoDir {
    UP = 1,
    DOWN = 2,
    LEFT = 3,
    RIGHT = 4,
}

@ccclass
export default class TipsInfo extends PopUpViewBase {
    @property({ type: cc.Node, tooltip: "tips" })
    tips_up: cc.Node = null;

    @property({ type: cc.Node, tooltip: "tips" })
    tips_down: cc.Node = null;

    @property({ type: cc.Node, tooltip: "tips" })
    tips_left: cc.Node = null;

    @property({ type: cc.Node, tooltip: "tips" })
    tips_right: cc.Node = null;

    private curTips: cc.Node = null;

    onLoad() {
        this.node.on(cc.Node.EventType.TOUCH_END, this.close, this);
    }

    public init(parm: ITipsInfoParm) {
        this.tips_up.active = false;
        this.tips_down.active = false;
        this.tips_left.active = false;
        this.tips_right.active = false;

        if (parm.direction == TipsInfoDir.UP) {
            this.curTips = this.tips_up;
        } else if (parm.direction == TipsInfoDir.DOWN) {
            this.curTips = this.tips_down;
        } else if (parm.direction == TipsInfoDir.LEFT) {
            this.curTips = this.tips_left;
        } else if (parm.direction == TipsInfoDir.RIGHT) {
            this.curTips = this.tips_right;
        } else {
            this.curTips = this.tips_up;
        }
        this.curTips.active = true;

        let lab_info: cc.Label = cc
            .find("img_infoBG/lab_info", this.curTips)
            .getComponent(cc.Label);
        let lab_title: cc.Label = cc
            .find("img_infoBG/lab_title", this.curTips)
            .getComponent(cc.Label);
        if (parm.width) {
            let img_infoBG = cc.find("img_infoBG", this.curTips);
            img_infoBG.width = parm.width;
            lab_info.node.width = parm.width - 30;
            lab_title.node.width = parm.width - 30;
        }
        if (parm.title && parm.title != "") {
            lab_title.overflow = cc.Label.Overflow.RESIZE_HEIGHT;
            lab_title.string = parm.title;
            lab_title["_forceUpdateRenderData"](true); // lable
        } else {
            lab_title.node.active = false;
        }
        lab_info.overflow = cc.Label.Overflow.RESIZE_HEIGHT;
        lab_info.string = parm.content;
        lab_info["_forceUpdateRenderData"](true); // lable
        this.node.getComponent(cc.Widget).updateAlignment();
        let nodePos = this.node.convertToNodeSpaceAR(parm.worldPos);
        this.curTips.setPosition(nodePos);
    }
}
