import { ApiProto } from "../../service/api/ApiProto";
import { CommonResultType } from "../define/CommonResultType";
import { CommonAccountUtil } from "../utils/CommonAccountUtil";
import { ApiErrorHelper } from "../../service/api/ApiErrorHelper";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { CommonUserInfoUtil } from "../utils/CommonUserInfoUtil";
import { ServiceErrorUtils } from "../../service/service_error/ServiceErrorUtils";
import { AdjustTrack } from "../../../../../../main/core/track/AdjustTrack";
import { CommonSocketModel } from "../../../../../../main/core/ws/CommonSocketModel";

export interface ISuccessOptions extends ProtoObject<ApiProto.LoginInfo> {
    isFormal?: boolean;
}

export type IFailureOptions = RespHeader

export interface ILogincHandler {
    onEnterLoginSuccess?: (data: any) => boolean;
    onLoginSuccess?: (data: any) => void;
    onEnterLoginFailure?: (data: any) => boolean;
    onLoginFailure?: (data: any) => void;
}

export default abstract class CommonUserLoginBase<T extends CommonUser.ILoginOptions> {
    protected options: T = null;

    protected handlers: ILogincHandler[] = [];

    public login(options?: T) {
        this.options = options;
        return this.onLogin(options).then(async (response) => {
            switch (response.result) {
                case CommonResultType.Success: {
                    await this.onLoginSuccess(response.content);
                    break;
                }
                case CommonResultType.Failed: {
                    this.onLoginFailure(response.content);
                    break;
                }
                default: {
                    if (CC_DEBUG) {
                        CC_DEBUG && cc.warn("login result is unknown");
                    }
                    break;
                }
            }
            return response;
        });
    }

    public applyHandler(handler: ILogincHandler) {
        if (this.handlers.includes(handler)) {
            return;
        }
        this.handlers.push(handler);
    }

    public removeHandler(handler: ILogincHandler) {
        lodash.pull(this.handlers, handler);
    }

    protected abstract onLogin(options: T): Promise<CommonUser.ILoginResult>;

    protected async onLoginSuccess(data: ISuccessOptions) {
        if (
            this.handlers
                .map((handler) => handler.onEnterLoginSuccess?.(data) ?? true)
                .includes(false)
        ) {
            return;
        }

        CommonDepend.LocalStorageMgr.saveUserDataEvent(data);
        CommonAccountUtil.updateLastAccount(data.token);
        CommonSocketModel.getInstance().init();
        switch (data.newbie_type) {
            case ApiProto.NewbieType.NB_ANONYMOUS: {
                AdjustTrack.getInstance().trackEvent(AdjustTrack.getInstance().user_active);
                break;
            }
        }

        return CommonUserInfoUtil.updateUserInfo().then(() => {
            this.handlers.forEach((handler) => handler.onLoginSuccess?.(data));
        });
    }

    protected onLoginFailure(data: IFailureOptions) {
        if (
            this.handlers
                .map((handler) => handler.onEnterLoginFailure?.(data) ?? true)
                .includes(false)
        ) {
            return;
        }

        this.handlers.forEach((handler) => handler.onLoginFailure?.(data));
    }
}
