const { ccclass, property } = cc._decorator;

import DynamicMounNodeUtil from "./DynamicMounNodeUtil";

@ccclass
export default class DynamicMounNodeComponent extends cc.Component {

    @property(cc.Node)
    spSkeletonNode: cc.Node = null;
    _spSkeleton: sp.Skeleton = null;

    _DynamicMounNodeUtil = null;

    _mounNode = null;
    _boneName = null;

    _playingAnim = false;

    onLoad() {
        let self = this;
        self._spSkeleton = self.spSkeletonNode.getComponent(sp.Skeleton);
        self._playingAnim = false;
        self._DynamicMounNodeUtil = new DynamicMounNodeUtil(self.spSkeletonNode);
    }

    startSkeletonAnimation(mounNode, skeletonAnimName, boneName, completeListener, addEventListener = null) {
        let self = this;
        if (!self._spSkeleton) {
            return;
        }
        self._mounNode = mounNode;
        self._boneName = boneName;
        self._playingAnim = true;
        self.spSkeletonNode.active = true;
        self.spSkeletonNode.opacity = 255;

        self._spSkeleton.setEventListener(function (trackEntry, eventData) {
            if (eventData) {
                let dataInfo = eventData.data;
                if (dataInfo) {
                    let trackName = dataInfo.name;
                    if (trackName) {
                        if (addEventListener) {
                            addEventListener(trackName);
                        }
                    }
                }
            }
        });

        self._spSkeleton.setAnimation(0, skeletonAnimName, false);
        self._spSkeleton.setCompleteListener(function () {
            self._playingAnim = false;
            if (completeListener) {
                completeListener();
            }
        });
    }

    getSpineSkeleton() {
        return this._spSkeleton;
    }

    update(dt) {
        let self = this;
        if (!self._playingAnim) {
            return;
        }
        if (self._DynamicMounNodeUtil) {
            self._DynamicMounNodeUtil.updateMountNode(self._mounNode, self._boneName);
        }
    }

}