import CommonLanguage from "../conf/lang/CommonLanguage";

const { ccclass, property } = cc._decorator;

@ccclass
export default class LoadingWithFont extends cc.Component {
    @property(cc.Label)
    text: cc.Label = null;

    protected onLoad(): void {
        let self = this;
        if (cc.isValid(self.text)) {
            UIDepend.LabelManager.setLabelString(self.text, CommonLanguage.computeing);
            self.scheduleOnce(() => {
                self.updateBgWidth();
            });
        }
    }

    private updateBgWidth() {
        let bgNode = this.node.getChildByName("bg");
        if (!cc.isValid(bgNode)) return;
        if (bgNode.width < this.text.node.width) {
            bgNode.width = this.text.node.width + 100;
        }
    }
}
