import { GameType } from "../../../../main/core/conf/GameType";

const { ccclass, property } = cc._decorator;

@ccclass
export default class I18nUtil extends cc.Component {
    @property({ type: cc.Enum(GameType) })
    gameType = GameType.GAME_HALL;

    @property(cc.String)
    wroldPath: string = "";

    @property(cc.Integer)
    maxLength: number = -1;

    private _targetComponent: cc.Label | cc.RichText | cc.EditBox;

    async onEnable() {
        // node  cc.Label  cc.RichText
        let hasValidComponent =
            this.node.getComponent(cc.Label) || this.node.getComponent(cc.RichText) || cc.EditBox;
        if (!hasValidComponent) {
            throw new Error(" cc.Label  cc.RichText ");
        }
        let langMap = await this.getLanguageMap();
        if (!langMap) return;

        if (this.node.getComponent(cc.Label)) {
            this._targetComponent = this.node.getComponent(cc.Label);
            let text = this.getValueByObjPath(langMap, this.wroldPath);
            this._targetComponent.string = text;
        } else if (this.node.getComponent(cc.RichText)) {
            this._targetComponent = this.node.getComponent(cc.RichText);
            let text = this.getValueByObjPath(langMap, this.wroldPath);
            this._targetComponent.string = text;
        } else if (this.node.getComponent(cc.EditBox)) {
            this._targetComponent = this.node.getComponent(cc.EditBox);
            let text = this.getValueByObjPath(langMap, this.wroldPath);
            this._targetComponent.placeholder = text;
        }

        //
        if (this.maxLength > -1) {
            this._targetComponent.string = this._targetComponent.string.limtLength(this.maxLength);
        }
    }

    getLanguageMap() {
        return new Promise((resolve, reject) => {
            let modulePath;
            switch (this.gameType) {
                case GameType.SHARK:
                    modulePath = "SharkLanguage";
                    break;

                case GameType.WALLET:
                    modulePath = "WalletLanguage";
                    break;
                case GameType.GAME_HALL:
                    modulePath = "HallLanguage";
                    break;
            }

            import(modulePath)
                .then((obj) => {
                    resolve(obj.default);
                })
                .catch((err) => {
                    reject(err);
                });
        });
    }

    getValueByObjPath(obj, path: string) {
        const paths = path.split(".");
        let res = obj;
        let tempPath;
        while ((tempPath = paths.shift())) {
            res = res[tempPath];
        }
        return res;
    }
}
