    public getGameHttpServer(gameID: GameType): string[] {
        return Instance(CommonHostListObserver).getHttpServer(gameID);
    }

    public getGameWsServer(gameID: GameType): string[] {
        return Instance(CommonHostListObserver).getWsServer(gameID);
    }

    public switchHost(host: string): void {
        Instance(CommonHostListObserver).next(host);
    }

    public hostLenght() {
        return Instance(CommonHostListObserver).get().length;
    }

    public getRoomInfo(): ProtoObject<ApiProto.GameInfoResp.RoomInfo> {
        let self = this;
        let gameID = self.current.gameID;
        let room = self.current.room;
        let tableId = self.current.tableId;
        let curRoomList = self.getSeeRoomListByGameID(gameID);
        let foundRoom = {};
        if (Array.isArray(curRoomList)) {
            for (let i = 0; i < curRoomList.length; i++) {
                let curRoom = curRoomList[i];
                if (curRoom.id == room && (!tableId || curRoom.table_id == tableId)) {
                    foundRoom = curRoom;
                    break;
                }
            }
        }
        return foundRoom;
    }

    public updateRoomList(gameID: number, roomlist: ProtoObject<ApiProto.GameInfoResp.RoomInfo[]>) {
        let self = this;
        if (roomlist) {
            self.roomList[gameID] = roomlist;
        }
    }

