    public getRoomInfoByGameID(gameID: number, room: number): ApiProto.GameInfoResp.RoomInfo {
        let self = this;
        let curRoomList = self.getSeeRoomListByGameID(gameID);
        for (let i = 0; i < curRoomList.length; i++) {
            let curRoom = curRoomList[i];
            if (curRoom.id == room) {
                return curRoom;
            }
        }
    }

    public getSeeRoomListByGameID(gameID: number): ApiProto.GameInfoResp.RoomInfo[] {
        let self = this;
        let roomList = self.roomList;
        if (roomList == null) {
            return [];
        }
        let curRoomList = roomList[gameID];
        return curRoomList;
    }

