import { Injector } from "../../../../../../main/core/utils/decorators/Injector";
import Listener from "../../../../../../main/core/utils/Listener";
import { Tools } from "../../../../../../main/core/utils/Tools";
import { UniverseProto } from "../../universe/proto/UniverseProto";
import CommonBroadCastObserver from "../obs/CommonBroadCastObserver";
import { CommonBroadCastEvents } from "../define/CommonBroadCastEvents";
import { Instance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import CommonNetworkSwitchObserver from "../../obs/CommonNetworkSwitchObserver";

type TData = ProtoObject<UniverseProto.BroadCastNT>;

@Injector.Injectable()
export default class CommonBroadCastLogic {
    @Injector.Inject(CommonBroadCastObserver, {
        onCreate: () => {
            setTimeout(() => {
                let logic = Injector.get(CommonBroadCastLogic);
                logic.init();
            });
        },
        create: false,
    })
    private observer: CommonBroadCastObserver = null;

    private current: TData = null;

    public getCurrent() {
        return this.current;
    }

    public init() {
        this.observer.listenChange(this.onUpdateData, this);
    }

    public destroy() {
        this.observer?.unListenChange?.(this.onUpdateData, this);
    }

    private onUpdateData() {
        if (this.current) {
            return;
        }
        if (!Instance(CommonNetworkSwitchObserver).getSwitch("broadcast")) {
            return;
        }
        this.checkMessage();
    }

    private checkMessage() {
        //
        if (!this.observer) {
            return;
        }

        let message = this.observer.pop();
        if (!message) {
            this.current = null;
            return;
        }

        this.current = message;

        Listener.send(CommonBroadCastEvents.show, message);
    }

    public showFinish() {
        setTimeout(() => {
            this.checkMessage();
        }, (Math.random() * 3 + 1) * 1000);
    }
}
