import { Instance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { ApiProto } from "../../service/api/ApiProto";
import { CommonResultType } from "../define/CommonResultType";
import CommonUserLoginInfoObserver from "../obs/CommonUserLoginInfoObserver";
import { CommonAccountUtil } from "../utils/CommonAccountUtil";
import { CommonUserInfoUtil } from "../utils/CommonUserInfoUtil";
import CommonUserLoginBase from "./CommonUserLoginBase";

export default class CommonUserLoginAuto extends CommonUserLoginBase<CommonUser.IPasswordLoginOptions> {
    private userInfo: ProtoObject<ApiProto.UserInfo> = null;
    protected onLogin(): Promise<CommonUser.ILoginResult> {
        this.userInfo = null;
        return CommonUserInfoUtil.updateUserInfo(true).then((data) => {
            switch (data.result) {
                case CommonResultType.Success: {
                    this.userInfo = data.content;
                    Instance(CommonUserLoginInfoObserver).set({});
                    break;
                }
                case CommonResultType.Failed: {
                    break;
                }
            }

            return data;
        });
    }

    protected onLoginSuccess(data: ProtoObject<ApiProto.UserInfo>): Promise<void> {
        if (
            this.handlers
                .map((handler) => handler.onEnterLoginSuccess?.(data) ?? true)
                .includes(false)
        ) {
            return;
        }

        CommonAccountUtil.updateAccountToLast();

        this.handlers.forEach((handler) => handler.onLoginSuccess?.(data));
    }
}
