import CommonSkin from "../conf/skin/CommonSkin";

export class BundleCommonMgr {
    private static _instance: BundleCommonMgr = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new BundleCommonMgr();
        }

        return this._instance;
    }

    public getBundleNameDescForCommonGame(): IResDescribe {
        return CommonSkin.Priority.commonGame;
    }

    public getBundleNameDescForToastUI(): IResDescribe {
        return CommonSkin.Priority.ToastUI;
    }

    public getBundleNameDescForLoadingUI(): IResDescribe {
        return CommonSkin.Priority.LoadingUI;
    }

    public getBundleNameDescForAlert(): IResDescribe {
        return CommonSkin.Priority.Alert;
    }

    public getBundleNameDescForStartAndStopAnim(): IResDescribe {
        return CommonSkin.Priority.StartAndStopAnim;
    }

    public getBundleNameDescForQbCommonBtnBack(): IResDescribe {
        return CommonSkin.LoadOnDemand.backBtn;
    }

    public getBundleNameDescForCommonBtnPeople(): IResDescribe {
        return CommonSkin.LoadOnDemand.commonPeopleBtn;
    }

    public getBundleNameDescForCommonGameBtnRechange(): IResDescribe {
        return CommonSkin.LoadOnDemand.RechargeBtn;
    }

    public getBundleNameDescForBetChip(): IResDescribe {
        return CommonSkin.UnPriority.BetChip;
    }

    public getBundleNameDescForGameRuleContent(): IResDescribe {
        return CommonSkin.UnPriority.gameRuleContent;
    }

    public getBundleNameDescForLoadingWithFont(): IResDescribe {
        return CommonSkin.LoadOnDemand.LoadingWithFont;
    }
}
