import BaseServiceErrorHandler from "./base/BaseServiceErrorHandler";
import UIMgr from "../../../Component/base/UIMgr";
import { ServiceCatchResult } from "../define/ServiceCatchResult";

export class ServiceErrorSelectAlert extends BaseServiceErrorHandler {
    protected onCatchError(data: ServiceError.IError): ServiceError.TErrorCatch {
        return new Promise((resolve, reject) => {
            UIMgr.showAlert({
                contentText: data.message || this.getDefaultShowNotice(),
                contextAlign: cc.macro.TextAlignment.CENTER,
                style: 3, // UIMgr.ALERT_STYLE.NEGATIVE_POSITIVE,
                negativeBtnText: this.cancelText(),
                positiveBtnText: this.comfirmeText(),
            })
            // .onClose((result) => {
            //     if (result) {
            //         this.onConfirmBtnClick();
            //         reject({
            //             ...data,
            //             result: ServiceCatchResult.Confirm,
            //         });
            //     } else {
            //         this.onCancelBtnClick();
            //         reject({
            //             ...data,
            //             result: ServiceCatchResult.Cancel,
            //         });
            //     }
            // });
        });
    }

    protected comfirmeText(): string {
        return;
    }

    protected cancelText(): string {
        return;
    }

    protected getDefaultShowNotice() {
        return "";
    }

    protected onConfirmBtnClick(): void { }

    protected onCancelBtnClick(): void { }
}
