import { AccountType } from "../../../../../../main/core/conf/GameEnum";

import { ApiProto } from "../../service/api/ApiProto";
import ApiMgr from "../../service/api/ApiMgr";
import { CommonResultType } from "../define/CommonResultType";
import { CommonAccountUtil } from "../utils/CommonAccountUtil";
import CommonUserLoginBase, { ISuccessOptions } from "./CommonUserLoginBase";
import CommonUserLoginInfoObserver from "../obs/CommonUserLoginInfoObserver";
import { Instance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { CommonUserInfoUtil } from "../utils/CommonUserInfoUtil";
import { ServiceErrorUtils } from "../../service/service_error/ServiceErrorUtils";

export default class CommonUserLoginGuest extends CommonUserLoginBase<CommonUser.ILoginOptions> {
    protected onLogin(): Promise<CommonUser.ILoginResult> {
        return new Promise<CommonUser.ILoginResult>((resolve, reject) => {
            let httpCallBack: IHttpCallback = {
                success: async (resp: ApiProto.LoginInfo) => {
                    let data = resp.toObject();

                    resolve({
                        result: CommonResultType.Success,
                        content: {
                            ...data,
                        },
                    });
                    Instance(CommonUserLoginInfoObserver).set(data);
                    //
                    CommonAccountUtil.addAccount(
                        data.user_id,
                        data.token,
                        null,
                        null,
                        null,
                        AccountType.TYPE_GUEST
                    );
                },
                error: (respHeader: RespHeader) => {
                    resolve({
                        result: CommonResultType.Failed,
                        content: respHeader,
                    });
                },
            };
            let aid = CommonDepend.AppParamManager.getDeviceID();
            ApiMgr.getInstance()
                .guestLogin(aid, httpCallBack)
                .catch(() => {
                    if (CC_DEBUG) {
                        CC_DEBUG && cc.warn("guest login error");
                    }
                });
        });
    }
    protected onLoginSuccess(data: ISuccessOptions): Promise<void> {
        return super.onLoginSuccess(data);
    }
}
