import CacheObserver from "../../../../../main/core/utils/class/CacheObserver";
import { setInstance } from "../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { lodash } from "../../../../../main/core/utils/NpmExport";
import { UniverseProto } from "../universe/proto/UniverseProto";
import { RedPointType } from "./RedPointCacheKey";

interface IRedPointData {
    count: number;
}

interface IItem {
    [index: string | number]: IRedPointData;
}

type IData = {
    points: {
        [key in RedPointType]?: IItem;
    };
};

@setInstance
export default class RedPointObserver extends CacheObserver<IData> {
    protected getCacheKey(): string {
        return "_red_points";
    }

    private toIndex(index: number | string): string {
        return `_${index}`;
    }

    public getPoint(key: RedPointType, index: number | string = 0) {
        let data = this.get("points", {});
        let value = lodash.get(data, `${key}.${this.toIndex(index)}.count`, 0);
        return value;
    }

    public hasPoint(key: RedPointType, index: number | string = 0) {
        let data = this.get("points", {});
        return lodash.get(data, `${key}.${this.toIndex(index)}.count`, -1) != -1;
    }

    public resetPoint(key: RedPointType, index: number | string, value: number) {
        delete this.get("points")?.[key];
        this.setPoint(key, index, value);
    }

    public setPoint(
        key: RedPointType,
        index: number | string,
        value: number,
        once: boolean = false
    ) {
        let data = this.get("points", {});

        if (once && this.hasPoint(key, index)) {
            return;
        }

        lodash.set(data, `${key}.${this.toIndex(index)}.count`, value);
        this.modify({
            points: data,
        });
    }

    public removePoint(key: RedPointType, index: number | string = 0) {
        if (!this.hasPoint(key, index)) {
            return;
        }
        this.setPoint(key, index, 0);
    }

    public initGameGuide(gameTag: ProtoObject<UniverseProto.FeatureConfResp.GameTag>[]) {
        gameTag
            .find((v) => {
                return v.type == UniverseProto.GameTagType.TAG_TYPE_NEWBIE_GUIDE;
            })
            ?.game_ids.forEach((item) => {
                this.setPoint(RedPointType.NewbieGuide, item, 1, true);
            });
    }

    initBar(bar: UniverseProto.BarItem[]) {
        delete this.get("points")?.[RedPointType.HallBar];
        bar.forEach((item) => {
            this.setPoint(RedPointType.HallBar, item, 1, false);
        });
    }
}
