import { MoneyUtil } from "../../../../../../main/core/utils/MoneyUtil";
import { numeral } from "../../../../../../main/core/utils/NpmExport";

/**
 * 
 */
export namespace CurrencyBRL {
    /**
     * 999999M
     * 32
     * @param money
     */
    export function formatMoney(money: number, digit?: number): string {
        if (money >= 1000000) {
            money = money / 1000000;
            if (digit == 3) {
                return Math.floor(money * 1000) / 1000 + "M";
            } else {
                return MoneyUtil.getInstance().keepDigitPrecision(money) + "M";
            }
        } else {
            if (digit == 3) {
                return MoneyUtil.getInstance().keepDigitPrecision(money, 3);
            } else {
                return MoneyUtil.getInstance().keepDigitPrecision(money);
            }
        }
    }
}
