import { BundleMgr } from "../../../../main/core/bd/BundleMgr";
import CommonSkin from "./conf/skin/CommonSkin";

export default class PropConfMgr {
    private static _instance: PropConfMgr = null;
    private _PropConf: Map<number, IPropsConf> = new Map();

    public static getInstance() {
        if (!this._instance) {
            this._instance = new PropConfMgr();
        }

        return this._instance;
    }

    private constructor() {}

    /**
     * ID
     * @param id ID
     * @returns icon
     */
    public getPropSpriteFrameById(id: number): cc.SpriteFrame {
        let propConf: IPropsConf = this._PropConf.get(id);
        let resDesc: IResDescribe = null;
        if (propConf) {
            resDesc = CommonSkin.Priority.prop[propConf.icon];
        }
        if (!resDesc) {
            resDesc = CommonSkin.Priority.prop.default;
        }

        return BundleMgr.getInstance().getResFromBundle(resDesc);
    }

    /**
     * icon
     * @param icon icon
     * @returns icon
     */
    public getPropSpriteFrameByIcon(icon: number): cc.SpriteFrame {
        let resDesc: IResDescribe = CommonSkin.Priority.prop[icon];
        if (!resDesc) {
            resDesc = CommonSkin.Priority.prop.default;
        }

        return BundleMgr.getInstance().getResFromBundle(resDesc);
    }

    /** id */
    public getPropDescById(id: number): string {
        let desc: string = "";
        let propConf: IPropsConf = this._PropConf.get(id);
        if (propConf) {
            desc = propConf.desc;
        }
        return desc;
    }

    /** id */
    public getPropNameById(id: number): string {
        let name: string = "";
        let propConf: IPropsConf = this._PropConf.get(id);
        if (propConf) {
            name = propConf.name;
        }
        return name;
    }

    /** id */
    public getPropConfById(id: number): IPropsConf {
        return this._PropConf.get(id);
    }

    public setPropConf(propConf: Map<number, IPropsConf>) {
        this._PropConf = propConf;
    }

    public getPropConf(): Map<number, IPropsConf> {
        return this._PropConf;
    }
}
