import { lodash } from "../../../../../../../main/core/utils/NpmExport";
import { CommonProto } from "../proto/CommonProto";
import { SocketInternalEvent } from "../SocketInternalEvent";
export default class BaseSocketAttachment implements Socket.ISocketAttachment {
    public tag: string = "Unknown";

    protected config: Socket.ISocketConfig = {};

    constructor(protected options: Socket.ISocketAttachmentConstructorParameters) {}
    public applyConfig(config: Socket.ISocketConfig) {
        this.config = config;
    }

    public send(msgId: number, msgData: Socket.IMessageClass) {
        this.options.listener.emit(SocketInternalEvent.SEND_MESSAGE, msgId, msgData);
    }

    public close() {
        this.options.listener.emit(SocketInternalEvent.CLOSE);
    }
    public destroy() {
        this.options.listener.emit(SocketInternalEvent.DESTROY);
    }

    public reconnect(options: Socket.SocketInitOptions<number, any> = {}) {
        this.options.listener.emit(SocketInternalEvent.RECONNECT, options);
    }

    public error(data: ProtoObject<CommonProto.ErrorST>) {
        this.options.listener.emit(SocketInternalEvent.ERROR, data);
    }

    public onInit(option: Socket.SocketOption): void {}

    public onOpen(): void {}

    public onClose(): void {}

    public onBeforeUserClose(): void {}
    public onSendMessage(msgType: number, data: Socket.IMessageClass) {}
    public getMessageCtor(msgType: number) {
        return null;
    }
    public onMessage(data: Socket.TMessageObject<number, any>): void {
        return;
    }
    public onMessageHandle(data: Socket.TMessageObject<number, any>): void {
        if (CC_DEBUG) {
            if ((window?.["socket_attachment_filter"] ?? true) == false) {
                this.onMessage(data);
                return;
            }
        }

        if (lodash.get(this.config, "blacklist", []).includes(data.msgId)) {
            return;
        }
        this.onMessage(data);
        return;
    }
}
