import UserAvatarMgr from "./UserAvatarMgr";
import SkeletonHelper from "../../../../../../main/core/utils/class/SkeletonHelper";
import { Tools } from "../../../../../../main/core/utils/Tools";
import UserAvatarModel from "./UserAvatarModel";

const { ccclass, property } = cc._decorator;

@ccclass
export default class UserAvatar extends cc.Component {
    @property(cc.Node)
    avatar: cc.Node = null;

    @property(cc.SpriteFrame)
    avatarList =[];

    avatarInfo: any;
    currentAvatar: any;

    private skeletonHelper: SkeletonHelper = null;

    private curAnim: Promise<any> = Promise.resolve();

    public init(data: number, gender: UserAvatarModel.GENDER): void {
        this.unscheduleAllCallbacks();
        this.skeletonHelper = null;
        this.curAnim = Promise.resolve();
        this.avatar.scale = 1;
        if (data) {
            this.currentAvatar = data;
            this.avatarInfo = this.avatarList[ data%4 ]   // UserAvatarMgr.getAvatar(data, gender);

            UIDepend.SpriteManager.setSpriteFrame(this.avatar, this.avatarInfo);
            this.avatar.getComponent(cc.Sprite).enabled = true;
            this.avatar.width =  this.avatarInfo.getRect().width;
            this.avatar.height =  this.avatarInfo.getRect().height;

            // if (this.avatarInfo instanceof cc.SpriteFrame) {
            //     const spf = this.avatarInfo as cc.SpriteFrame;
            //     this.avatar.getComponent(sp.Skeleton)?.destroy();
            //     this.scheduleOnce(() => {
            //         UIDepend.SpriteManager.setSpriteFrame(this.avatar, this.avatarInfo);
            //         this.avatar.getComponent(cc.Sprite).enabled = true;
            //         this.avatar.width = spf.getRect().width;
            //         this.avatar.height = spf.getRect().height;
            //     });
            // }
            // if (this.avatarInfo instanceof sp.SkeletonData) {
            //     this.avatar.scale = 1;
            //     this.avatar.removeComponent(cc.SpriteFrame);
            //     let skeleton =
            //         this.avatar.getComponent(sp.Skeleton) || this.avatar.addComponent(sp.Skeleton);
            //     skeleton.skeletonData = this.avatarInfo;
            //     this.skeletonHelper = new SkeletonHelper(skeleton);
            //     if (!this.node.activeInHierarchy) {
            //         this.curAnim = null;
            //     }
            //     this.playWaitAnim();
            // }
        } else {
            let value = UserAvatarMgr.getAvatar(data, gender);
            UIDepend.SpriteManager.setSpriteFrame(this.avatar, this.avatarList[0]);
        }
    }

    protected playWaitAnim() {
        this.curAnim =
            this.curAnim?.then?.(() => {
                return this.skeletonHelper?.play("idle").then(() => {
                    Tools.delayTime(0).then(() => {
                        this.playWaitAnim();
                    });
                });
            }) ??
            Promise.resolve().then(() => {
                Tools.delayTime(3).then(() => {
                    this.playWaitAnim();
                });
            });
    }
    /**  */
    public playWinAnimation() {
        let onStart: () => void;
        let onEnd: () => void;

        if (this.skeletonHelper) {
            let helper = SkeletonHelper.createInParent(this.node.parent, {
                position: this.node.position,
            });
            helper.setData(this.avatarInfo);
            helper.tryPlay("gold").then(() => {
                helper.destroy();
            });
        }
        onStart?.();
        this.curAnim = (
            this.skeletonHelper
                ?.play("smile")
                .then(() => this.skeletonHelper?.play("smile"))
                .then(() => {
                    onEnd?.();
                }) ??
            Promise.resolve().then(() => {
                onEnd?.();
            })
        ).then(() => this.playWaitAnim());

        let result = {
            onStart: (callfunc: () => void) => {
                onStart = callfunc;
                return result;
            },
            onEnd: (callfunc: () => void) => {
                onEnd = callfunc;
                return result;
            },
        };
        return result;
    }

    /**  */
    public playLoseAnimation() {
        let onStart: () => void;
        let onEnd: () => void;

        onStart?.();
        this.curAnim = (
            this.skeletonHelper
                ?.play("sad")
                .then(() => this.skeletonHelper?.play("sad"))
                .then(() => {
                    onEnd?.();
                }) ??
            Promise.resolve().then(() => {
                onEnd?.();
            })
        ).then(() => this.playWaitAnim());

        let result = {
            onStart: (callfunc: () => void) => {
                onStart = callfunc;
                return result;
            },
            onEnd: (callfunc: () => void) => {
                onEnd = callfunc;
                return result;
            },
        };

        return result;
    }
}
