import { PoolMgr } from "../../../../../../main/core/objectPool/PoolMgr";
import Listener from "../../../../../../main/core/utils/Listener";
import CommonEventName from "../../CommonEventName";
import PageItem from "./PageItem";

const { ccclass, property } = cc._decorator;

enum CREATE_ITEM_DIR {
    LEFT,
    RIGHT,
}

/** item */
const ENLARGE_RATIO: number = 1.15;

@ccclass
export default class SelectPageCom extends cc.Component {
    @property({ type: cc.Label, tooltip: "lab" })
    lab_totalPage: cc.Label = null;

    @property({ type: cc.Node, tooltip: "" })
    btn_left: cc.Node = null;

    @property({ type: cc.Node, tooltip: "" })
    btn_right: cc.Node = null;

    @property({ type: cc.EditBox, tooltip: "" })
    pageEditBox: cc.EditBox = null;

    @property({ type: cc.Node, tooltip: "item" })
    pageItemRoot: cc.Node = null;

    @property({ type: cc.Prefab, tooltip: "" })
    pageItem: cc.Prefab = null;

    @property({ type: cc.Integer, tooltip: "," })
    maxPageItemNum: number = 0;

    @property({ type: cc.Integer, tooltip: "" })
    space: number = 10;

    /** item */
    private curSelectItemNode: cc.Node = null;
    private totalPage: number = 0;

    private itemNodeList: cc.Node[] = [];
    private itemNodePosList: cc.Vec3[] = [];
    private isMoveing: boolean = false;
    private itemCenterIndex: number = 0;
    private minSelectThreshold: number = 0;
    private maxSelectThreshold: number = 0;
    private selectCallFunc: (pageNum: number) => void = null;

    onLoad() {
        this.btn_left.on(cc.Node.EventType.TOUCH_END, this.onLeft, this);
        this.btn_right.on(cc.Node.EventType.TOUCH_END, this.onRight, this);

        this.pageEditBox.node.on("editing-did-ended", this.onPageEditBoxEnded, this);
    }

    start() {}

    protected onEnable(): void {
        Listener.on(CommonEventName.CLICK_PAGE_ITEM, this.onCilckPageItem, this);
    }

    protected onDisable(): void {
        Listener.targetOff(this);
    }

    public init(totalPage: number, selectCallFunc: (pageNum: number) => void) {
        this.selectCallFunc = selectCallFunc;
        this.isMoveing = false;
        this.totalPage = totalPage;
        this.lab_totalPage.string = String(this.totalPage);
        this.pageEditBox.string = "";
        this.itemNodeList = [];
        this.itemNodePosList = [];

        this.initItems();
        this.initLeftAndRightBtnActive(1);
        this.initLeftAndRightBtnPos();
    }

    private initLeftAndRightBtnPos() {
        let width = this.pageItemRoot.width / 2;
        this.btn_left.x = -(width + this.btn_left.width / 2 + 25);
        this.btn_right.x = width + this.btn_right.width / 2 + 25;
    }

    private initLeftAndRightBtnActive(curSelectPageNum: number) {
        if (this.totalPage <= 1) {
            this.btn_left.active = false;
            this.btn_right.active = false;
        } else if (curSelectPageNum == 1) {
            this.btn_left.active = false;
            this.btn_right.active = true;
        } else if (curSelectPageNum == this.totalPage) {
            this.btn_left.active = true;
            this.btn_right.active = false;
        } else {
            this.btn_left.active = true;
            this.btn_right.active = true;
        }
    }

    private initItemNodePosList() {
        let indexs: number[] = [];

        if (this.totalPage <= this.maxPageItemNum) {
            for (let i = 0; i < this.totalPage; i++) {
                indexs.push(i);
            }
        } else {
            for (let i = 0, len = this.maxPageItemNum * 3; i < len; i++) {
                indexs.push(-this.maxPageItemNum + i);
            }
        }

        let itemWidth = this.itemNodeList[0].width;
        let totalWidth = this.calcWidthWithItenNum(itemWidth, indexs.length);
        for (let i = 0, len = indexs.length; i < len; i++) {
            let index = indexs[i];
            this.itemNodePosList[index] = this.calcItemPos(totalWidth, i, itemWidth);
        }
    }

    private initItems() {
        this.createItems();
        this.initItemNodePosList();
        this.initItemsPos();
        this.initPageItemRootNodeSize();
        this.curSelectItemNode = this.itemNodeList[0];
        this.curSelectItemNode.setScale(ENLARGE_RATIO);
    }

    private initPageItemRootNodeSize() {
        let totalWidth = this.calcWidthWithItenNum(
            this.itemNodeList[0].width,
            this.itemNodeList.length
        );

        let itemWidth = this.itemNodeList[0].width;
        let itemHeight = this.itemNodeList[0].height;

        let increaseWith = itemWidth * ENLARGE_RATIO - itemWidth + 2;
        let increaseHeight = itemHeight * ENLARGE_RATIO - itemHeight + 2;

        this.pageItemRoot.width = totalWidth + increaseWith;
        this.pageItemRoot.height = itemHeight + increaseHeight;
    }

    private createItems() {
        this.itemCenterIndex = Math.floor(this.maxPageItemNum / 2);
        this.minSelectThreshold = Math.ceil(this.maxPageItemNum / 2);
        this.maxSelectThreshold = this.totalPage - this.minSelectThreshold + 1;

        //   
        this.pageItemRoot.destroyAllChildren();
        let createNum =
            this.totalPage <= this.maxPageItemNum ? this.totalPage : this.maxPageItemNum;
        for (let i = 0; i < createNum; i++) {
            let item: cc.Node = this.createPageItemNode(i + 1, i);
            this.itemNodeList.push(item);
        }
    }

    private createPageItemNode(pageNun: number, posIndex: number): cc.Node {
        let item = PoolMgr.getInstance().getNode(this.pageItem);
        this.pageItemRoot.addChild(item);
        item.getComponent(PageItem).init(pageNun, posIndex);

        return item;
    }

    private initItemsPos() {
        for (let i = 0, len = this.itemNodeList.length; i < len; i++) {
            this.itemNodeList[i].setPosition(this.itemNodePosList[i]);
        }
    }

    private calcWidthWithItenNum(itemWidth: number, itemNum: number): number {
        return itemNum * itemWidth + (itemNum - 1) * this.space;
    }

    private calcItemPos(totalWidth: number, index: number, itemWidth: number): cc.Vec3 {
        let startPosX = -totalWidth / 2;
        return cc.v3(startPosX + index * itemWidth + itemWidth / 2 + index * this.space, 0, 0);
    }

    private onLeft() {
        if (this.isMoveing) {
            return;
        }

        let preSelectPageNum = this.curSelectItemNode.getComponent(PageItem).getPageNum();
        let curSelectPageNum = preSelectPageNum - 1;
        let moveStep = this.calcMoveStep(curSelectPageNum, preSelectPageNum);
        this.changePage(CREATE_ITEM_DIR.LEFT, moveStep, curSelectPageNum);
    }

    private onRight() {
        if (this.isMoveing) {
            return;
        }

        let preSelectPageNum = this.curSelectItemNode.getComponent(PageItem).getPageNum();
        let curSelectPageNum = preSelectPageNum + 1;
        let moveStep = this.calcMoveStep(curSelectPageNum, preSelectPageNum);
        this.changePage(CREATE_ITEM_DIR.RIGHT, moveStep, curSelectPageNum);
    }

    private changePage(
        dir: CREATE_ITEM_DIR,
        moveStep: number,
        curSelectPageNum: number,
        startPageNum?: number
    ) {
        let selectNode: cc.Node = null;

        for (let i = 0; i < moveStep; i++) {
            if (dir == CREATE_ITEM_DIR.RIGHT) {
                let pageNum = 0;
                if (startPageNum) {
                    pageNum = startPageNum + i;
                } else {
                    pageNum =
                        this.itemNodeList[this.itemNodeList.length - 1]
                            .getComponent(PageItem)
                            .getPageNum() + 1;
                }
                let posIndex = this.maxPageItemNum + i;
                let item = this.createPageItemNode(pageNum, posIndex);
                item.setPosition(this.itemNodePosList[posIndex]);

                this.itemNodeList.push(item);

                if (pageNum == curSelectPageNum) {
                    selectNode = item;
                }
            } else {
                let pageNum = 0;
                if (startPageNum) {
                    pageNum = startPageNum + moveStep - i - 1;
                } else {
                    pageNum = this.itemNodeList[0].getComponent(PageItem).getPageNum() - 1;
                }
                let posIndex = 0 - i - 1;
                let item = this.createPageItemNode(pageNum, posIndex);
                item.setPosition(this.itemNodePosList[posIndex]);

                this.itemNodeList.unshift(item);

                if (pageNum == curSelectPageNum) {
                    selectNode = item;
                }
            }
        }

        if (selectNode == null) {
            for (let i = 0; i < this.itemNodeList.length; i++) {
                let pageNum = this.itemNodeList[i].getComponent(PageItem).getPageNum();
                if (pageNum == curSelectPageNum) {
                    selectNode = this.itemNodeList[i];
                    break;
                }
            }
        }
        this.curSelectItemNode.setScale(1);
        this.curSelectItemNode = selectNode;
        this.curSelectItemNode.setScale(ENLARGE_RATIO);

        this.initLeftAndRightBtnActive(curSelectPageNum);

        if (moveStep > 0) {
            this.moveItem(dir, moveStep);
        }

        this.selectCallFunc && this.selectCallFunc(curSelectPageNum);
    }

    private getItemMoveToPos(index: number, dir: CREATE_ITEM_DIR, moveStep: number): cc.Vec3 {
        let pageIndex = this.itemNodeList[index].getComponent(PageItem).getPosIndex();
        let toPos: cc.Vec3;
        if (dir == CREATE_ITEM_DIR.RIGHT) {
            toPos = this.itemNodePosList[pageIndex - moveStep];
        } else {
            toPos = this.itemNodePosList[pageIndex + moveStep];
        }

        return toPos;
    }

    private updateItemPosIndex(itemNode: cc.Node, dir: CREATE_ITEM_DIR, moveStep: number) {
        let pageIndex = itemNode.getComponent(PageItem).getPosIndex();
        if (dir == CREATE_ITEM_DIR.RIGHT) {
            itemNode.getComponent(PageItem).setPosIndex(pageIndex - moveStep);
        } else {
            itemNode.getComponent(PageItem).setPosIndex(pageIndex + moveStep);
        }
    }

    private moveItem(dir: CREATE_ITEM_DIR, moveStep: number) {
        this.isMoveing = true;

        let animList = [];

        for (let i = 0; i < this.itemNodeList.length; i++) {
            let toPos = this.getItemMoveToPos(i, dir, moveStep);
            animList.push(
                new Promise<void>((resolve) => {
                    cc.tween(this.itemNodeList[i])
                        .to(0.1, { position: toPos })
                        .call(() => {
                            this.updateItemPosIndex(this.itemNodeList[i], dir, moveStep);
                            resolve();
                        })
                        .start();
                })
            );
        }

        Promise.all(animList).then(() => {
            for (let i = 0; i < moveStep; i++) {
                let node: cc.Node;
                if (dir == CREATE_ITEM_DIR.RIGHT) {
                    node = this.itemNodeList.shift();
                } else {
                    node = this.itemNodeList.pop();
                }
                PoolMgr.getInstance().putNode(node);
            }

            this.isMoveing = false;
        });
    }

    private calcMoveStep(curSelectPageNum: number, preSelectPageNum: number): number {
        let moveStep: number = 0;
        let difference = curSelectPageNum - preSelectPageNum;
        if (difference == 0) {
            moveStep = 0;
        } else if (difference > 0) {
            if (difference >= this.maxPageItemNum) {
                moveStep = this.maxPageItemNum;
            } else if (curSelectPageNum <= this.minSelectThreshold) {
                moveStep = 0;
            } else {
                let pageNum = this.itemNodeList[this.itemNodeList.length - 1]
                    .getComponent(PageItem)
                    .getPageNum();
                let critical = this.totalPage - pageNum;
                if (critical >= difference) {
                    //  3 4 5    1 2 3    7  8  9  5  6  7
                    moveStep =
                        curSelectPageNum -
                        this.itemNodeList[this.itemCenterIndex].getComponent(PageItem).getPageNum();
                } else {
                    moveStep = critical;
                }
            }
        } else {
            difference = Math.abs(difference);
            if (difference >= this.maxPageItemNum) {
                moveStep = this.maxPageItemNum;
            } else if (curSelectPageNum >= this.maxSelectThreshold) {
                moveStep = 0;
            } else {
                let pageNum = this.itemNodeList[0].getComponent(PageItem).getPageNum();
                let critical = pageNum - difference;
                if (critical > 0) {
                    moveStep =
                        this.itemNodeList[this.itemCenterIndex]
                            .getComponent(PageItem)
                            .getPageNum() - curSelectPageNum;
                } else {
                    moveStep = pageNum - 1;
                }
            }
        }

        return moveStep;
    }

    private onCilckPageItem(posIndex: number) {
        if (this.isMoveing) {
            return;
        }

        let curSelectPageNum = this.itemNodeList[posIndex].getComponent(PageItem).getPageNum();
        let preSelectPageNum = this.curSelectItemNode.getComponent(PageItem).getPageNum();
        let moveStep = this.calcMoveStep(curSelectPageNum, preSelectPageNum);

        if (curSelectPageNum - preSelectPageNum > 0) {
            this.changePage(CREATE_ITEM_DIR.RIGHT, moveStep, curSelectPageNum);
        } else {
            this.changePage(CREATE_ITEM_DIR.LEFT, moveStep, curSelectPageNum);
        }
    }

    private onPageEditBoxEnded() {
        if (this.isMoveing) {
            return;
        }

        let str = this.pageEditBox.string.trim();
        if (str == "") {
            return;
        }

        let inputPageNum = Number(str);
        let curSelectPageNum = inputPageNum;
        if (inputPageNum < 1) {
            curSelectPageNum = 1;
            this.pageEditBox.string = String(curSelectPageNum);
        } else if (inputPageNum > this.totalPage) {
            curSelectPageNum = this.totalPage;
            this.pageEditBox.string = String(curSelectPageNum);
        }

        let preSelectPageNum = this.curSelectItemNode.getComponent(PageItem).getPageNum();
        let moveStep = this.calcMoveStep(curSelectPageNum, preSelectPageNum);
        let difference = curSelectPageNum - preSelectPageNum;
        if (moveStep <= this.itemCenterIndex) {
            if (difference > 0) {
                this.changePage(CREATE_ITEM_DIR.RIGHT, moveStep, curSelectPageNum);
            } else {
                this.changePage(CREATE_ITEM_DIR.LEFT, moveStep, curSelectPageNum);
            }
        } else {
            if (difference > 0) {
                let startPageNum = this.calcStartPageNum(
                    CREATE_ITEM_DIR.RIGHT,
                    curSelectPageNum,
                    moveStep
                );
                this.changePage(CREATE_ITEM_DIR.RIGHT, moveStep, curSelectPageNum, startPageNum);
            } else {
                let startPageNum = this.calcStartPageNum(
                    CREATE_ITEM_DIR.LEFT,
                    curSelectPageNum,
                    moveStep
                );
                this.changePage(CREATE_ITEM_DIR.LEFT, moveStep, curSelectPageNum, startPageNum);
            }
        }
    }

    private calcStartPageNum(
        dir: CREATE_ITEM_DIR,
        curSelectPageNum: number,
        moveStep: number
    ): number {
        let startPageNum: number = 0;

        if (moveStep < this.maxPageItemNum) {
            if (dir == CREATE_ITEM_DIR.RIGHT) {
                let pageNum = this.itemNodeList[this.itemNodeList.length - 1]
                    .getComponent(PageItem)
                    .getPageNum();
                startPageNum = pageNum + 1;
            } else {
                let pageNum = this.itemNodeList[0].getComponent(PageItem).getPageNum();
                startPageNum = pageNum - moveStep;
            }
        } else {
            let rightPageNum = this.totalPage - curSelectPageNum;
            let leftPageNum = curSelectPageNum - 1;
            if (rightPageNum >= this.itemCenterIndex) {
                if (leftPageNum >= this.itemCenterIndex) {
                    startPageNum = curSelectPageNum - this.itemCenterIndex;
                } else {
                    startPageNum = 1;
                }
            } else {
                startPageNum = this.totalPage - this.itemCenterIndex * 2;
            }
        }

        return startPageNum;
    }

    protected onDestroy(): void {
        PoolMgr.getInstance().clearPoolWithPrefab(this.pageItem);
    }
}
