import { CalendarDayType } from "./CalendarEnum";
import { ICalendarItemInfo } from "./CalendarInterface";
import CalendarItem from "./CalendarItem";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Calendar extends cc.Component {
    @property({ type: cc.Label, tooltip: "lab" })
    lab_yearMonth: cc.Label = null;

    @property({ type: cc.Node, tooltip: "item" })
    daysRoot: cc.Node = null;

    @property({ type: cc.Node, tooltip: "item" })
    calendarItemNode: cc.Node = null;

    @property({ type: cc.Button, tooltip: "" })
    btn_right: cc.Button = null;

    @property({ type: cc.Button, tooltip: "" })
    btn_bigRight: cc.Button = null;

    @property({ type: cc.Button, tooltip: "" })
    btn_left: cc.Button = null;

    @property({ type: cc.Button, tooltip: "" })
    btn_bigLeft: cc.Button = null;

    /**  */
    private _curSelectYear: number = null;
    private _curSelectMonth: number = null;
    private _curSelectDay: number = null;

    /**  */
    private _curYear: number = null;
    private _curMonth: number = null;
    private _currDay: number = null;

    /**  */
    private selectDateTimestamp: number = 0;

    /**  */
    private minSelectYearLimit: number = 0;
    /**  */
    private minSelectMonthLimit: number = 0;
    /**  */
    private minSelectDayLimit: number = 0;

    private _dayItemList: cc.Node[] = [];
    private _daysItemInfo: ICalendarItemInfo[] = [];

    private selectCallFunc: (_dayItemInfo: ICalendarItemInfo) => void = null;

    /**
     *
     * @param func 
     * @param selectDateLimit   
     */
    public init(func: (_dayItemInfo: ICalendarItemInfo) => void, selectDateLimit: number) {
        this.selectCallFunc = func;

        let selectMinTimestampLimit =
            CommonDepend.DateUtils.getPreviousStartTimestamp(selectDateLimit);
        let date = new Date(selectMinTimestampLimit);
        this.minSelectYearLimit = date.getFullYear();
        this.minSelectMonthLimit = date.getMonth(); // js0
        this.minSelectDayLimit = date.getDate();

        this.initDayItem(selectMinTimestampLimit);
    }

    public initView(timestamp: number) {
        this.initDate(timestamp);
        this.updateDate();
        this.initBtnStatus();
    }

    private initDate(timestamp: number) {
        let date = new Date(timestamp);
        this._curSelectYear = date.getFullYear();
        this._curSelectMonth = date.getMonth(); // js0
        this._curSelectDay = date.getDate();

        date = new Date();
        this._curYear = date.getFullYear();
        this._curMonth = date.getMonth(); // js0
        this._currDay = date.getDate();

        //  000
        this.selectDateTimestamp = new Date(
            this._curSelectYear,
            this._curSelectMonth,
            this._curSelectDay
        ).getTime();
    }

    private initDayItem(selectMinTimestampLimit: number) {
        const itemNum = 42;
        for (let i = 0; i < itemNum; ++i) {
            let node = cc.instantiate(this.calendarItemNode);
            this.daysRoot.addChild(node, itemNum - i);
            node.getComponent(CalendarItem).init(
                this.onSelectDate.bind(this),
                selectMinTimestampLimit
            );
            this._dayItemList.push(node);
        }

        this.calendarItemNode.destroy();
    }

    private getWeekInMonth(year: number, month: number) {
        return new Date(year + "/" + month + "/" + "01").getDay();
    }

    private calcDaysData() {
        // 1 getWeekInMonth0 16
        let totalDays = this.getDaysWithYeayAndNibth(this._curSelectYear, this._curSelectMonth + 1); // 
        let fromWeek = this.getWeekInMonth(this._curSelectYear, this._curSelectMonth + 1); // 1

        // 
        let preMonth = this._curSelectMonth - 1; // 
        let preMonthInYear = this._curSelectYear; // 
        if (this._curSelectMonth == 0) {
            preMonth = 11;
            preMonthInYear -= 1;
        }
        let preTotalDays = this.getDaysWithYeayAndNibth(preMonthInYear, preMonth + 1); // 

        // 
        let nextMonth = this._curSelectMonth + 1; // 
        let nextMonthInYear = this._curSelectYear; // 
        if (this._curSelectMonth == 11) {
            nextMonth = 0;
            nextMonthInYear += 1;
        }

        let curMonthStartDay = 1;
        let nexMonthStartDay = 1;

        for (let i = 0; i < this._dayItemList.length; ++i) {
            if (fromWeek > 0) {
                // 
                this._daysItemInfo[i] = <ICalendarItemInfo>{
                    day: preTotalDays - fromWeek + 1,
                    inMonth: preMonth,
                    inYear: preMonthInYear,
                    belongTo: CalendarDayType.IN_PRE_MONTH,
                    curDay: this._currDay,
                    curYear: this._curYear,
                    curMonth: this._curMonth,
                    isSelect: false,
                };
                fromWeek--;
            } else {
                if (curMonthStartDay <= totalDays) {
                    let timestamp = new Date(
                        this._curSelectYear,
                        this._curSelectMonth,
                        curMonthStartDay
                    ).getTime();
                    // 
                    this._daysItemInfo[i] = <ICalendarItemInfo>{
                        day: curMonthStartDay,
                        inMonth: this._curSelectMonth,
                        inYear: this._curSelectYear,
                        belongTo: CalendarDayType.IN_CUR_MONTH,
                        curDay: this._currDay,
                        curYear: this._curYear,
                        curMonth: this._curMonth,
                        isSelect: timestamp == this.selectDateTimestamp,
                    };
                    curMonthStartDay++;
                } else {
                    // 
                    this._daysItemInfo[i] = <ICalendarItemInfo>{
                        day: nexMonthStartDay,
                        inMonth: nextMonth,
                        inYear: nextMonthInYear,
                        belongTo: CalendarDayType.IN_NEXT_MONTH,
                        curDay: this._currDay,
                        curYear: this._curYear,
                        curMonth: this._curMonth,
                        isSelect: false,
                    };
                    nexMonthStartDay++;
                }
            }
        }
    }

    private getDaysWithYeayAndNibth(year: number, month: number): number {
        var d = new Date(year, month, 0);
        return d.getDate();
    }

    private updateDate() {
        let timeStamp = new Date(
            this._curSelectYear,
            this._curSelectMonth,
            this._curSelectDay
        ).getTime();
        this.lab_yearMonth.string = CommonDepend.DateUtils.transformDateNoZone(
            // canghai TODO 
            timeStamp,
            "Y-M"
        );

        this.calcDaysData();

        for (let i = 0; i < this._dayItemList.length; ++i) {
            let node = this._dayItemList[i];
            let script = node.getComponent(CalendarItem);
            script.setDay(this._daysItemInfo[i]);
        }
    }

    private onSelectDate(dayInfo: ICalendarItemInfo) {
        if (this.selectCallFunc) {
            this.selectCallFunc(dayInfo);

            let timestamp: number = new Date(
                dayInfo.inYear,
                dayInfo.inMonth,
                dayInfo.day
            ).getTime();

            this.initView(timestamp);
        }
    }

    private onClickLeft() {
        if (this.checkBtnMonthLeft()) {
            return;
        }
        if (this._curSelectMonth > 0) {
            this._curSelectMonth -= 1;
        } else {
            this._curSelectMonth = 11;
            this._curSelectYear -= 1;
        }
        this.updateDate();
        this.setBtnMonthLeftDisable();
        this.setBtnYearLeftDisable();
        this.setBtnMonthRightEnable();
        this.setBtnYearRightEnable();
    }

    private onClickBigLeft() {
        this._curSelectYear -= 1;
        this.updateDate();
        this.setBtnMonthLeftDisable();
        this.setBtnYearLeftDisable();
        this.setBtnYearRightEnable();
        this.setBtnMonthRightEnable();
    }

    private onClickRight() {
        if (this.checkBtnMonthRight()) {
            return;
        }
        if (this._curSelectMonth < 11) {
            this._curSelectMonth += 1;
        } else {
            this._curSelectMonth = 0;
            this._curSelectYear += 1;
        }
        this.updateDate();
        this.setBtnMonthRightDisable();
        this.setBtnYearRightDisable();
        this.setBtnMonthLeftEnable();
        this.setBtnYearLeftEnable();
    }

    private onClickBigRight() {
        this._curSelectYear += 1;
        if (this._curSelectYear > this._curYear) {
            this._curSelectYear = this._curYear;
            return;
        }
        this.setBtnYearRightDisable();
        this.setBtnMonthRightDisable();
        this.setBtnMonthLeftEnable();
        this.setBtnYearLeftEnable();
        this.updateDate();
    }

    private setBtnYearRightDisable() {
        if (
            this._curSelectYear >= this._curYear ||
            (this._curSelectYear + 1 == this._curYear && this._curSelectMonth > this._curMonth)
        ) {
            this.btn_bigRight.interactable = false;
            this.btn_bigRight.enableAutoGrayEffect = true;
        }
    }

    private setBtnYearLeftDisable() {
        if (
            this._curSelectYear <= this.minSelectYearLimit ||
            (this._curSelectYear - 1 == this.minSelectYearLimit &&
                this._curSelectMonth < this.minSelectMonthLimit)
        ) {
            this.btn_bigLeft.interactable = false;
            this.btn_bigLeft.enableAutoGrayEffect = true;
        }
    }

    private setBtnMonthRightDisable() {
        if (this.checkBtnMonthRight()) {
            this.btn_right.interactable = false;
            this.btn_right.enableAutoGrayEffect = true;
        }
    }

    private setBtnMonthLeftDisable() {
        if (this.checkBtnMonthLeft()) {
            this.btn_left.interactable = false;
            this.btn_left.enableAutoGrayEffect = true;
        }
    }

    private setBtnYearRightEnable() {
        if (this._curSelectYear < this._curYear && this._curSelectMonth <= this._curMonth) {
            this.btn_bigRight.interactable = true;
            this.btn_bigRight.enableAutoGrayEffect = false;
        }
    }

    private setBtnYearLeftEnable() {
        if (
            this._curSelectYear > this.minSelectYearLimit &&
            this._curSelectMonth >= this.minSelectMonthLimit
        ) {
            this.btn_bigLeft.interactable = true;
            this.btn_bigLeft.enableAutoGrayEffect = false;
        }
    }

    private setBtnMonthRightEnable() {
        if (!this.checkBtnMonthRight()) {
            this.btn_right.interactable = true;
            this.btn_right.enableAutoGrayEffect = false;
        }
    }

    private setBtnMonthLeftEnable() {
        if (!this.checkBtnMonthLeft()) {
            this.btn_left.interactable = true;
            this.btn_left.enableAutoGrayEffect = false;
        }
    }

    private checkBtnMonthRight(): boolean {
        return this._curSelectMonth >= this._curMonth && this._curSelectYear >= this._curYear;
    }

    private checkBtnMonthLeft(): boolean {
        return (
            this._curSelectMonth <= this.minSelectMonthLimit &&
            this._curSelectYear <= this.minSelectYearLimit
        );
    }

    private initBtnStatus() {
        this.setBtnYearRightDisable();
        this.setBtnMonthRightDisable();
        this.setBtnYearRightEnable();
        this.setBtnMonthRightEnable();

        this.setBtnYearLeftDisable();
        this.setBtnMonthLeftDisable();
        this.setBtnYearLeftEnable();
        this.setBtnMonthLeftEnable();
    }
}
