import Listener from "../../../../../../main/core/utils/Listener";
import CommonEventName from "../../CommonEventName";
import CommonSkin from "../../conf/skin/CommonSkin";
import UIMgr from "../base/UIMgr";

class GetAwardUtils {
    constructor() {
        Listener.on(CommonEventName.EVENT_GET_AWARD, (data) => this.openAwardDialog(data));
    }

    public openAwardDialog(awardList) {
        UIMgr.showUI(CommonSkin.LoadOnDemand.getAwardDialog, {
            args: [awardList],
        });
    }
}
let getAwardUtils = new GetAwardUtils();
window["getAwardUtils"] = getAwardUtils;
