const { ccclass, property, menu, requireComponent, disallowMultiple, executeInEditMode } =
    cc._decorator;

export const Gradient = cc.Enum({
    None: 0,
    OneColor: 1,
    TwoColor: 2,
    TriColor: 3,
});

/**
 * 
 * - 
 * - 
 */
@ccclass
@menu("UI/LabelShader")
@requireComponent(cc.Label)
@disallowMultiple()
@executeInEditMode()
export default class LabelShader extends cc.Component {
    //-----------------------
    @property
    private _outlineUse: boolean = false;
    @property({
        tooltip: "",
    })
    get outlineUse() {
        return this._outlineUse;
    }
    set outlineUse(v: boolean) {
        this._outlineUse = v;
        if (CC_EDITOR) {
            this.updateMat();
        }
    }

    @property
    private _outlineWidth: number = 1;
    @property({
        tooltip: "",
        min: 1,
        visible: function () {
            return this.outlineUse;
        },
    })
    get outlineWidth() {
        return this._outlineWidth;
    }
    set outlineWidth(v: number) {
        this._outlineWidth = v;
        if (CC_EDITOR) {
            this.updateMat();
        }
    }

    @property
    private _outlineColor: cc.Color = cc.color(255, 255, 255, 255);
    @property({
        tooltip: "",
        visible: function () {
            return this.outlineUse;
        },
    })
    get outlineColor() {
        return this._outlineColor;
    }
    set outlineColor(v: cc.Color) {
        this._outlineColor = v;
        if (CC_EDITOR) {
            this.updateMat();
        }
    }

    //-----------------------
    @property
    private _gradient = Gradient.None;
    @property({
        tooltip:
            "\nNone: , color\nOneColor: , color1\nTwoColor: -\nTriColor: -",
        type: cc.Enum(Gradient),
    })
    get gradient() {
        return this._gradient;
    }
    set gradient(v: number) {
        this._gradient = v;
        if (CC_EDITOR) {
            this.updateMat();
        }
    }

    @property
    private _color1: cc.Color = cc.color(255, 0, 0, 255);
    @property({
        visible: function () {
            return this.gradient > Gradient.None;
        },
    })
    get color1() {
        return this._color1;
    }
    set color1(v: cc.Color) {
        this._color1 = v;
        if (CC_EDITOR) {
            this.updateMat();
        }
    }

    @property
    private _color2: cc.Color = cc.color(0, 255, 0, 255);
    @property({
        visible: function () {
            return this.gradient > Gradient.OneColor;
        },
    })
    get color2() {
        return this._color2;
    }
    set color2(v: cc.Color) {
        this._color2 = v;
        if (CC_EDITOR) {
            this.updateMat();
        }
    }

    @property
    private _color3: cc.Color = cc.color(0, 0, 255, 255);
    @property({
        visible: function () {
            return this.gradient > Gradient.TwoColor;
        },
    })
    get color3() {
        return this._color3;
    }
    set color3(v: cc.Color) {
        this._color3 = v;
        if (CC_EDITOR) {
            this.updateMat();
        }
    }

    @property(cc.Boolean)
    renderNextFrame: boolean = false;

    private _mtl: cc.Material = null;

    protected onEnable(): void {
        if (this.renderNextFrame) {
            return;
        }
        cc.director.once(cc.Director.EVENT_AFTER_DRAW, this.updateMat, this);
    }
    protected onDisable(): void {
        if (this.renderNextFrame) {
            return;
        }
        cc.director.off(cc.Director.EVENT_AFTER_DRAW, this.updateMat, this);
        this.node["_renderFlag"] |= cc["RenderFlow"].FLAG_COLOR;
    }

    public setLabel(str: string) {
        let label = this.node.getComponent(cc.Label);
        if (!label) return;
        label.string = str;
        this.scheduleOnce(() => {
            this.initMat();
        });
    }

    public updateMat() {
        if (CC_EDITOR) {
            this._mtl = this.node.getComponent(cc.Label).getMaterial(0);
            if (this._mtl.name.indexOf(LabelShader.name) > -1) {
                this.initMat();
            }
        } else {
            this.initMat();
        }
    }

    private initMat() {
        this._mtl = this.node.getComponent(cc.Label).getMaterial(0);
        this._mtl.define("USE_TEXTURE", true, 0);
        this.node.getComponent(cc.Label).setMaterial(0, this._mtl);
        this.use();
    }

    private use() {
        if (!this._mtl || this.node.width == 0 || this.node.height == 0) return;
        this._mtl.setProperty("i_outline", this.outlineUse ? 1 : 0);
        this._mtl.setProperty("i_outlineWidth", [
            this.outlineWidth / this.node.width,
            this.outlineWidth / this.node.height,
        ]);
        this._mtl.setProperty("i_outlineColor", [
            this.outlineColor.r / 255,
            this.outlineColor.g / 255,
            this.outlineColor.b / 255,
            this.outlineColor.a / 255,
        ]);
        this._mtl.setProperty("i_gradient", this.gradient - 1);
        switch (this.gradient) {
            case Gradient.None:
                this._mtl.setProperty("i_color1", [
                    this.node.color.r / 255,
                    this.node.color.g / 255,
                    this.node.color.b / 255,
                    this.node.color.a / 255,
                ]);
                break;
            case Gradient.OneColor:
            case Gradient.TwoColor:
            case Gradient.TriColor:
                this._mtl.setProperty("i_color1", [
                    this.node.color.r / 255,
                    this.node.color.g / 255,
                    this.node.color.b / 255,
                    this.node.color.a / 255,
                ]);
                this._mtl.setProperty("i_color1", [
                    this.color1.r / 255,
                    this.color1.g / 255,
                    this.color1.b / 255,
                    this.color1.a / 255,
                ]);
                this._mtl.setProperty("i_color2", [
                    this.color2.r / 255,
                    this.color2.g / 255,
                    this.color2.b / 255,
                    this.color2.a / 255,
                ]);
                this._mtl.setProperty("i_color3", [
                    this.color3.r / 255,
                    this.color3.g / 255,
                    this.color3.b / 255,
                    this.color3.a / 255,
                ]);
                break;
        }
    }
}
