import { LanguageType } from "../../../../../../main/core/conf/LanguageType";
import BaseBundleLanguage from "../../../../../../main/core/language/BaseBundleLanguage";
import { CommonLanguageEn } from "./en/CommonLanguageEn";
import { CommonLanguagePt } from "./pt/CommonLanguagePt";
import { CommonLanguageHi } from "./hi/CommonLanguageHi";
import { CommonLanguageId } from "./id/CommonLanguageId";
import { CommonLanguageZh } from "./zh/CommonLanguageZh";

type TLanguage = typeof CommonLanguageEn &
    typeof CommonLanguagePt &
    typeof CommonLanguageHi &
    typeof CommonLanguageId &
    typeof CommonLanguageZh;
class CommonLanguage extends BaseBundleLanguage<TLanguage> {
    protected language: LG.ILanguageConfigs = {
        [LanguageType.EN]: CommonLanguageEn,
        [LanguageType.PT]: CommonLanguagePt,
        [LanguageType.HI]: CommonLanguageHi,
        [LanguageType.ID]: CommonLanguageId,
        [LanguageType.ZH]: CommonLanguageZh,
    };
}
export default new CommonLanguage().get();
