//import BrcColorVariableModel from "../../Game/Brc/BRC_ColorDish/Script/Model/BrcColorVariableModel";

import { GameType } from "../../../../../../main/core/conf/GameType";

const { ccclass, property } = cc._decorator;

@ccclass
export default class RuleItem extends cc.Component {
    @property(cc.Label)
    titleText: cc.Label = null;

    @property(cc.Node)
    contentNode: cc.Node = null;

    @property(cc.Color)
    tagColor: cc.Color = null;

    protected onLoad(): void {}

    public initView(title, content, contentRich, contentDeploy, gameID) {
        let self = this;
        if (cc.isValid(self.titleText)) {
            if (title) {
                self.titleText.node.active = true;
                self.titleText.string = title;
            } else {
                self.titleText.node.active = false;
                self.titleText.string = "";
            }
        }

        let formatedContent = "";
        if (content) {
            let colorValue = "";

            if (cc.isValid(self.tagColor)) {
                colorValue = "#" + self.tagColor.toHEX("#rrggbb");
                formatedContent = content.replace(/{tag_color}/g, colorValue);
            } else {
                formatedContent = content;
            }
        }

        if (formatedContent) {
            if (cc.isValid(self.contentNode)) {
                self.contentNode.active = true;
                if (contentRich) {
                    let richTextLabel = self.contentNode.getComponent(cc.RichText);
                    if (cc.isValid(richTextLabel)) {
                        richTextLabel.string = formatedContent;
                    }
                } else {
                    let label = self.contentNode.getComponent(cc.Label);
                    if (cc.isValid(label)) {
                        label.string = formatedContent;
                    }
                }
            }
        } else {
            if (cc.isValid(self.contentNode)) {
                self.contentNode.active = false;
            }
        }
    }
}
