import { UniverseProto } from "../universe/proto/UniverseProto";

const { ccclass, property } = cc._decorator;
type SlapFaceData = ProtoObject<UniverseProto.PopUpsConfResp>["PopUps"];

const BOOLEAN_TO_STORAGE = {
    FLASE: "0",
    TRUE: "1",
};

const STORAGE_TO_BOOLEAN = {
    [BOOLEAN_TO_STORAGE.FLASE]: false,
    [BOOLEAN_TO_STORAGE.TRUE]: true,
};
@ccclass
export default class CommonStorageMgr {
    private static _instance: CommonStorageMgr = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new CommonStorageMgr();
        }

        return this._instance;
    }

    private constructor() { }

    /**
     * -gameType
     */
    public saveGameType(param) {
        CommonDepend.LocalStorageMgr.saveData("_gameType", param);
    }

    public readGameType() {
        let gameType = CommonDepend.LocalStorageMgr.readData("_gameType");
        return gameType;
    }

    /**
     * -
     */
    public saveCustomerRed(count: number) {
        CommonDepend.LocalStorageMgr.saveData("_CustomerRed", String(count));
    }

    /**
     * -Master 
     */
    public saveRechargeRed(count: number) {
        CommonDepend.LocalStorageMgr.saveData("_RechargeRed", String(count));
    }

    /**
     * -
     */
    public saveTaskSystemRed(count: number) {
        CommonDepend.LocalStorageMgr.saveData("_TaskSystemRed", String(count));
    }

    /**
     * -APPLoading
     */
    public saveAppFirstStart(isFirst: boolean) {
        let value: string = isFirst ? BOOLEAN_TO_STORAGE.TRUE : BOOLEAN_TO_STORAGE.FLASE;
        CommonDepend.LocalStorageMgr.saveData("_app_first_start", value);
    }

    public readAppFirstStart(): boolean {
        let value = CommonDepend.LocalStorageMgr.readData("_app_first_start");
        if (value == null) {
            return true; // 
        }
        return STORAGE_TO_BOOLEAN[value];
    }

    /**  */
    public saveActivityReminderShowState(param: string) {
        CommonDepend.LocalStorageMgr.saveData("_activity_reminder_show_state", param);
    }

    /**  */
    public readActivityReminderShowState(): boolean {
        let flag = CommonDepend.LocalStorageMgr.readData("_activity_reminder_show_state");
        if (flag == null) {
            this.saveActivityReminderShowState("on"); // 
            flag = "on";
        }
        return flag == "on";
    }

    public savaActivityReminderPreData(param: string) {
        CommonDepend.LocalStorageMgr.saveData("_activity_reminder_pre_data", param);
    }

    public readActivityReminderPreData(): string {
        return CommonDepend.LocalStorageMgr.readData("_activity_reminder_pre_data");
    }

    public savaDrumRedDotFlag(redDotFlag: boolean) {
        let value: string = redDotFlag ? BOOLEAN_TO_STORAGE.TRUE : BOOLEAN_TO_STORAGE.FLASE;
        CommonDepend.LocalStorageMgr.saveData("_drum_red_dot_flag", value);
    }

    public readDrumRedDotFlag(): boolean {
        let value = CommonDepend.LocalStorageMgr.readData("_drum_red_dot_flag");
        if (value == null) {
            return false;
        }
        return STORAGE_TO_BOOLEAN[value];
    }

    public savaFirstOpenActivityReminderFlag(isFirst: boolean) {
        let value: string = isFirst ? BOOLEAN_TO_STORAGE.TRUE : BOOLEAN_TO_STORAGE.FLASE;
        CommonDepend.LocalStorageMgr.saveData("_first_open_activity_reminder", value);
    }

    public readFirstOpenActivityReminderFlag(): boolean {
        let value = CommonDepend.LocalStorageMgr.readData("_first_open_activity_reminder");
        if (value == null) {
            this.savaFirstOpenActivityReminderFlag(false);
            return true; // 
        }
        return STORAGE_TO_BOOLEAN[value];
    }

    public savaDrumPos(PosStr: string) {
        CommonDepend.LocalStorageMgr.saveData("_drum_Pos", PosStr);
    }

    public readDrumPos(): string {
        let PosStr = CommonDepend.LocalStorageMgr.readData("_drum_Pos");
        return PosStr;
    }

    public savaTaskSystemId(taskId: string, userId: number) {
        CommonDepend.LocalStorageMgr.saveData("_task_system_task_id" + userId, taskId);
    }

    public readTaskSystemId(userId: number): string {
        return CommonDepend.LocalStorageMgr.readData("_task_system_task_id" + userId);
    }

    /**
     *  
     * @param id
     */
    writeNoticeReadRecords(id) {
        let localData = CommonDepend.LocalStorageMgr.readData("_notice_read_data_") ?? "[]";
        let noticeReadRecordList = JSON.parse(localData);
        noticeReadRecordList.push(id);
        CommonDepend.LocalStorageMgr.saveData(
            "_notice_read_data_",
            JSON.stringify(noticeReadRecordList)
        );
    }

    /**
     *  
     */
    readNoticeReadRecords() {
        let ret = CommonDepend.LocalStorageMgr.readData("_notice_read_data_") ?? "[]";
        return JSON.parse(ret);
    }

    /**
     *  
     * @param readRecordList
     * @returns
     */
    forceSaceNoticeReadRecords(readRecordList) {
        if (!readRecordList || readRecordList.length === 0) return;
        let localStorageMgr = CommonDepend.LocalStorageMgr;
        localStorageMgr.saveData("_notice_read_data_", JSON.stringify(readRecordList));
    }

    /**
     *  
     * @param id
     */
    writeNoticeSlapRecords(id) {
        let localStorageMgr = CommonDepend.LocalStorageMgr;
        let localData = localStorageMgr.readData("_notice_slap_data_") ?? "[]";
        let noticeSlapList = JSON.parse(localData);
        noticeSlapList.push(id);
        localStorageMgr.saveData("_notice_slap_data_", JSON.stringify(noticeSlapList));
    }

    readNoticeSlapRecords() {
        let localStorageMgr = CommonDepend.LocalStorageMgr;
        let ret = localStorageMgr.readData("_notice_slap_data_") ?? "[]";
        return JSON.parse(ret);
    }

    /**
     *  
     * @returns
     */
    readWithdrawUps() {
        let slapfaceLocalJson = CommonDepend.LocalStorageMgr.readData("_withdraw_ups");
        if (!slapfaceLocalJson) return false;
        return JSON.parse(slapfaceLocalJson);
    }

    /**
     *  
     * @param upsConf
     */
    writeWithdrawUps(upsConf: SlapFaceData) {
        !!upsConf && this._saveSlapfaceData("_withdraw_ups", upsConf);
    }

    /**
     *   
     */
    clearWithdrawUps() {
        CommonDepend.LocalStorageMgr.clearData("_withdraw_ups");
    }

    /**
     *  
     * @returns
     */
    readTopupUps() {
        let slapfaceLocalJson = CommonDepend.LocalStorageMgr.readData("_topup_ups");
        if (!slapfaceLocalJson) return false;
        return JSON.parse(slapfaceLocalJson);
    }

    /**
     *  
     * @param upsConf
     */
    writeTopupUps(upsConf: SlapFaceData) {
        !!upsConf && this._saveSlapfaceData("_topup_ups", upsConf);
    }

    /**
     *   
     */
    clearTopupUps() {
        CommonDepend.LocalStorageMgr.clearData("_topup_ups");
    }

    /**
     *  
     * @returns
     */
    readFishTopUpUps() {
        let slapfaceLocalJson = CommonDepend.LocalStorageMgr.readData("_fish_topup_ups");
        if (!slapfaceLocalJson) return false;
        return JSON.parse(slapfaceLocalJson);
    }

    /**
     *  
     * @param upsConf
     */
    writeFishTopUpUps(upsConf: SlapFaceData) {
        !!upsConf && this._saveSlapfaceData("_fish_topup_ups", upsConf);
    }

    /**
     *   
     */
    clearFishTopUpUps() {
        CommonDepend.LocalStorageMgr.clearData("_fish_topup_ups");
    }

    /**
     *  
     * @returns
     */
    readTopupFailUps() {
        let slapfaceLocalJson = CommonDepend.LocalStorageMgr.readData("_topup_fail_ups");
        if (!slapfaceLocalJson) return false;
        return JSON.parse(slapfaceLocalJson);
    }

    /**
     *  
     * @param upsConf
     */
    writeTopupFailUps(upsConf: SlapFaceData) {
        !!upsConf && this._saveSlapfaceData("_topup_fail_ups", upsConf);
    }

    /**
     *  
     */
    clearTopupFailUps() {
        CommonDepend.LocalStorageMgr.clearData("_topup_fail_ups");
    }

    /**
     * 
     * @param key
     * @param upsConf
     */
    private _saveSlapfaceData(key: string, upsConf: SlapFaceData) {
        let topupSaveJson = JSON.stringify(upsConf);
        CommonDepend.LocalStorageMgr.saveData(key, topupSaveJson);
    }

    /**
     * 
     * @param visible
     */
    writeWithdrawRecords(visible: boolean) {
        let saveVisible = visible ? "1" : "0";
        CommonDepend.LocalStorageMgr.saveData("withdraw_records", saveVisible);
    }

    /**
     * 
     */
    readWithdrawRecords() {
        return !!~~CommonDepend.LocalStorageMgr.readData("withdraw_records");
    }

    /**
     * 
     * @param visible
     */
    writeTopupRecords(visible: boolean) {
        let saveVisible = visible ? "1" : "0";
        CommonDepend.LocalStorageMgr.saveData("topup_records", saveVisible);
    }

    /**
     * 
     */
    readTopupRecords() {
        return !!~~CommonDepend.LocalStorageMgr.readData("topup_records");
    }

    /**
     * 
     */
    writeCustomerUnreadMsg(visible: boolean) {
        let saveVisible = visible ? "1" : "0";
        CommonDepend.LocalStorageMgr.saveData("customer_unread_msg", saveVisible);
    }

    /**
     * 
     */
    readCustomerUnreadMsg() {
        return !!~~CommonDepend.LocalStorageMgr.readData("customer_unread_msg");
    }

    /**
    *  
    * @returns
    */
    readFishingFireCount() {
        let count = CommonDepend.LocalStorageMgr.readData("_fish_fire_count") ?? "0";
        return Number(count);
    }

    /**
     *  
     * @param upsConf
     */
    writeFishingFireCount(count: string) {
        CommonDepend.LocalStorageMgr.saveData("_fish_fire_count", count);
    }

    /**
     *  
     */
    clearFishingFireCount() {
        CommonDepend.LocalStorageMgr.clearData("_fish_fire_count");
    }

}
