import { BundleMgr } from "../../../../../../main/core/bd/BundleMgr";
import CommonSkin from "../../conf/skin/CommonSkin";

export class ChipUtil {
    private static _instance: ChipUtil = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new ChipUtil();
        }

        return this._instance;
    }

    private constructor() {}

    public getChipTypeSpriteFrame(spriteName: string): cc.SpriteFrame {
        let res = CommonSkin.Priority.commonChipType[spriteName];
        if (!res) {
            return null;
        }
        return BundleMgr.getInstance().getResFromBundle(res);
    }

    public getChipVauleSpriteFrame(spriteName: string): cc.SpriteFrame {
        let res = CommonSkin.Priority.commonChipValue[spriteName];
        if (!res) {
            return null;
        }
        return BundleMgr.getInstance().getResFromBundle(res);
    }
}
