import BundleCenter from "../../../../main/core/bd/BundleCenter";
import { BundleNames } from "../../../../main/core/conf/BundleNames";
import { GameType } from "../../../../main/core/conf/GameType";
import UIMgr from "./Component/base/UIMgr";

import { GameKind } from "../../../../main/core/conf/GameEnum";
import { Injector } from "../../../../main/core/utils/decorators/Injector";
import CommonLanguage from "./conf/lang/CommonLanguage";
import LoadingSceneStateObserver from "./obs/LoadingSceneStateObserver";
import { SceneType } from "./SceneType";
import Switch from "./Switch";

export class SceneControl {
    private static _instance: SceneControl = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new SceneControl();
        }

        return this._instance;
    }

    @Injector.Inject(LoadingSceneStateObserver)
    private loadingSceneStateObserver: LoadingSceneStateObserver = null;

    private _currentScene: SceneType = SceneType.None;

    public get scene() {
        return this._currentScene;
    }

    private constructor() {}

    public launchScene(gameId: GameType) {
        let bundleName = CommonDepend.GameDatabase.getBundleNameWithGameId(gameId);
        if (!bundleName) {
            return;
        }
        switch (bundleName) {
            case BundleNames.LOADING: {
                return this.onLaunchScene(BundleNames.LOADING, "LoginScene");
            }
            case BundleNames.BIG_SMALL: {
                return UIMgr.applyLoadingUi(this.onLaunchScene(bundleName));
            }
            default: {
                return this.onLaunchScene(bundleName);
            }
        }
    }

    private onLaunchScene(bundleName: BundleNames, scene?: string) {
        Switch.isSwitchUi = true;
        this.loadingSceneStateObserver.onLoadingStart(bundleName);
        return new Promise((resolve) => {
            BundleCenter.getInstance()
                .launchScene(bundleName, scene)
                .onProgress((current, total) => {
                    this.loadingSceneStateObserver.set("progress", current / total);
                })
                .onSuccess(() => {
                    this.loadingSceneStateObserver.onLoadingSuccess(bundleName);
                    resolve(true);
                })
                .onFailed(() => {
                    if (bundleName !== BundleNames.HALL) {
                        UIMgr.showAlert({
                            contentText: CommonLanguage.loadingResFailure,
                            negativeBtnText: CommonLanguage.returnHall,
                            positiveBtnText: CommonLanguage.tryAgain,
                            style: 3,
                        }).onClose((result) => {
                            switch (result) {
                                case false: {
                                    this.loadingSceneStateObserver.onError(bundleName);
                                    break;
                                }
                                case true: {
                                    this.onLaunchScene(bundleName, scene);
                                    break;
                                }
                            }
                        });
                    }
                });
        }).then(() => {
            switch (bundleName) {
                case BundleNames.HALL: {
                    this._currentScene = SceneType.Hall;
                    break;
                }
                case BundleNames.LOADING: {
                    this._currentScene = SceneType.Login;
                    break;
                }
                default: {
                    this._currentScene = SceneType.Game;
                }
            }
        });
    }
}
window["scx"] = SceneControl.getInstance();
