import { GameType } from "../../../../../../../main/core/conf/GameType";
import UIMgr from "../../../Component/base/UIMgr";
import CommonSkin from "../../../conf/skin/CommonSkin";
import { SceneControl } from "../../../SceneControl";
import UniverseServiceMgr from "../../../universe/UniverseServiceMgr";
import { BlackIpUtil } from "../../../utils/BlackIpUtil";
import { ServiceErrorConfirmAlert } from "./ServiceErrorConfirmAlert";

export class ServiceErrorToLoginScene extends ServiceErrorConfirmAlert {
    protected onConfirmBtnClick(): void {
        CommonDepend.UserManager.clearAccount();
        CommonDepend.UserManager.clearAvailableAvatar();
        CommonDepend.LocalStorageMgr.clearNewAvatarId();
        CommonDepend.LocalStorageMgr.clearUserLoginData();

        if (cc.director.getScene().name == "LoginScene") {
            //
            if (BlackIpUtil.chcekIsInBlackList()) return;
            UIMgr.showUI(CommonSkin.LoadOnDemand.PasswordLogin);
        } else {
            SceneControl.getInstance()
                .launchScene(GameType.LOGIN)
                .then(() => {
                    setTimeout(() => {
                        if (BlackIpUtil.chcekIsInBlackList()) return;
                        UIMgr.showUI(CommonSkin.LoadOnDemand.PasswordLogin);
                    });
                });
        }
    }
}
