import { AudioManager } from "../../../../../main/core/audio/AudioManager";
import CommonSkin from "../conf/skin/CommonSkin";

const { ccclass, property } = cc._decorator;

@ccclass
export default class StartAndStopAnim extends cc.Component {
    @property(sp.Skeleton)
    animSpine: sp.Skeleton = null;

    _isShowEndBetAnim = false;

    public init(): void {
        let self = this;
        self._isShowEndBetAnim = false;
        if (cc.isValid(self.node)) {
            self.node.opacity = 0;
        }
        if (cc.isValid(self.animSpine)) {
            self.animSpine.node.opacity = 0;
        }
    }

    public playStartBetAnimAction(callback?: Function): void {
        AudioManager.getInstance().playEffect(CommonSkin.LoadOnDemand.Show, false, 1);
        AudioManager.getInstance().playEffect(CommonSkin.LoadOnDemand.Start, false, 1);
        this.playAnim("start_betting", callback);
    }

    public playEndBetAnimAction(callback?: Function, option?): void {
        if (this._isShowEndBetAnim) {
            return;
        }
        AudioManager.getInstance().playEffect(CommonSkin.LoadOnDemand.Show, false, 1);
        AudioManager.getInstance().playEffect(CommonSkin.LoadOnDemand.Stop, false, 1);
        this.playAnim("stop_betting", callback);
        this._isShowEndBetAnim = true;
    }

    public playStartGameAnimAction(callback?: Function, option?): void {
        AudioManager.getInstance().playEffect(CommonSkin.LoadOnDemand.Show, false, 1);
        this.playAnim("game_start", callback, option);
    }

    private playAnim(animName: string, callback?: Function, option?) {
        if (!cc.isValid(this.node)) return;
        this.node.opacity = 255;
        if (!cc.isValid(this.animSpine)) return;
        this.animSpine.node.opacity = 255;
        this.animSpine.timeScale = option && option.timeScale ? option.timeScale : 1;
        this.animSpine.getComponent(sp.Skeleton).setAnimation(0, animName, false);
        this.animSpine.getComponent(sp.Skeleton).setCompleteListener(() => {
            this.init();
            callback && callback();
        });
    }
}
