    private getHostList(gameID): string[] {
        let hostList = NewGameInfo.getInstance().getGameHttpServer(gameID);
        return [].concat(hostList);
    }

    private getHost(hostList: string[]): string {
        let host = null;
        if (hostList) {
            host = hostList[0];
        }
        return host;
    }

    private createHallRequest<
        Req extends {
            fromObject(data: ReqData): pb_1.Message;
        },
        Resp extends {
            toObject();
        },
        ReqData extends ProtoObject<Req>
    >(options: {
        api: string;
        data?: ReqData;
        request: Req;
        response: { new (): Resp };
        httpCallBack?: IHttpCallback;
    }): Promise<ProtoObject<Resp>["data"]> {
        let host_backup = this.getHostList(GameType.GAME_HALL);
        let host = this.getHost(host_backup);
        let req_header = new ApiProto.ReqHeader();

        let message = options.request.fromObject(options.data || ({} as ReqData));
        let protoInfo: IProtoInfo = {
            req_plain: ApiReqPlain.createApiPlain(message),
            req_header: req_header,
            req_prototype: options.request,
            resp_prototype: options.response,
        };
        return new Promise((resolve, reject) => {
            if (CC_DEBUG) {
                CC_DEBUG && cc.log(`${options.api} query:`, message.toObject());
            }

            new HttpRequest().post(host, host_backup, options.api, protoInfo, {
                success: (res: Resp) => {
                    if (res instanceof Array) {
                        resolve(
                            res.map((item) => {
                                return item.toObject();
                            })
                        );
                    } else {
                        resolve(res?.toObject?.() ?? res);
                    }

                    options?.httpCallBack?.success?.(res);
                },
                error: (res) => {
                    reject(res);
                    options?.httpCallBack?.error?.(res);
                },
            });
        });
    }

