    private loginRequest(
        type: ApiProto.IdentityType,
        identity: string,
        password?: string,
        vcode?: string,
        httpCallBack?: IHttpCallback
    ) {
        return this.createHallRequest({
            httpCallBack: httpCallBack,
            api: ApiUrl.login,
            data: {
                identity_info: {
                    type: type,
                    identity: identity,
                    cert: {
                        password: password,
                        vcode: vcode,
                    },
                },
            },
            request: ApiProto.LoginReq,
            response: ApiProto.LoginResp,
        });
    }

