import { ApiProto } from "../../service/api/ApiProto";

interface IGameReplayMessageData {
    msgId: number;
    msgData: any;
}

interface IGameReplayOptions {
    messageMap?: {
        [index: number]: Socket.IMessageCtor;
    };
    timeScale?: number;
    messageDelay?: {
        [msgId: number]: number;
    };
    onMessage?: (data: IGameReplayMessageData) => void;
}

class GameReplayMessageItem {
    constructor(private _message: ProtoObject<ApiProto.Msgs>) { }

    public timestamp() {
        return this._message.timestamp;
    }

    public toObject(): IGameReplayMessageData {
        return {
            msgId: this._message.msg_type,
            msgData: JSON.parse(this._message.msg_info),
        };
    }
}

enum State {
    None,
    Play,
    Pause,
}

export enum ReplayState {
    None,
    Play,
    Pause,
}

export enum ReplayOperate {
    /**  */
    Play,
    /**  */
    Pause,
    /**  */
    Resume,
    /**  */
    Stop,
}

export default class GameReplayControl {
    private _queue: GameReplayMessageItem[] = null;
    private _playIndex: number = -1;

    private _state: State = State.None;

    private _delayTimeId: NodeJS.Timeout = null;

    constructor(
        private _data: ProtoObject<ApiProto.GameReplayRecord>,
        private _options: IGameReplayOptions = {}
    ) {
        this.initialize();
    }

    public play() {
        this._playIndex = 0;
        this.state = State.Play;
    }
    public pause() {
        this.state = State.Pause;
    }
    public resume() {
        this.state = State.Play;
    }
    public stop() {
        this.state = State.None;
    }

    public get timeScale(): number {
        return this._options?.timeScale ?? 1;
    }

    public set timeScale(value: number) {
        this._options.timeScale = value;
    }

    private initialize() {
        this.createQueue();

        cc.game.on(
            cc.game.EVENT_SHOW,
            () => {
                this.resume();
            },
            this
        );
        cc.game.on(
            cc.game.EVENT_HIDE,
            () => {
                this.pause();
            },
            this
        );
    }

    public destroy() {
        this.stop();
        cc.game.targetOff(this);
    }

    private get state() {
        return this._state;
    }

    private set state(value: State) {
        if (value === this.state) {
            console.warn("invalid state set", this.state, value);
            return;
        }
        switch (value) {
            case State.None: {
                this._playIndex = -1;
                this.stopDelay();
                break;
            }
            case State.Pause: {
                if (this.state == State.None) {
                    console.warn("invalid state set", this.state, value);
                    return;
                }
                this.stopDelay();
                break;
            }
            case State.Play: {
                this.toNext(0);
                break;
            }
        }

        this._state = value;
    }

    private stopDelay() {
        if (this._delayTimeId) {
            clearTimeout(this._delayTimeId);
        }
        this._delayTimeId = null;
    }

    private toNext(delayTime: number) {
        this.stopDelay();
        this._delayTimeId = setTimeout(() => {
            this.next();
        }, delayTime);
    }

    private next() {
        let message = this._queue[this._playIndex++];
        if (!message) {
            console.warn("[GameReplayHelper] message is empty!");
            this.stop();
            return;
        }
        const nextMessage = this._queue[this._playIndex];

        const objectData = message.toObject();
        const data = {
            msgId: objectData.msgId,
            msgData:
                this._options?.messageMap?.[objectData.msgId]
                    ?.fromObject?.(objectData.msgData)
                    ?.toObject() ?? objectData.msgData,
        };

        const delayTime =
            (this._options?.messageDelay?.[objectData.msgId] ??
                (nextMessage?.timestamp?.() ?? message.timestamp()) - message.timestamp()) /
            this.timeScale;

        let date = new Date();
        CC_DEBUG &&
            cc.log(
                `%c[${date.toLocaleString()}:${date.getMilliseconds()}] <replay> on=> Id:${data.msgId
                }->${this._options?.messageMap?.[objectData.msgId]?.name ?? "Unknown"
                } ,delayTime:${delayTime} \n`,
                "color:#a5a",
                data.msgData
            );
        this._options?.onMessage?.(data);

        this.toNext(delayTime);
    }

    private createQueue() {
        this._queue = this._data.msgs.map((msg) => {
            return new GameReplayMessageItem(msg);
        });
    }
}
