import PopUpViewBase from "../../../../../../main/core/popUpViewUtils/PopUpViewBase";
import EnumActive from "../../../../../../main/core/ui/component/EnumActive";
import EnumController from "../../../../../../main/core/ui/component/EnumController";
import ObjectLabel from "../../../../../../main/core/ui/component/ObjectLabel";
import { Instance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import UIMgr from "../../Component/base/UIMgr";
import CommonLanguage from "../../conf/lang/CommonLanguage";
import CommonSkin from "../../conf/skin/CommonSkin";
import CommonFeatureSettingObserver from "../../obs/CommonFeatureSettingObserver";
import CommonPromoterVerifyBrdObserver from "../../obs/CommonPromoterVerifyBrdObserver";
import ApiMgr from "../../service/api/ApiMgr";
import { ApiProto } from "../../service/api/ApiProto";
import { CurrencyUtil } from "../../utils/CurrencyUtil";
import { FormatTestUtil } from "../../utils/FormatTestUtil";
import { CommonUserUtil } from "../CommonUserUtil";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CommonRegisterForGiftView extends PopUpViewBase {
    @property({ type: cc.Label, displayName: "" })
    amount: cc.Label = null;
    @property({ type: cc.Node, displayName: "" })
    title: cc.Node = null;
    @property({ type: cc.Node, displayName: "" })
    confirmPassword: cc.Node = null;

    @property({ type: cc.Node, displayName: "" })
    password: cc.Node = null;

    @property({ type: cc.Node, displayName: "" })
    account: cc.Node = null;
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {}

    @property({ type: cc.EditBox })
    inviteEdit: cc.EditBox = null;

    @property(EnumActive)
    inviteNode: EnumActive = null;

    protected onHasInviteCodeClicked() {
        this.inviteNode.show4Name("editBox");
    }

    public init(...args: any[]): void {
        this.getComponent(EnumController)?.show4Name(
            Instance(CommonPromoterVerifyBrdObserver).get() ? "invite" : "normal"
        );
        const launchAmount = Instance(CommonFeatureSettingObserver).getUpgradeAmount();
        this.title.active = !!launchAmount;
        this.amount.getComponent(ObjectLabel).object = {
            value: CurrencyUtil.formatMoney(launchAmount),
        };
    }

    @UIMgr.applyLoadingUi
    protected async onRegisterBtnClick() {
        let password = this.password.getComponentInChildren(cc.EditBox).string;
        let confirmPassword = this.confirmPassword.getComponentInChildren(cc.EditBox).string;
        let account = lodash.toLower(this.account.getComponentInChildren(cc.EditBox).string);

        if (account.length < 6) {
            UIMgr.showToast(CommonLanguage.accountRuleShort);
            return;
        }

        if (password.length < 6 || confirmPassword.length < 6) {
            UIMgr.showToast(CommonLanguage.passworldLenLimit);
            return;
        }

        if (confirmPassword != password) {
            UIMgr.showToast(CommonLanguage.passwordDifferent);
            return;
        }

        if (FormatTestUtil.containInvaildSymbol(account)) {
            UIMgr.showToast(CommonLanguage.accountRuleShort);
            return;
        }

        if (FormatTestUtil.containInvaildSymbol(password)) {
            UIMgr.showToast(CommonLanguage.passworldFormot);
            return;
        }

        if (
            !!Number(this.inviteEdit?.string ?? 0) &&
            !(await CommonUserUtil.checkReferId(Number(this.inviteEdit?.string ?? 0)))
        ) {
            UIMgr.showToast(CommonLanguage.invalidReferId);
            return;
        }

        return ApiMgr.getInstance()
            .bindIdentity({
                identity_info: {
                    type: ApiProto.IdentityType.AT_ACCOUNT_NO,
                    identity: account,
                    cert: {
                        password: password,
                    },
                },
                refer_id: Number(this.inviteEdit?.string ?? 0),
            })
            .then(() => {
                UIMgr.showToast(CommonLanguage.upgradeAccountSuccess);
                CommonDepend.AdjustTrack.trackEvent(CommonDepend.AdjustTrack.register_account);
                return CommonUserUtil.passwordLogin({
                    account,
                    password,
                }).then(() => {
                    this.close(true);
                    return true;
                });
            });
    }

    protected onPasswordLogin() {
        UIMgr.showUI(CommonSkin.LoadOnDemand.PasswordLogin);
    }

    // update (dt) {}
}
