export namespace FormatConstBRL {
    /**  */
    export const MIN_PHONE_LEN = 10;
    /**  */
    export const MAX_PHONE_LEN = 11;

    export const PHONE_MOSAIC = {
        reg: /(\d{2})(\d{2})(.+)(\d{4})$/,
        template: "($1)($2)****$4",
    };

    export const PHONE_FORMAT = {
        reg: /^(\d{2})(\d{5})(\d+)/,
        template: "($1)$2-$3",
    };
}
