import { Injector } from "../../../../../../../main/core/utils/decorators/Injector";
import BaseSocketAttachment from "../../../service/ws/attachment/BaseSocketAttachment";
import { UniverseProto } from "../../../universe/proto/UniverseProto";
import UniverseSocketMgr from "../../../universe/UniverseSocketMgr";
import CommonBroadCastObserver from "../../obs/CommonBroadCastObserver";

const { MessageID } = UniverseProto;

export default class CommonBroadCastSocketAttachment extends BaseSocketAttachment {
    public tag: string = "broadcast";

    @Injector.Inject(CommonBroadCastObserver)
    private observer: CommonBroadCastObserver = null;

    public onMessage(data: Socket.TMessageObject<UniverseProto.MessageID, any>): void {
        switch (data.msgId) {
            case MessageID.BROADCAST_NT: {
                this.observer.push(data.msgData);
                break;
            }
            case MessageID.BROADCASTS_NT: {
                let ret = data.msgData as ProtoObject<UniverseProto.BroadCastsNT>;
                this.observer.push(ret.BroadCastList);
                break;
            }
        }
    }
}
UniverseSocketMgr.applyAttachment(CommonBroadCastSocketAttachment);
