import PopUpViewBase from "../../../../../../../main/core/popUpViewUtils/PopUpViewBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class SevenUpDownRule extends PopUpViewBase {
    @property([cc.Label])
    oddsLabs: cc.Label[] = [];

    protected onLoad(): void {
        let oddsStr = CommonDepend.LocalStorageMgr.readSevenUpDownOdds();
        let odds = JSON.parse(oddsStr) || ["1", "1", "1"];
        for (let i = 0; i < this.oddsLabs.length; i++) {
            this.oddsLabs[i].string = odds[i] + "x";
        }
    }
}
