import PopUpViewBase from "../../../../../../main/core/popUpViewUtils/PopUpViewBase";
import DropDownListEx from "../../../../../../main/core/ui/component/DropDownListEx/DropDownListEx";
import Listener from "../../../../../../main/core/utils/Listener";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { Tools } from "../../../../../../main/core/utils/Tools";
import CommonEventName from "../../CommonEventName";
import UIMgr from "../../Component/base/UIMgr";
import CommonLanguage from "../../conf/lang/CommonLanguage";
import CommonSkin from "../../conf/skin/CommonSkin";
import { CommonUserUtil } from "../CommonUserUtil";
import { CommonAccountUtil } from "../utils/CommonAccountUtil";
import { CommonUserLoginCheckUtil } from "../utils/CommonUserLoginCheckUtil";
import { CommonUserLoginUiUtil } from "../utils/CommonUserLoginUiUtil";

const { ccclass, property } = cc._decorator;

@ccclass
export default class PasswordLogin extends PopUpViewBase {
    @property({ type: cc.EditBox })
    accountNode: cc.EditBox = null;

    @property({ type: cc.EditBox })
    pwdNode: cc.EditBox = null;

    public init() {
        this.initUserDropList();
    }

    protected initUserDropList() {
        let accounts = CommonAccountUtil.getHistoryAccounts() as string[][];
        let dataList =
            accounts
                ?.filter((v) => {
                    return !!v[3];
                })
                .map((v) => {
                    return { title: v[2], password: v[3] };
                })
                .splice(0, 3) ?? [];
        let dropList = this.getComponentInChildren(DropDownListEx);
        dropList.initItems({
            data: dataList,
            onSelect: (index: number, data) => {
                this.accountNode.string = data.title;
                this.pwdNode.string = data["password"];
            },
            onDelete: (index: number, data, list) => {
                if (list.length <= 1) {
                    UIMgr.showToast(CommonLanguage.deleteAccountFailed);
                } else {
                    CommonAccountUtil.deleteAccount(data.title);
                }
            },
        });
        if (dataList.length >= 1) {
            this.accountNode.string = lodash.first(dataList).title;
            this.pwdNode.string = lodash.first(dataList).password;
        }
    }

    @UIMgr.applyLoadingUi
    protected async onLoginBtnClick(event: { target: cc.Node }) {
        let self = this;
        Tools.clickManager(event.target);

        let loginAccount = lodash.toLower(this.accountNode.string);
        let password = this.pwdNode.string;

        if (!this.verifyData(loginAccount, password)) {
            return;
        }

        return CommonUserUtil.passwordLogin({
            account: loginAccount,
            password,
            success: (data) => {},
        }).then(() => {
            Listener.emit(CommonEventName.PASSWORD_LOGIN_SUECECC);
            return CommonUserLoginUiUtil.launchHall();
        });
    }

    protected onRegisterBtnClick(event: { target: cc.Node }) {
        Tools.clickManager(event.target);
        this.close();
        UIMgr.showUI(CommonSkin.LoadOnDemand.Register).then((res) => {
            res.onClose((ret) => {
                if (ret) {
                    UIMgr.showUI(CommonSkin.LoadOnDemand.PasswordLogin);
                }
            });
        });
    }

    protected onResetPassBtnClick(event: { target: cc.Node }) {
        UIMgr.showUI(CommonSkin.LoadOnDemand.CommonRetrievePassword);
    }

    private verifyData(accountStr: string, pwdStr: string) {
        if (!CommonUserLoginCheckUtil.checkAccount(accountStr)) {
            return false;
        }
        if (!CommonUserLoginCheckUtil.checkPassword(pwdStr)) {
            return false;
        }
        return true;
    }
}
