import { ProjectInfo } from "../../../../../main/core/conf/ProjectInfo";
import CacheObserver from "../../../../../main/core/utils/class/CacheObserver";
import {
    setInstance,
    Instance,
} from "../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { UniverseProto } from "../universe/proto/UniverseProto";
import { PlatformUtil } from "../../../../../main/core/platform/PlatformUtil";
import UIMgr from "../Component/base/UIMgr";
import { SceneControl } from "../SceneControl";
import { SceneType } from "../SceneType";
import { H5Util } from "../../../../../main/core/platform/H5Util";
import { BrowserUtil } from "../utils/BrowserUtil";
import { lodash } from "../../../../../main/core/utils/NpmExport";
import { PlatformType } from "../../../../../main/core/platform/PlatformType";

type TData = ProtoObject<UniverseProto.UpdateNewNT> & {
    isUpdate?: boolean;
    stop?: boolean;
};

@setInstance
export default class CommonUpdateNewObserver extends CacheObserver<TData> {
    protected getCacheKey(): string {
        return "_update_new";
    }

    protected onNotice(newValue: TData) {
        super.onNotice(newValue);
    }

    private compareVersion(curent: string, target: string) {
        /***
         *  local - remote
         *  0
         *  0
         */

        let vL = curent.split(".");
        let vR = target.split(".");
        for (let i = 0; i < vL.length; ++i) {
            let a = parseInt(vL[i]);
            let b = parseInt(vR[i] || "0");
            if (a === b) {
                continue;
            } else {
                return a - b;
            }
        }
        if (vR.length > vL.length) {
            return -1;
        } else {
            return 0;
        }
    }

    public check() {
        let data = this.get();
        this.tryUpdate(data);
        this.saveUrl();
    }

    public saveUrl() {
        let data = this.get();
        const gameVersion = ProjectInfo.getInstance().GAME_VERSION;
        if (gameVersion !== data?.VersionName) {
            return;
        }
        let url = data.UpdateUrl;
        if (!url) {
            return;
        }
        this.set({});
        PlatformUtil.getInstance().saveGameUrl(url);
    }

    public tryUpdate(data: TData) {
        if (PlatformUtil.getInstance().type == PlatformType.H5) {
            return;
        }
        if (lodash.isEmpty(data)) {
            return;
        }
        const gameVersion = ProjectInfo.getInstance().GAME_VERSION;
        if (this.compareVersion(gameVersion, data?.VersionName ?? "0.0.0") >= 0) {
            CC_DEBUG && cc.log("[CommonUpdateNewObserver] current is up to date");
            return;
        }
        CC_DEBUG && cc.log("[CommonUpdateNewObserver] start update");
        this.modify(data);
        switch (data.UpdateType) {
            case UniverseProto.UpdateTypeEnu.FORCE_HOT:
            case UniverseProto.UpdateTypeEnu.NORMAL_HOT: {
                if (SceneControl.getInstance().scene !== SceneType.None) {
                    if (UniverseProto.UpdateTypeEnu.FORCE_HOT == data.UpdateType) {
                        if (SceneControl.getInstance().scene === SceneType.Hall) {
                            UIMgr.showAlert({
                                contentText: data.Content,
                                contextAlign: cc.macro.TextAlignment.CENTER,
                                style: UIMgr.ALERT_STYLE.POSITIVE,
                            }).onClose((result) => {
                                if (result) {
                                    this.onUpdateH5(data);
                                }
                            });
                        }
                    }
                } else {
                    this.onUpdateH5(data);
                }
                break;
            }
        }
    }

    onUpdateH5(data: TData) {
        Instance(CommonUpdateNewObserver).modify({
            isUpdate: true,
        });

        H5Util.getInstance().loadUrl(data.UpdateUrl);
    }
}
