import CommonSkin from "../../../conf/skin/CommonSkin";
import { ServiceErrorUtils } from "../../service_error/ServiceErrorUtils";
import { CommonProto } from "../proto/CommonProto";
import BaseSocketAttachment from "./BaseSocketAttachment";

enum MsgType {
    Ping = 0,
    Pong = 99,
    ErrorST = 100,
}
export type TMsgData<T extends { toObject: any }> = ReturnType<Pick<T, "toObject">["toObject"]>;
export default class ErrorStSocketAttachment extends BaseSocketAttachment {
    public getMessageCtor(msgType: MsgType) {
        return {
            100: CommonProto.ErrorST,
        }[msgType];
    }

    public onMessage(data: Socket.TMessageObject<number, any>): void {
        switch (data.msgId) {
            case MsgType.ErrorST: {
                try {
                    this.onErrorST(data.msgData);
                } catch {
                    //
                }
                break;
            }
        }
    }

    private onErrorST(data: TMsgData<CommonProto.ErrorST>) {
        switch (data.Status) {
            case CommonProto.STATUS.OK: {
                //0~99, 
                break;
            }
            case CommonProto.STATUS.UNKNOWN_ERROR: {
                // 
                break;
            }
            case CommonProto.STATUS.PARAM_ERROR: {
                //
                break;
            }
            // 
            case CommonProto.STATUS.NOT_AUTH:
            //
            case CommonProto.STATUS.ROOM_IS_FULL:
            //
            case CommonProto.STATUS.SERVER_CLOSED:
            //
            case CommonProto.STATUS.DUP_LOGIN:
            // token
            case CommonProto.STATUS.TOKEN_INVALID:
            //
            case CommonProto.STATUS.GOLDLOCK_CONFLICT:
            // 
            case CommonProto.STATUS.CUBE_ROOM_NOT_EXIST: {
                this.close();
            }
            //
            case CommonProto.STATUS.CUBE_BANKER_FULL:
            // 
            case CommonProto.STATUS.CUBE_BET_OVER_LIMIT:
            //
            case CommonProto.STATUS.CUBE_OFF_BANKER_FOR_CREDIT:
            //
            case CommonProto.STATUS.CUBE_OFF_BANKER_FOR_COUNT:
            // 
            case CommonProto.STATUS.CUBE_BET_INSUFFICIENT:
            // 
            case CommonProto.STATUS.CUBE_BET_RED_LIMIT_LOW:
            // 
            case CommonProto.STATUS.CUBE_BET_RED_LIMIT_HIGH:
            //TODO @stone  
            case CommonProto.STATUS.CUBE_BANKER_INSUFFICIENT:

            //  
            case CommonProto.STATUS.CUBE_BET_OVER_CREDIT: {
                ServiceErrorUtils.applySocketError(data);
                break;
            }
            case CommonProto.STATUS.REDIRECT: {
                // 
                if (data.Msg) {
                    this.close();
                    this.reconnect({
                        host: data.Msg,
                    });
                } else {
                    this.close();
                    ServiceErrorUtils.applySocketError(data);
                }
                break;
            }
            // 
            case CommonProto.STATUS.INSUFFICIENT:
            //
            case CommonProto.STATUS.CUBE_CHIP_ERROR:
            //
            case CommonProto.STATUS.IDLE_TIMEOUT:
            //
            case CommonProto.STATUS.PLAY_RULE: {
                //
                break;
            }
        }
        this.error(data);
    }
}
