import { UserManager } from "../../../../../../main/core/user/UserManager";
import { ApiProto } from "../../service/api/ApiProto";
import ApiMgr from "../../service/api/ApiMgr";
import { CommonResultType } from "../define/CommonResultType";

export namespace CommonUserInfoUtil {
    export function updateUserInfo(
        login = false
    ): Promise<CommonUser.TLoginResultWithType<ProtoObject<ApiProto.UserInfo>>> {
        return new Promise((resolve, reject) => {
            ApiMgr.getInstance()
                .getUserInfoRequest(login, {
                    success: (userInfo: ApiProto.UserInfo) => {
                        const info = userInfo.toObject();
                        UserManager.getInstance().initUser(info);
                        resolve({
                            result: CommonResultType.Success,
                            content: info,
                        });
                    },
                    error: (err: RespHeader) => {
                        resolve({
                            result: CommonResultType.Failed,
                            content: err,
                        });
                    },
                })
                .catch((res) => {
                    if (CC_DEBUG) console.warn("updateUserInfo failed", res);
                });
        });
    }

    export function updateUserBalance(): Promise<
        CommonUser.TLoginResultWithType<ProtoObject<ApiProto.UserBalance>>
    > {
        return new Promise((resolve, reject) => {
            ApiMgr.getInstance().getUserBalanceRequest({
                success: (userInfo: ApiProto.UserBalance) => {
                    const info = userInfo.toObject();
                    UserManager.getInstance().initUser(info);
                    resolve({
                        result: CommonResultType.Success,
                        content: info,
                    });
                },
                error: (err: RespHeader) => {
                    resolve({
                        result: CommonResultType.Failed,
                        content: err,
                    });
                },
            });
        });
    }
}
