import { UserManager } from "../../../../../../main/core/user/UserManager";
import { CurrencyUtil } from "../../utils/CurrencyUtil";

const { ccclass, property, menu } = cc._decorator;

/**
 * 
 */
@ccclass
@menu("common/UserAmountComp")
export default class UserAmountComp extends cc.Component {
    protected onEnable(): void {
        UserManager.getInstance().info.listenChange(this.updateLabel, this);
    }
    protected onDisable(): void {
        UserManager.getInstance().info.unListenChange(this.updateLabel, this);
    }

    updateLabel() {
        const tmpNum = parseInt(UserManager.getInstance().coin * 100 + "") / 100;
        const amount = CurrencyUtil.formatMoney(tmpNum);
        this.getComponent(cc.Label).string = amount;
    }
}
