import BaseSocketAttachment from "./BaseSocketAttachment";
import { DateUtils } from "../../../../../../../main/core/utils/DateUtils";
import CommonSocket from "../../../../../../../main/core/ws/CommonSocket";
import HeartCheck from "../heartCheck/HeartCheck";
import { CommonProto } from "../proto/CommonProto";
import { GameType } from "../../../../../../../main/core/conf/GameType";
import { lodash } from "../../../../../../../main/core/utils/NpmExport";
import BackHeartCheck from "../heartCheck/BackHeartCheck";
import FrontHeartCheck from "../heartCheck/FrontHeartCheck";
import { CommonSocketUtil } from "../CommonSocketUtil";

enum MsgType {
    Ping = 0,
    Pong = 99,
    ErrorST = 100,
}
export type TMsgData<T extends { toObject: any }> = ReturnType<Pick<T, "toObject">["toObject"]>;
export default class CommonSocketAttachment extends BaseSocketAttachment {
    private _heartCheck: HeartCheck = null;

    private createHearkCheck() {
        this._heartCheck?.close();
        this._heartCheck = new (lodash.get(
            {
                [GameType.UNIVERSE]: BackHeartCheck,
            },
            this.options.gameId,
            FrontHeartCheck
        ) as typeof HeartCheck)();
        return this._heartCheck;
    }

    public getMessageCtor(msgType: MsgType) {
        return {
            0: CommonProto.Ping,
            99: CommonProto.Pong,
        }[msgType];
    }

    public onInit(option: Socket.SocketOption): void {
        this.options.gameId = option.gameId;
    }

    private ping() {
        let nowTimer = new Date().getTime();
        let ping = new CommonProto.Ping({
            Timestamp: nowTimer,
        });
        this.send(0, ping);
    }

    public onOpen(): void {
        this.createHearkCheck()
            .init({
                gameId: this.options.gameId,
                connect: () => {
                    this.reconnect();
                },
                ping: () => {
                    this.ping();
                },
            })
            .play();
    }

    public onBeforeUserClose(): void {
        this._heartCheck?.close();
        this._heartCheck = null;
    }

    public onClose(): void {
        /*  this._heartCheck?.close();
        this._heartCheck = null; */
    }

    public onMessage(data: Socket.TMessageObject<number, any>): void {
        this._heartCheck?.updateLastTime();
        switch (data.msgId) {
            case MsgType.Pong: {
                this.onPong(data.msgData);
                break;
            }
        }
    }

    private onPong(data: TMsgData<CommonProto.Pong>) {
        DateUtils.getInstance().checkTimestampDiff(data.Timestamp);
    }
}
