const { ccclass, property } = cc._decorator;

const content_MAX_WIDTH = 960;

@ccclass
export default class ToastItem extends cc.Component {
    @property(cc.Label)
    lab_content: cc.Label = null;

    public setContent(content: string) {
        this.lab_content.string = content;
        this.lab_content["_forceUpdateRenderData"](true);
        this.scheduleOnce(() => {
            let layout = this.node.getComponent(cc.Layout);
            if (this.lab_content.node.width < content_MAX_WIDTH) {
                this.lab_content.overflow = cc.Label.Overflow.NONE;
                layout.type = cc.Layout.Type.HORIZONTAL;
            } else {
                this.lab_content.overflow = cc.Label.Overflow.RESIZE_HEIGHT;
                this.lab_content.node.width = content_MAX_WIDTH;
                layout.updateLayout();
                layout.type = cc.Layout.Type.VERTICAL;
                layout.paddingTop = 10;
                layout.paddingBottom = 20;
            }
        });
    }
}
