import {
    setInstance,
    Instance,
} from "../../../../../main/core/utils/decorators/InstanceClassDecorator";
import ValueObserver from "../../../../../main/core/utils/class/ValueObserver";

interface ISwitchData {
    /** */
    withdraw_guide?: boolean;
    /** */
    broadcast?: boolean;
    /** */
    game_replay?: boolean;
    /** */
    mini_game_window?: boolean;
    /** */
    push?: boolean;
    /** */
    activity_center?: boolean;
}

@setInstance
export default class CommonNetworkSwitchObserver extends ValueObserver<ISwitchData> {
    protected onInit(): ISwitchData {
        return {};
    }

    public getSwitch(key: keyof ISwitchData, defaultValue: boolean = false): boolean {
        return this.get(key, defaultValue);
    }
}
