import ValueObserver from "../../../../../../main/core/utils/class/ValueObserver";
import { ApiProto } from "../../service/api/ApiProto";
import HttpObserver from "../../../../../../main/core/utils/class/HttpObserver";
import ApiMgr from "../../service/api/ApiMgr";
import { Injector } from "../../../../../../main/core/utils/decorators/Injector";
import CommonLanguage from "../../conf/lang/CommonLanguage";

type IData = ProtoObject<ApiProto.KycData>

@Injector.Injectable()
export default class CommonKycInfoObserver extends HttpObserver<IData> {
    protected onQuery(): Promise<IData> {
        return ApiMgr.getInstance()
            .getKyc()
            .then((data) => {
                data.phone = data.phone.replace(new RegExp(`^${CommonLanguage.phoneHead}`), "");
                return data;
            });
    }
    protected onInit(data?: IData): IData {
        return {};
    }
}
