import PopUpViewBase from "../../../../../../../main/core/popUpViewUtils/PopUpViewBase";
import UIMgr from "../UIMgr";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Alert extends PopUpViewBase {
    @property({ type: cc.Node, tooltip: "" })
    btn_positive: cc.Node = null;

    @property({ type: cc.Node, tooltip: "" })
    btn_negative: cc.Node = null;

    @property({ type: cc.Node, tooltip: "" })
    btn_close: cc.Node = null;

    @property({ type: cc.Label, tooltip: "lab" })
    lab_positive: cc.Label = null;

    @property({ type: cc.Label, tooltip: "lab" })
    lab_negative: cc.Label = null;

    @property({ type: cc.Label, tooltip: "lab" })
    lab_title: cc.Label = null;

    @property({ type: cc.RichText, tooltip: "lab" })
    lab_content: cc.RichText = null;

    private _negativeCallFunc: () => void = null;
    private _positiveCallFunc: () => void = null;

    onLoad(): void {
        this.btn_positive.on(cc.Node.EventType.TOUCH_END, this.onPositive, this);
        this.btn_negative.on(cc.Node.EventType.TOUCH_END, this.onNegative, this);
        this.btn_close.on(cc.Node.EventType.TOUCH_END, this.onClickClose, this);
    }

    public init(parm: IAlertParm) {
        if (parm.style != undefined) {
            switch (parm.style) {
                case UIMgr.ALERT_STYLE.NEGATIVE_POSITIVE:
                    this.btn_negative.active = true;
                    this.btn_positive.active = true;
                    this.btn_close.active = false;
                    break;
                case UIMgr.ALERT_STYLE.POSITIVE:
                    this.btn_negative.active = false;
                    this.btn_positive.active = true;
                    this.btn_close.active = false;
                    break;
                case UIMgr.ALERT_STYLE.NEGATIVE_POSITIVE_CLOSE:
                    this.btn_negative.active = true;
                    this.btn_positive.active = true;
                    this.btn_close.active = true;
                    break;
                default:
                    this.btn_negative.active = true;
                    this.btn_positive.active = true;
                    this.btn_close.active = false;
                    break;
            }
        } else {
            this.btn_negative.active = true;
            this.btn_positive.active = true;
        }

        if (parm.negativeBtnText) {
            this.lab_negative.string = parm.negativeBtnText;
        }

        if (parm.positiveBtnText) {
            this.lab_positive.string = parm.positiveBtnText;
        }

        if (parm.titleText) {
            this.lab_title.string = parm.titleText;
        }

        if (parm.negativeCallFunc) {
            this._negativeCallFunc = parm.negativeCallFunc;
        }

        if (parm.positiveCallFunc) {
            this._positiveCallFunc = parm.positiveCallFunc;
        }

        this.lab_content.horizontalAlign = parm?.contextAlign ?? cc.macro.TextAlignment.LEFT;

        if (parm.countdown) {
            this.startCountdown(parm);
        } else {
            this.lab_content.string = parm.contentText || "";
        }
    }

    private startCountdown(parm: IAlertParm) {
        this.lab_content.string = parm.contentText.format(parm.countdown);

        let countDown = parm.countdown;
        if (countDown && countDown > 0) {
            this.schedule(
                () => {
                    countDown--;
                    this.lab_content.string = parm.contentText.format(countDown);
                    if (countDown <= 0) {
                        parm.countdownEndCallFunc && parm.countdownEndCallFunc();
                        this.close();
                    }
                },
                1,
                parm.countdown - 1
            );
        }
    }

    private onClickClose() {
        this.close(undefined);
    }

    private onNegative() {
        this._negativeCallFunc && this._negativeCallFunc();
        this.close(false);
    }

    private onPositive() {
        this._positiveCallFunc && this._positiveCallFunc();
        this.close(true);
    }
}
