import { GameType } from "../../../../../../main/core/conf/GameType";
import Lighter from "../../../../../../main/core/services/Lighthouse/Lighter";
import { EVENT_CODE } from "../../../../../../main/core/services/Lighthouse/proto/EventCode";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { Tools } from "../../../../../../main/core/utils/Tools";
import { BundleCommonMgr } from "../../bd/BundleCommonMgr";
import UIMgr from "../../Component/base/UIMgr";
import CommonLanguage from "../../conf/lang/CommonLanguage";
import { NewGameInfo } from "../../NewGameInfo";
import { SceneControl } from "../../SceneControl";
import { TimerUtil } from "../../utils/TimerUtil";
import CommonSocketAttachment from "./attachment/CommonSocketAttachment";
import DebugLogAttachment from "./attachment/DebugLogAttachment";
import ErrorStSocketAttachment from "./attachment/ErrorStSocketAttachment";
import BaseSocket from "./BaseSocket";
import { CommonProto } from "./proto/CommonProto";
import { SocketInternalEvent } from "./SocketInternalEvent";

interface SocketInitOptions<MsgId extends number, Data>
    extends Socket.SocketInitOptions<MsgId, Data> {
    onError?: (err: ProtoObject<CommonProto.ErrorST>) => void;
}

enum SocketState {
    None,
    Connect,
    Open,
    Disconnect,
}
const TIMEOUT_CONNECT: number = 5000;
export default abstract class BaseSocketMgr<MsgType extends number>
    extends BaseSocket<MsgType>
    implements Socket.IScoketProto {
    private attachments: Socket.ISocketAttachment[] = [];

    private _listener: cc.EventTarget = new cc.EventTarget();

    private _configs: Socket.ISocketConfigs = {};

    private options: SocketInitOptions<MsgType, any> = {};

    private state: SocketState = SocketState.None;

    private checkTimeKey = Symbol("socket_connect_check");

    private retryConnect = 0;

    public constructor() {
        super();
        setTimeout(() => {
            //
            this.initListen();
            this.initAttachments();
        });
    }

    private initHost() {
        /*  if (!this.host) {
            this.host = CommonDepend.NewGameInfo.getGameWsServer(this.gameId);
        } */
        if (this.options?.gameId) {
            this.gameId = this.options?.gameId;
        }
    }

    private initListen() {
        this._listener.on(SocketInternalEvent.SEND_MESSAGE, this.send, this);
        this._listener.on(SocketInternalEvent.RECONNECT, this.reconnect, this);
        this._listener.on(SocketInternalEvent.CLOSE, this.close, this);
        this._listener.on(SocketInternalEvent.ERROR, this.error, this);
    }

    private initAttachments() {
        this.applyAttachment(CommonSocketAttachment);
        this.applyAttachment(ErrorStSocketAttachment);
        this.applyAttachment(DebugLogAttachment);
        this.applyConfig(this._configs);
        this.applyConfig({
            log: {
                blacklist: [99, 0],
            },
        });
    }

    public applyAttachment(attachmentCtor: Socket.ISocketAttachmentConstructor) {
        const attachment = new attachmentCtor({
            gameId: this.gameId,
            listener: this._listener,
        });
        this.attachments.push(attachment);
        return attachment;
    }

    public applyConfig(configs: Socket.ISocketConfigs) {
        this._configs = lodash.merge(this._configs, configs);
        Object.keys(configs).forEach((tag) => {
            let config = this._configs[tag];
            for (let key in config) {
                let item = config[key];
                let targetConfig = config[key];
                if (targetConfig instanceof Array) {
                    targetConfig = [...(item || []), ...targetConfig];
                } else if (targetConfig instanceof Object) {
                    targetConfig = { ...(item || {}), ...targetConfig };
                }
                this._configs[tag][key] = targetConfig;
            }

            this.attachments
                .filter((v) => v.tag == tag)
                .forEach((attachment) => {
                    attachment.applyConfig(this._configs[tag]);
                });
        });
    }

    private enableGameEvent() {
        cc.game.on(
            cc.game.EVENT_HIDE,
            () => {
                if (this.state != SocketState.None) {
                    this.stopCheckConnect();
                    this.attachments.forEach((attach) => {
                        attach.onBeforeUserClose?.();
                    });
                    this.disconnect();
                }
            },
            this
        );
        cc.game.on(
            cc.game.EVENT_SHOW,
            () => {
                if (this.state != SocketState.None) {
                    this.stopCheckConnect();
                    this.attachments.forEach((attach) => {
                        attach.onBeforeUserClose?.();
                    });
                    this.reconnect();
                }
            },
            this
        );
    }
    private enableNetworkEvent() {
        window.addEventListener("offline", this.NetworkOffline);
        window.addEventListener("online", this.NetworkOnline);
    }
    private disableGameEvent() {
        cc.game.targetOff(this);
    }
    private disableNetworkEvent() {
        window.removeEventListener("offline", this.NetworkOffline);
        window.removeEventListener("online", this.NetworkOnline);
    }

    private NetworkOnline = this.onNetworkOnline.bind(this);
    private onNetworkOnline() {
        if (this.state != SocketState.None) {
            this.reconnect();
        }
    }

    private NetworkOffline = this.onNetworkOffline.bind(this);
    private onNetworkOffline() {
        if (this.state == SocketState.None) {
            return;
        }
        this.stopCheckConnect();
        this.attachments.forEach((attach) => {
            attach.onBeforeUserClose?.();
        });
        this.disconnect();

        if (this.gameId == GameType.UNIVERSE) {
            return;
        }
        UIMgr.showAlert({
            contentText: CommonLanguage.networkClose,
            style: UIMgr.ALERT_STYLE.POSITIVE,
        }).onClose(() => {
            UIMgr.applyLoadingUi(SceneControl.getInstance().launchScene(GameType.GAME_HALL));
        });
    }

    public init(options: SocketInitOptions<MsgType, any>) {
        this.options = options;
        this.retryConnect = 0;
        this.attachments.forEach((attachment) =>
            attachment.onInit({
                gameId: this.gameId,
            })
        );
        TimerUtil.steup(this.checkTimeKey, this.checkConnect.bind(this), TIMEOUT_CONNECT);
    }

    public stopCheckConnect() {
        if (TimerUtil.stop(this.checkTimeKey)) {
        }
    }
    public checkConnect(count: number, option: { type?: string }) {
        if (this.getState() == WebSocket.OPEN) {
            this.stopCheckConnect();
            return;
        }
        if (!this.options) {
            this.stopCheckConnect();
            return;
        }

        if (this.retryConnect >= 1) {
            if (this.retryConnect >= 2) {
                NewGameInfo.getInstance().switchHost(this.curentHost);
            }
        }

        if (this.gameId == GameType.UNIVERSE) {
        } else {
            if (this.retryConnect > NewGameInfo.getInstance().hostLenght() + 1) {
                UIMgr.hideLoadingToast();
                UIMgr.showAlert({
                    contentText: CommonLanguage.networkError,
                    style: UIMgr.ALERT_STYLE.POSITIVE,
                });
                this.attachments.forEach((attach) => {
                    attach.onBeforeUserClose?.();
                });
                this.disconnect();
                this.stopCheckConnect();
                return;
            } else {
                if (this.retryConnect) {
                    UIMgr.showLoadingToast(
                        Tools.formatString(CommonLanguage.retryConnect, {
                            count:
                                CommonLanguage?.count?.[this.retryConnect - 1] ?? this.retryConnect,
                        })
                    );
                }
            }
        }
        if (count >= 1) {
            Lighter.getInstance().trackEnd(
                this.getSocket()?.getHost?.(),
                EVENT_CODE.RequestTimeout,
                this.getSocket()?.getHost?.()
            );
        }
        this.state = SocketState.Connect;
        this.initHost();
        this.disconnect();
        //this.enableGameEvent();
        this.enableNetworkEvent();
        this.connect();
        this.retryConnect++;
        return option;
    }

    public reconnect(options: SocketInitOptions<MsgType, any> = {}) {
        this.retryConnect = 1;
        options = this.options = lodash.merge(this.options, options);
        this.initHost();
        this.close();
        this.options = options;
        this.attachments.forEach((attach) => {
            attach.onBeforeUserClose?.();
        });
        this.stopCheckConnect();

        TimerUtil.steup(this.checkTimeKey, this.checkConnect.bind(this), TIMEOUT_CONNECT, {
            type: "reconnect",
        });
    }

    public close() {
        this.options = null;
        this.state = SocketState.None;
        CC_DEBUG && cc.log(`[socket] gameId:${this.gameId} close`);
        this.attachments.forEach((attach) => {
            attach.onBeforeUserClose?.();
        });
        this.stopCheckConnect();
        this.disconnect();
        this.disableGameEvent();
        this.disableNetworkEvent();
    }

    protected onOpen() { }
    protected abstract onAuthReq();

    // 
    protected onError(error: ProtoObject<CommonProto.ErrorST>) {
        /*  switch (error.Status) {
            // 
            case CommonProto.STATUS.INSUFFICIENT:
            //
            case CommonProto.STATUS.CUBE_CHIP_ERROR:
            //
            case CommonProto.STATUS.IDLE_TIMEOUT:
            //
            case CommonProto.STATUS.PLAY_RULE: {
                //
                break;
            }
        } */
    }

    protected onClose() { }

    private error(error: ProtoObject<CommonProto.ErrorST>) {
        this.onError(error);
        this.options?.onError?.(error);
    }

    protected onMessage(data: Socket.TMessageObject<MsgType, any>) {
        this.attachments.forEach((attach) => {
            attach.onMessageHandle?.(data);
        });
        this.options?.onMessage?.(data);
    }

    protected getDecodeClass(msgType: MsgType) {
        //proto 
        let ctor = this.getMessageCtor(msgType);
        if (!ctor) {
            for (let attachment of this.attachments) {
                //
                if ((ctor = attachment.getMessageCtor(msgType))) {
                    break;
                }
            }
        }
        return ctor;
    }

    protected onOpenHandler() {
        this.attachments.forEach((attach) => {
            attach.onOpen?.();
        });
        this.onOpen();
        this.options?.onOpen?.();
        this.onAuthReq();
        if (this.gameId !== GameType.UNIVERSE) {
            UIMgr.destroy(BundleCommonMgr.getInstance().getBundleNameDescForAlert());
            if (this.retryConnect > 1) {
                UIMgr.showLoadingToast(CommonLanguage.connectSuccess, 0.5);
            } else {
                UIMgr.hideLoadingToast();
            }
        }
        this.state = SocketState.Open;
    }

    protected onCloseHandler() {
        this.attachments.forEach((attach) => attach.onClose?.());
        this.onClose();
        this.options?.onClose?.();
        /*   if (this.state == SocketState.Open) {
            this.reconnect();
        } */
    }

    public abstract getMessageCtor(msgType: MsgType);

    protected send(msgType: MsgType, msgData: Socket.IMessageClass) {
        this.attachments.forEach((attachment) => {
            attachment.onSendMessage(msgType, msgData);
        });
        super.send(msgType, msgData);
    }
}
