import { GameType } from "../../../../../../main/core/conf/GameType";
import CommonRuleLanguage from "./lang/CommonRuleLanguage";
import RuleItem from "./RuleItem";
import { BundleMgr } from "../../../../../../main/core/bd/BundleMgr";
import CommonSkin from "../../conf/skin/CommonSkin";

const { ccclass, property } = cc._decorator;

@ccclass
export default class RuleContent extends cc.Component {
    @property(cc.Prefab)
    ruleItem: cc.Prefab = null;

    @property(cc.Prefab)
    ruleItemRich: cc.Prefab = null;

    protected onLoad(): void { }

    public loadContent(gameID): void {
        let self = this;
        let content = null;
        gameID = Number(gameID);
        switch (gameID) {
            case GameType.BIGBATTLE:
                content = CommonRuleLanguage.rule.brcBigbattle;
                break;
            case GameType.REDBLACK:
                content = CommonRuleLanguage.rule.brcRedBlack;
                break;
            case GameType.SHARK:
                content = CommonRuleLanguage.rule.brcShark;
                break;
            case GameType.BIG_SMALL:
                content = CommonRuleLanguage.rule.bigsmall;
                break;
            default: {
                const desc = CommonSkin.LoadOnDemand.GameRule?.[gameID] ?? [];
                this.loadSpriteRule(desc);
                break;
            }
        }
        self.fillContent(content, gameID);
    }

    private async loadSpriteRule(desc: IResDescribe[]) {
        try {
            for (let item of desc) {
                let spriteFrame = await BundleMgr.getInstance().loadResFromBundle<cc.SpriteFrame>(
                    item
                );
                if (!spriteFrame) {
                    return;
                }
                const page = new cc.Node("page");
                let width = spriteFrame.getRect().width;
                this.node.scale = this.node.width / width;
                page.addComponent(cc.Sprite).spriteFrame = spriteFrame;
                this.node.addChild(page);
            }
        } catch (e) { }
    }

    private fillContent(ruleContent, gameID): void {
        let self = this;
        if (!ruleContent) {
            return;
        }
        self.node.removeAllChildren();
        for (let key in ruleContent) {
            let title = ruleContent[key].title;
            let content = ruleContent[key].content;
            let contentRich = ruleContent[key].contentRich;
            let contentDeploy = ruleContent[key].contentDeploy;

            if (contentRich) {
                if (cc.isValid(self.ruleItemRich)) {
                    let ruleItemNode = cc.instantiate(self.ruleItemRich);
                    if (ruleItemNode) {
                        self.node.addChild(ruleItemNode);
                        if (ruleItemNode.getComponent(RuleItem)) {
                            ruleItemNode
                                .getComponent(RuleItem)
                                .initView(title, content, contentRich, contentDeploy, gameID);
                        }
                    }
                }
            } else {
                if (cc.isValid(self.ruleItem)) {
                    let ruleItemNode = cc.instantiate(self.ruleItem);
                    if (ruleItemNode) {
                        self.node.addChild(ruleItemNode);
                        if (ruleItemNode.getComponent(RuleItem)) {
                            ruleItemNode
                                .getComponent(RuleItem)
                                .initView(title, content, contentRich, contentDeploy, gameID);
                        }
                    }
                }
            }
        }
    }

    public updateContent(ruleContent): void {
        this.fillContent(ruleContent, null);
    }
}
