import CommonSkin from "../../conf/skin/CommonSkin";
import RuleContent from "./RuleContent";
import PopUpViewBase from "../../../../../../main/core/popUpViewUtils/PopUpViewBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Rule extends PopUpViewBase {
    @property(cc.ScrollView)
    mainScrollView: cc.ScrollView = null;

    @property(cc.Node)
    content: cc.Node = null;

    public init(gameID): void {
        UIDepend.PrefabManager
            .loadPrefabBundle(CommonSkin.LoadOnDemand.gameRuleContent)
            .then((prefab) => {
                if (!prefab) {
                    return;
                }
                let node = cc.instantiate(prefab);
                node.getComponent(RuleContent).loadContent(gameID);
                this.content.addChild(node);
            });
    }

    private onClickClose(): void {
        CommonDepend.AudioManager.playEffect(CommonSkin.LoadOnDemand.btnEffect);

        this.close();
    }
}
