import { GameType } from "../../../../../main/core/conf/GameType";
import { LocalStorageMgr } from "../../../../../main/core/storage/LocalStorageMgr";
import LocalStorageObserver from "../../../../../main/core/utils/class/LocalStorageObserver";
import {
    setInstance,
    Instance,
} from "../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { lodash } from "../../../../../main/core/utils/NpmExport";
import { UniverseProto } from "../universe/proto/UniverseProto";
import HostListLogic from "./hostList/HostListLogic";
import CommonHostListSwitchCountObserver from "./CommonHostListSwitchCountObserver";

const GAME_WS_ADDR = "wss://{game_code}.{host}";
const GAME_HTTP_ADDR = "https://{game_code}.{host}";

@setInstance
export default class CommonHostListObserver extends LocalStorageObserver<string[]> {
    private _wsHost: HostListLogic = new HostListLogic(GAME_WS_ADDR, {
        [GameType.UNIVERSE]: "un",
    });
    private _httpHost: HostListLogic = new HostListLogic(GAME_HTTP_ADDR, {
        [GameType.GAME_HALL]: "lb",
    });

    private serviceHostList: string[] = CommonDepend.ProjectInfo.DEFAULT_HOST;

    protected read(): string[] {
        return LocalStorageMgr.getInstance().readHostList();
    }
    protected save(data: string[]) {
        LocalStorageMgr.getInstance().saveHostList(data);
    }

    public next(host: string) {
        let list = this.get();
        if (!RegExp(`\.?${lodash.first(list)}$`).test(host)) {
            return;
        }
        let currentHost = list.shift();
        if (!this.serviceHostList.includes(currentHost)) {
            if (!Instance(CommonHostListSwitchCountObserver).addSwitchCount(currentHost)) {
                return;
            }
        }

        list.push(currentHost);

        //this.modify();
    }

    public loadServerHostList(hostList: string[]) {
        this.serviceHostList.splice(1, this.serviceHostList.length);
        this.serviceHostList.push(...hostList);
    }

    public loadGameInfo(game_list: ProtoObject<UniverseProto.FeatureConfResp.GameInfo>[]) {
        const gameCode = lodash
            .chain(game_list)
            .map((v) => [v.game_id, v.game_code])
            .fromPairs()
            .value();

        this._wsHost.mergeGameCode(gameCode);
        this._httpHost.mergeGameCode(gameCode);
    }

    public getWsServer(gameType: GameType) {
        return this._wsHost.getHosts(this.get(), gameType);
    }

    public getHttpServer(gameType: GameType) {
        return this._httpHost.getHosts(this.get(), gameType);
    }
}
