const { ccclass, property } = cc._decorator;

@ccclass
export default class TrucoRule extends cc.Component {
    @property({ type: cc.Node, displayName: "" })
    close: cc.Node = null;

    @property({ type: cc.Node, displayName: "" })
    left: cc.Node = null;

    @property({ type: cc.ScrollView, displayName: "" })
    private right: cc.ScrollView = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {
        this.left.children.forEach((node, index) => {
            node.on("click", () => {
                this.right.stopAutoScroll();
                this.right.scrollToPercentVertical(1 - (1 / (this.left.childrenCount - 1)) * index);
            });
        });
        this.close.on("click", () => {
            this.onCloseClick();
        });
    }

    private onCloseClick(): void {
        this.node.emit("onCloseClick");
    }

    // update (dt) {}
}
