import { PokerCardEnum } from "./enums/PokerCardEnum";
import PokerCardMgr from "./PokerCardMgr";
import PokerResMgr from "./PokerResMgr";

const { ccclass, property } = cc._decorator;

const SuitShift = 20;
const RankMask = (1 << SuitShift) - 1;
enum Rank {
    RA = 1 << 0,
    R2 = 1 << 1,
    R3 = 1 << 2,
    R4 = 1 << 3,
    R5 = 1 << 4,
    R6 = 1 << 5,
    R7 = 1 << 6,
    R8 = 1 << 7,
    R9 = 1 << 8,
    R10 = 1 << 9,
    RJ = 1 << 10,
    RQ = 1 << 11,
    RK = 1 << 12,
    RX = 1 << 13, // small joker
    RD = 1 << 14, // big joker
}
enum Suit {
    Spade = 1 << (0 + SuitShift),
    Heart = 1 << (1 + SuitShift),
    Club = 1 << (2 + SuitShift),
    Diamond = 1 << (3 + SuitShift),
}
export class Card {
    private code: number;

    constructor(code: number) {
        this.code = code;
    }

    suit(): Suit {
        return (this.code >> SuitShift) << SuitShift;
    }

    rank(): Rank {
        return this.code & RankMask;
    }

    getFlower(): number {
        let temp = this.suit();
        switch (temp) {
            case Suit.Spade:
                return 1;
            case Suit.Heart:
                return 2;
            case Suit.Club:
                return 3;
            case Suit.Diamond:
                return 4;
            default:
                return 0;
        }
    }

    getNumber(): number {
        let temp = this.rank();
        switch (temp) {
            case Rank.RA:
                return 1;
            case Rank.R2:
                return 2;
            case Rank.R3:
                return 3;
            case Rank.R4:
                return 4;
            case Rank.R5:
                return 5;
            case Rank.R6:
                return 6;
            case Rank.R7:
                return 7;
            case Rank.R8:
                return 8;
            case Rank.R9:
                return 9;
            case Rank.R10:
                return 10;
            case Rank.RJ:
                return 11;
            case Rank.RQ:
                return 12;
            case Rank.RK:
                return 13;
            default:
                return 0;
        }
    }
}

@ccclass
export default class Poker extends cc.Component {
    @property(cc.Sprite)
    number: cc.Sprite = null;

    @property(cc.Sprite)
    flowerColor: cc.Sprite = null;

    @property(cc.Sprite)
    icon: cc.Sprite = null;

    @property(cc.Node)
    mask: cc.Node = null;

    _isShowingCarFlower: boolean = false;

    private getCardId(number: number, flower: number): number {
        return (4 - flower) * 13 + number - 1;
    }

    public async init(number: number, flower: number, showMask?: boolean) {
        showMask = false;
        let cardId: PokerCardEnum = this.getCardId(number, flower);
        let cardFrames = await PokerCardMgr.getCardFrames(cardId);
        if (cardFrames) {
            if (this.icon) {
                this.icon.spriteFrame = cardFrames.icon;
                this.icon.node.opacity = 255;
            }
            if (this.flowerColor) {
                this.flowerColor.spriteFrame = cardFrames.flowerColor;
            }
            this.number.spriteFrame = cardFrames.number;
            this.node.getComponent(cc.Sprite).spriteFrame = await PokerResMgr.getPokerBg();
            if (showMask) {
                this.showMask();
            } else {
                this.hideMask();
            }
        }
    }

    public showMask() {
        this.mask.active = true;
    }

    public hideMask() {
        this.mask.active = false;
    }

    public async showCardback() {
        this.node.getComponent(cc.Sprite).spriteFrame = await PokerResMgr.getPokerNormal();
        this.hideCardFlower();
        this.hideMask();
    }

    public async showCardGiveup() {
        this.node.getComponent(cc.Sprite).spriteFrame = await PokerResMgr.getPokerGray();
        this.hideCardFlower();
    }

    public hideCardFlower(): void {
        this._isShowingCarFlower = false;
        if (this.number) {
            this.number.spriteFrame = null;
        }
        if (this.flowerColor) {
            this.flowerColor.spriteFrame = null;
        }
        if (this.icon) {
            this.icon.spriteFrame = null;
            this.icon.node.opacity = 0;
        }
    }

    public isShowingCarFlower() {
        return this._isShowingCarFlower;
    }
}
