import ToastItem from "./ToastItem";

const { ccclass, property } = cc._decorator;

const ACTION_TIME = 0.24;

@ccclass
export default class ToastUI extends cc.Component {
    @property(cc.Node)
    content: cc.Node = null;

    @property(cc.Prefab)
    toastItemPrefab: cc.Prefab = null;

    private toastList: cc.Node[] = [];

    public addToast(msg: string) {
        let toastItem = cc.instantiate(this.toastItemPrefab);
        toastItem.position = cc.v3(0, 0, 0);
        toastItem.opacity = 0;
        this.content.addChild(toastItem);

        toastItem.stopAllActions();
        toastItem.targetOff(this);
        toastItem.on(cc.Node.EventType.SIZE_CHANGED, this.toastItemSizeChanged, this);
        toastItem.getComponent(ToastItem).setContent(msg);
        this.toastList.unshift(toastItem);

        cc.tween<cc.Node>(toastItem)
            .to(ACTION_TIME, { opacity: 255 })
            .delay(1.5)
            .to(ACTION_TIME, { opacity: 0 })
            .call(() => {
                this.deleteToast(toastItem);
            })
            .start();
    }

    private toastItemSizeChanged() {
        let satrtPos = cc.v3(0, 0, 0);
        let allHeight = 0;
        for (let i = 0; i < this.toastList.length; i++) {
            const node = this.toastList[i];
            let toPos = satrtPos.add(cc.v3(0, allHeight + node.height / 2, 0));
            allHeight = allHeight + node.height;
            cc.tween<cc.Node>(node).to(ACTION_TIME, { position: toPos }).start();
        }
    }

    private deleteToast(toastItem: cc.Node) {
        for (let i = 0; i < this.toastList.length; i++) {
            const element = this.toastList[i];
            if (toastItem.uuid === element.uuid) {
                this.toastList.splice(i, 1);
                break;
            }
        }
        toastItem.destroy();
    }
}
