import { BundleMgr } from "../../../../main/core/bd/BundleMgr";
import { H5Util } from "../../../../main/core/platform/H5Util";
import { PlatformType } from "../../../../main/core/platform/PlatformType";
import Listener from "../../../../main/core/utils/Listener";
import CommonEventName from "./CommonEventName";

/**
 *  task:1329
 * []
 * SafariH5H5
 * []
 * 
 */
class SafariDeskIcon {
    private _platform = null;
    constructor() {
        this._platform = H5Util.getInstance().platform;
        if (this._platform !== PlatformType.H5) return;
        if (!H5Util.getInstance().isSafari) return;
        Listener.once(CommonEventName.SET_SAFARI_DESK_ICON, this.iconHandler, this);
    }

    iconHandler() {
        //@ts-ignore
        import("CommonSkin").then((obj) => {
            let res = obj.default.Priority.safari_desk_icon;
            let iconSp = BundleMgr.getInstance().getResFromBundle(res);
            let nativeURL = iconSp._texture.nativeUrl;
            //set document
            //let host = window.location.host;
            let link = document.createElement("link");
            link.rel = "apple-touch-icon";
            link.href = nativeURL;
            document.head.appendChild(link);
        });
    }
}
const newSafariDeskIcon = new SafariDeskIcon();
window["sdiref"] = newSafariDeskIcon;
export default newSafariDeskIcon;
