import PopUpViewBase from "../../../../../main/core/popUpViewUtils/PopUpViewBase";
import { lodash } from "../../../../../main/core/utils/NpmExport";
import EnumActive from "../../../../../main/core/ui/component/EnumActive";
import { AudioManager } from "../../../../../main/core/audio/AudioManager";
import CommonLanguage from "../conf/lang/CommonLanguage";
import CommonStorageMgr from "../Storage/CommonStorageMgr";
import { Instance } from "../../../../../main/core/utils/decorators/InstanceClassDecorator";
import CommonNetworkSwitchObserver from "../obs/CommonNetworkSwitchObserver";
import Listener from "../../../../../main/core/utils/Listener";
import CommonEventName from "../CommonEventName";
import { PlayButtonSoundUtils } from "../../../../../main/core/utils/playButtonSoundUtils/PlayButtonSoundUtils";
import { H5Page } from "../h5/H5Page";
import MultLangLabel from "../../../../../main/core/ui/component/mult_lang/MultLangLabel";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CommonUserSetting extends PopUpViewBase {
    @property({ type: MultLangLabel, displayName: "" })
    versionLb: MultLangLabel = null;
    protected start(): void {
        this._content.getChildByName("layout").children.forEach((item) => {
            let func = this[`on${lodash.capitalize(item.name)}BtnClicked`];
            let initFunc = this[`get${lodash.capitalize(item.name)}State`];
            let toggle = item.getChildByName("toggle");
            let toggleComp = toggle.getComponent(EnumActive);
            PlayButtonSoundUtils.getInstance().addPlaySoundComp(toggle);
            toggleComp.show4Name(initFunc?.() ?? "off");
            toggle.addComponent(cc.Button);
            toggle.on(
                "click",
                () => {
                    toggleComp.show4Name(toggleComp.currentName == "off" ? "on" : "off");
                    func?.(toggleComp.currentName);
                },
                this
            );
        });

        this.versionLb.object = {
            value: CommonDepend.ProjectInfo.GAME_VERSION_V,
        };
        cc.find("content/layout/activity", this.node).active = Instance(
            CommonNetworkSwitchObserver
        ).getSwitch("activity_center");
    }

    protected getMusicState() {
        return AudioManager.getInstance().isMusicOpen() ? "on" : "off";
    }

    protected onMusicBtnClicked(state: "off" | "on"): void {
        AudioManager.getInstance().setMusicOpen(state == "on" ? true : false);
    }

    protected getEffectState() {
        return AudioManager.getInstance().isEffectOpen() ? "on" : "off";
    }
    protected onEffectBtnClicked(state: "off" | "on"): void {
        AudioManager.getInstance().setEffectOpen(state == "on" ? true : false);
    }

    protected getActivityState() {
        return CommonStorageMgr.getInstance().readActivityReminderShowState() ? "on" : "off";
    }

    protected onActivityBtnClicked(state: "off" | "on"): void {
        CommonStorageMgr.getInstance().saveActivityReminderShowState(state);
    }

    private onClickCloseBtn() {
        let showState: boolean = CommonStorageMgr.getInstance().readActivityReminderShowState();
        Listener.emit(CommonEventName.ACTIVITY_REMINDER_SHOW_STATE, showState);
        this.close();
    }

    onPrivacyClicked() {
        H5Page.onPrivacyClicked();
    }
    onTermsClicked() {
        H5Page.onTermsClicked();
    }
    onRefundClicked() {
        H5Page.onRefundClicked();
    }
    onAboutClicked() {
        H5Page.onAboutClicked();
    }
}
