    export let UILayer = cc.Enum({
        /**  */
        NORMAL: 100,

        /**  */
        POP: 200,

        /** Alert */
        ALERT: 300,

        /** Toast */
        TOAST: cc.macro.MAX_ZINDEX,

        /**  */
        LOADING: cc.macro.MAX_ZINDEX - 1,
    });

    let showLoadingCount = 0;

    /**
     * toast
     */
    export function showToast(msg: string) {
        let node = toastUI;
        if (!cc.isValid(node)) {
            let toastUIPrefab: cc.Prefab = BundleMgr.getInstance().getResFromBundle(
                BundleCommonMgr.getInstance().getBundleNameDescForToastUI()
            );
            let canvas: cc.Node = CommonDepend.CocosUtils.getSceneCanvas();
            node = cc.instantiate(toastUIPrefab);
            canvas.addChild(node, UILayer.TOAST);
            toastUI = node;
        }
        node.getComponent(ToastUI).addToast(msg);
    }

    export function applyLoadingUi<T = any>(
        backCatch: boolean
    ): (
        target: Object | Promise<T> | boolean,
        propertyKey?: string | symbol,
        descriptor?: TypedPropertyDescriptor<(...args: any[]) => Promise<T>>
    ) => TypedPropertyDescriptor<(...args: any[]) => Promise<T>>;
    export function applyLoadingUi<T>(target: Promise<T>): Promise<T>;

    export function applyLoadingUi<T>(
        target: Object,
        propertyKey: string | symbol,
        descriptor: TypedPropertyDescriptor<(...args: any[]) => Promise<T>>
    );
    export function applyLoadingUi<T>(
        target: Object | Promise<T> | boolean,
        propertyKey?: string | symbol,
        descriptor?: TypedPropertyDescriptor<(...args: any[]) => Promise<T>>
    ):
        | TypedPropertyDescriptor<(...args: any[]) => Promise<T>>
        | Promise<T>
        | ((
              target: Object | Promise<T> | boolean,
              propertyKey?: string | symbol,
              descriptor?: TypedPropertyDescriptor<(...args: any[]) => Promise<T>>
          ) => TypedPropertyDescriptor<(...args: any[]) => Promise<T>>) {
        const backCatch = typeof target === "boolean" ? target : false;
        const loading = (promise: Promise<T>): Promise<T> => {
            return new Promise<T>((resolve, reject) => {
                showLoading();
                if (!promise) {
                    hideLoading();
                } else {
                    import("../../service/service_error/ServiceErrorUtils")
                        .then((ret) => {
                            return ret.ServiceErrorUtils.catchError(promise);
                        })
                        .then((res) => {
                            resolve(res);
                        })
                        .catch((res) => {
                            backCatch && reject(res);
                        })
                        .then((ret) => {
                            hideLoading();
                        });
                }
            });
        };

        function decorator(
            target: Object,
            propertyKey: string | symbol,
            descriptor: TypedPropertyDescriptor<(...args: any[]) => Promise<T>>
        ) {
            let oldMethod = descriptor.value;
            descriptor.value = function (...args: any[]): Promise<T> {
                let result = oldMethod.apply(this, args);
                return loading(result);
            };
            return descriptor as TypedPropertyDescriptor<(...args: any[]) => Promise<T>>;
        }

        if (typeof target === "boolean") {
            return decorator;
        }
        if (target instanceof Promise) {
            return loading(target);
        } else {
            return decorator(target, propertyKey, descriptor);
        }
    }

    /**
     * loadingUI
     */
    export function showLoading(delayShow?: number) {
        showLoadingCount++;
        let node = loadingUI;
        if (!cc.isValid(node)) {
            let loadingUIPrefab: cc.Prefab = BundleMgr.getInstance().getResFromBundle(
                BundleCommonMgr.getInstance().getBundleNameDescForLoadingUI()
            );

            let canvas: cc.Node = CommonDepend.CocosUtils.getSceneCanvas();
            node = cc.instantiate(loadingUIPrefab);
            canvas.addChild(node, UILayer.LOADING);
            loadingUI = node;
        }
        loadingUI.active = true;
        if (delayShow) {
            loadingUI.getComponent(LoadingUI).init(delayShow);
        }
    }

    /**
     * loadingUI
     */
    export function hideLoading() {
        if (--showLoadingCount <= 0) {
            showLoadingCount = 0;
            if (cc.isValid(loadingUI)) {
                loadingUI.active = false;
            }
        }
    }

    export function applyAlert(option: IAlertParm) {
        return function <T>(
            target: Object,
            propertyKey: string | symbol,
            descriptor: TypedPropertyDescriptor<(...args: any[]) => any>
        ) {
            const oldValue = descriptor.value;

            descriptor.value = function (...args: any[]) {
                showAlert(
                    lodash.merge(
                        {
                            contextAlign: cc.macro.TextAlignment.CENTER,
                        } as IAlertParm,
                        option
                    )
                ).onClose((result) => {
                    oldValue.call(this, ...args, result);
                });
            };
        };
    }

    /**
     * Alert
     */
    export function showAlert(parm: IAlertParm) {
        let sceneName = cc.director.getScene().name;
        if(sceneName == "LoginScene") return
        return showUISync(BundleCommonMgr.getInstance().getBundleNameDescForAlert(), {
            args: [parm],
            zIndex: UILayer.ALERT,
            new: true,
        });
    }

    /**
     * Alert 
     */
    export function showAlertAwait(parm: IAlertParm) {
        return showUI(BundleCommonMgr.getInstance().getBundleNameDescForAlert(), {
            args: [parm],
            zIndex: UILayer.ALERT,
            new: true,
        });
    }

    export function hideLoadingToast() {
        const desc = CommonSkin.Priority.loadingToast;
        close(desc);
    }

    export function showLoadingToast(message: string, delayTime: number = -1) {
        const timeID = "showLoadingToast";
        const desc = CommonSkin.Priority.loadingToast;
        TimerUtil.stop(timeID);
        if (delayTime > 0) {
            TimerUtil.steupOnce(
                timeID,
                () => {
                    hideLoadingToast();
                },
                delayTime * 1000
            );
        }

        return showUISync(desc, {
            args: [message],
            zIndex: UILayer.LOADING,
        });
    }

    export function preload(resDesc: IResDescribe, options?: PopupUtil.IShowOptions) {
        return PopupUtil.preload(resDesc, options);
    }

    /**
     * UI
     */
    export function showUI(resDesc: IResDescribe, options?: PopupUtil.IShowOptions) {
        return PopupUtil.show(resDesc, options);
    }

    export function showUISync(resDesc: IResDescribe, options?: PopupUtil.IShowOptions) {
        return PopupUtil.showSync(resDesc, options);
    }

    export function close(resDesc: IResDescribe) {
        return PopupUtil.close(resDesc);
    }

    export function destroy(resDesc: IResDescribe) {
        return PopupUtil.destroy(resDesc);
    }

