import Flavor from "../../../../../main/core/conf/Flavor";
import { MoneyUtil } from "../../../../../main/core/utils/MoneyUtil";
import { CurrencyBRL } from "./currency/CurrencyBRL";
import { CurrencyINR } from "./currency/CurrencyINR";
import { CurrencyVND } from "./currency/CurrencyVND";

export namespace CurrencyUtil {
    /**
     * 
     * 999,999M/999K
     * 999999M32
     * 9999999.99Cr
     * @param money
     */
    export function formatMoney(money: number, digit?: number): string {
        if (money < 0) return money.toString();
        money = MoneyUtil.getInstance().liToYuan(money);
        if (Flavor.Skin.isBall()) {
            return CurrencyVND.formatMoney(money);
        } else if (Flavor.Skin.isWorldCup()) {
            return CurrencyBRL.formatMoney(money, digit);
        } else if (Flavor.Skin.isLight()) {
            return CurrencyINR.formatMoney(money);
        }
    }

    /**
     * 
     * formatMoney
     * formatMoney
     * 9999999.99Cr9999K
     * @param money
     */
    export function formatChipMoney(money: number, digit?: number): string {
        if (money < 0) return "0";
        money = MoneyUtil.getInstance().liToYuan(money);
        if (Flavor.Skin.isBall()) {
            return CurrencyVND.formatMoney(money);
        } else if (Flavor.Skin.isWorldCup()) {
            return CurrencyBRL.formatMoney(money, digit);
        } else if (Flavor.Skin.isLight()) {
            return CurrencyINR.formatChipMoney(money);
        }
    }

    /**
     *  1:1
     * 999,999M/999K
     * 999999M32
     * 9999999.99Cr
     * @param money
     */
    export function formatGold(money: number, digit?: number): string {
        if (money < 0) return "0";
        if (Flavor.Skin.isBall()) {
            return CurrencyVND.formatMoney(money);
        } else if (Flavor.Skin.isWorldCup()) {
            return CurrencyBRL.formatMoney(money, digit);
        } else if (Flavor.Skin.isLight()) {
            return CurrencyINR.formatMoney(money);
        }
    }
}
