import { Tools } from "../../../../../main/core/utils/Tools";

export namespace FormatTestUtil {
    export const MIN_ACCOUNT_LENGTH = 6;
    export const MAX_ACCOUNT_LENGTH = 15;

    export const MIN_CODE_LENGTH = 3;
    export const MAX_CODE_LENGTH = 8;

    /**  */
    export const MIN_NAME_LEN = 2;
    /**  */
    export const MAX_NAME_LEN = 14;
    /** CPF */
    export const CPF_LEN = 11;
    /**  */
    export const MIN_PHONE_LEN = 10;
    /**  */
    export const MAX_PHONE_LEN = 11;
    /** CNPJ */
    export const CNPJ_LEN = 14;
    /** EVP */
    export const EVP_LEN = 32;
    /**  */
    export const MAX_EMAIL_LEN = 120;
    /**  */
    export const MIN_BANK_LEN = 9;
    /**  */
    export const MAX_BANK_LEN = 20;
    /**  ()*/
    export const INDIA_PHONE_LEN = 10;
    /** Address*/
    export const MIN_INDIA_ADDRESS_LEN = 2;
    /** Address*/
    export const MAX_INDIA_ADDRESS_LEN = 150;

    /**  */
    export function testPhoneNumber(phone: string) {
        let isPhoneValid = false;
        let phoneReg = /^[0-9]+$/;
        isPhoneValid = phoneReg.test(phone);
        if (!isPhoneValid) {
            return false;
        }
        return true;
    }

    /**  */
    export function testValidCode(code: string) {
        let isCodeValid = false;
        let codeReg = /^[A-Za-z0-9]+$/;
        isCodeValid =
            codeReg.test(code) && code.length >= MIN_CODE_LENGTH && code.length <= MAX_CODE_LENGTH;
        if (!isCodeValid) {
            return false;
        }
        return true;
    }

    /**  a-z 0-9 A-Z _ - . */
    export function containInvaildSymbol(str: string) {
        if (str && str.length >= 6 && str.length <= 20) {
            let strReg = /^[a-z0-9A-Z_\-\.]{6,20}$/;
            if (!strReg.test(str)) {
                return true;
            }
        }
        return false;
    }

    /**  */
    export function containNumberAndLetter(str: string) {
        let strReg = /^[A-Za-z0-9]+$/;
        if (strReg.test(str)) {
            return true;
        }
        return false;
    }

    export function testNickNameLength(nickName: string) {
        return Tools.isLimit(
            nickName.replace(/[^\x00-\xff]/g, "**").length,
            MIN_NAME_LEN,
            MAX_NAME_LEN
        );
    }

    export function testEmail(email: string) {
        let emailReg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]{2,})$/;
        if (emailReg.test(email)) {
            return true;
        }
        return false;
    }

    export function testUpi(upi: string) {
        let emailReg = /^[A-Za-z0-9]+@[a-zA-Z0-9]+$/;
        if (emailReg.test(upi)) {
            return true;
        }
        return false;
    }

    export function testIfsc(ifsc: string) {
        let emailReg = /^[A-Za-z0-9]{4}0[a-zA-Z0-9]{6}$/;
        if (emailReg.test(ifsc)) {
            return true;
        }
        return false;
    }
}
