const { ccclass, property } = cc._decorator;

@ccclass
export default class LoadingUI extends cc.Component {
    public init(delayShow?: number) {
        if (delayShow) {
            cc.tween<cc.Node>(this.node)
                .set({ opacity: 0 })
                .delay(delayShow)
                .to(0.2, { opacity: 255 })
                .start();
        }
    }

    onDisable() {
        this.node.stopAllActions();
        this.node.opacity = 255;
    }
}
