import { CurrencyUtil } from "../../utils/CurrencyUtil";

const { ccclass, property } = cc._decorator;

@ccclass
export default class ChipFrame extends cc.Component {
    @property(cc.Node)
    animNode: cc.Node = null;

    @property(cc.Node)
    numItemListArr: Array<cc.Node> = [];

    @property(cc.SpriteAtlas)
    atlas: cc.SpriteAtlas = null;

    _status = true;
    _chipIndex = -1;
    _value = 0;

    initValueWithInfoAction(value, index): void {
        let chipFrame = this.getChipFrame(index + 1);
        this.node.getComponent(cc.Sprite).spriteFrame = chipFrame;
        let chipFrameName = chipFrame.name;
        let arr = chipFrameName.split("");
        let chipsNumType = arr[arr.length - 1] + "_";
        this.initChipNumFrame(value, chipsNumType);
        this._chipIndex = index;
        this._value = value;
    }

    initNumItem(): void {
        for (let i = 0, len = this.numItemListArr.length; i < len; i++) {
            let numItem = this.numItemListArr[i];
            if (numItem && numItem.active) {
                numItem.active = false;
            }
        }
    }

    showSelectedAnimAction(): void {
        if (cc.isValid(this.animNode)) {
            this.animNode.opacity = 255;
        }
    }

    hideSelectedAnimAction(): void {
        if (cc.isValid(this.animNode)) {
            this.animNode.opacity = 0;
        }
    }

    showDisableAction(): void {
        if (cc.isValid(this.node)) {
            let chipFrame = this.getChipFrame(7);
            this.node.getComponent(cc.Sprite).spriteFrame = chipFrame;
        }
        this.initChipNumFrame(this._value, "7_");
        if (cc.isValid(this.node)) {
            this.hideSelectedAnimAction();
            this._status = false;
        }
    }

    showNomalAction(): void {
        if (cc.isValid(this.node)) {
            let chipFrame = this.getChipFrame(this._chipIndex + 1);
            this.node.getComponent(cc.Sprite).spriteFrame = chipFrame;
            let chipFrameName = chipFrame.name;
            let arr = chipFrameName.split("");
            let chipsNumType = arr[arr.length - 1] + "_";
            this.initChipNumFrame(this._value, chipsNumType);
        }
        if (cc.isValid(this.node)) {
            this._status = true;
        }
    }

    private getChipFrame(index: number): cc.SpriteFrame {
        let chipFrame = this.atlas?.getSpriteFrame?.(`common_chip_0${index}`);
        return chipFrame;
    }

    setEnable(enable): void {
        if (cc.isValid(this.node) && !enable) {
            this.node.getComponent(cc.Sprite).spriteFrame = this.getChipFrame(7);
        }
        if (this.node && this.node.getComponent(cc.Button)) {
            this.node.getComponent(cc.Button).interactable = enable;
        }
    }

    initChipNumFrame(value, chipsNumType): void {
        if (value != null && value > 0) {
            this.initNumItem();
            let numSpriteFrame: cc.SpriteFrame = null;
            value = CurrencyUtil.formatChipMoney(value).replace("Cr", "C");
            for (let i = 0, len = value.length; i < len; i++) {
                let numCode = value[i];
                if (chipsNumType) {
                    numSpriteFrame = this.atlas?.getSpriteFrame?.(chipsNumType + numCode);
                } else {
                    numSpriteFrame = this.atlas?.getSpriteFrame?.(numCode);
                }
                let numItem = this.numItemListArr[i];
                if (numItem && numSpriteFrame) {
                    numItem.active = true;
                    numItem.getComponent(cc.Sprite).spriteFrame = numSpriteFrame;
                }
            }
        }
    }
}
