const {ccclass, property} = cc._decorator;

import DynamicMounNodeUtil from "./DynamicMounNodeUtil";

@ccclass
export default class DynamicMultipleMounNodeComponent extends cc.Component {

    @property(cc.Node)
    spSkeletonNode: cc.Node = null;

    _spSkeleton: sp.Skeleton = null;

    _DynamicMounNodeUtil = null;

    _mounNode = null;
    _boneName = null;

    _hasSafetyCorrective = false;
    /**
     * MounNodeObj
     */
    _mounArr = [];
    /**
     * key: eventName
     * value: [
     *      MounNodeObj
     * ]
     */
    _targetMultipleMounMap = {};

    _playingAnim = false;

    onLoad() {
        let self = this;
        self._spSkeleton = self.spSkeletonNode.getComponent(sp.Skeleton);
        self._playingAnim = false;
        self._DynamicMounNodeUtil = new DynamicMounNodeUtil(self.spSkeletonNode);
    }

    /**
     *
     * @param skeletonAnimName
     * @param targetMultipleMounMap = {
     *     eventName: [{
     *         mountNode: xx,
     *         boneName: xx
     *     }]
     * }
     * @param completeListener
     */
    startSkeletonAnimation(skeletonAnimName, targetMultipleMounMap, addEventListener, completeListener, delayTimeAfterComplete?) {
        let self = this;
        self.startSkeletonAnimationLogic(skeletonAnimName, targetMultipleMounMap, addEventListener, completeListener, delayTimeAfterComplete, false);
    }

    startSkeletonAnimationLoop(skeletonAnimName, targetMultipleMounMap, addEventListener, completeListener, delayTimeAfterComplete?) {
        let self = this;
        self.startSkeletonAnimationLogic(skeletonAnimName, targetMultipleMounMap, addEventListener, completeListener, delayTimeAfterComplete, true);
    }

    /**
     * addEventListener
     * noMount + firstTriggerFun
     * 
     *
     *
     * @param skeletonAnimName
     * @param targetMultipleMounMap
     * @param addEventListener
     * @param completeListener
     * @param delayTimeAfterComplete
     * @param isLoop
     */
    startSkeletonAnimationLogic(skeletonAnimName, targetMultipleMounMap, addEventListener, completeListener, delayTimeAfterComplete?, isLoop?) {
        let self = this;
        self._hasSafetyCorrective = false;
        self._mounArr = [];
        self._targetMultipleMounMap = targetMultipleMounMap;
        self._playingAnim = true;
        self.spSkeletonNode.active = true;
        self.spSkeletonNode.opacity = 255;

        let obj: MounNodeObj[] = self._targetMultipleMounMap["dwc_pack_default"];
        if (obj && (obj.length > 0)) {
            for (let index = 0; index < obj.length; index++) {
                self._mounArr.push(obj[index]);
            }
        }

        self._spSkeleton.timeScale = 1;
        self._spSkeleton.setEventListener(function (trackEntry, eventData) {
            if (self._targetMultipleMounMap) {
                if (eventData) {
                    let dataInfo = eventData.data;
                    if (dataInfo) {
                        let trackName = dataInfo.name;
                        if (trackName) {
                            let obj: MounNodeObj[] = self._targetMultipleMounMap[trackName];
                            if (obj && (obj.length > 0)) {
                                for (let index = 0; index < obj.length; index++) {
                                    if (obj[index].noMount) {
                                        if (obj[index].firstTriggerFun) {
                                            obj[index].firstTriggerFun();
                                        }
                                    } else {
                                        self._mounArr.push(obj[index]);
                                    }
                                }
                            }

                            if (addEventListener) {
                                addEventListener(trackName);
                            }
                        }
                    }
                }
            }
        });

        self._spSkeleton.setAnimation(0, skeletonAnimName, isLoop);
        self._spSkeleton.setCompleteListener(function () {
            if (delayTimeAfterComplete) {
                let timer = setTimeout(function () {
                    self._playingAnim = false;
                    clearTimeout(timer);
                }, delayTimeAfterComplete);
            } else {
                self._playingAnim = false;
            }

            self.safetyCorrective();

            if (completeListener) {
                completeListener();
            }
        });
    }

    getSpineSkeleton() {
        return this._spSkeleton;
    }

    update(dt) {
        let self = this;
        if (!self._playingAnim) {
            return;
        }
        if (self._DynamicMounNodeUtil) {
            self._DynamicMounNodeUtil.updateMultipleMountNode(self._mounArr);
        }
    }

    private safetyCorrective() {
        let self = this;

        if (!self._mounArr) {
            return;
        }

        if (self._hasSafetyCorrective) {
            return;
        }
        self._hasSafetyCorrective = true;

        let findNotTrigger = self.findNotTrigger();
        let findAllTargetArr = self.findAllTargetMount();

        if ((findAllTargetArr.allTargetArr.length == self._mounArr.length)
            && (!findNotTrigger)) {
            return;
        }

        if (self._DynamicMounNodeUtil) {
            self._DynamicMounNodeUtil.updateMultipleMountNode(findAllTargetArr.allTargetArr);
            for (let fun of findAllTargetArr.directTriggerFunArr) {
                if (fun) {
                    fun();
                }
            }
        }
    }

    private findNotTrigger() {
        let self = this;
        let findNotTrigger = false;
        for (let index = 0; index < self._mounArr.length; index++) {
            let mountObj = self._mounArr[index];
            if (mountObj && mountObj.mountNode && mountObj.boneName) {
                if (mountObj.isFirstTrigger != undefined) {
                    if (mountObj.isFirstTrigger == true) {
                        findNotTrigger = true;
                    }
                    mountObj.isFirstTrigger = true;
                }
            }
        }

        return findNotTrigger;
    }

    private findAllTargetMount() {
        let self = this;

        let allTargetArr = [];
        let directTriggerFunArr = [];
        for (let key in self._targetMultipleMounMap) {
            let obj: MounNodeObj[] = self._targetMultipleMounMap[key];
            if (obj && (obj.length > 0)) {
                for (let index = 0; index < obj.length; index++) {
                    if (obj[index].noMount) {
                        if (obj[index].firstTriggerFun) {
                            directTriggerFunArr.push(obj[index].firstTriggerFun);
                        }
                    } else {
                        allTargetArr.push(obj[index]);
                    }
                }
            }
        }

        return {
            allTargetArr: allTargetArr,
            directTriggerFunArr: directTriggerFunArr
        };
    }

}

interface MounNodeObj {
    mountNode: cc.Node,
    boneName: string,
    noMount: false, 
    isFirstTrigger: true,
    firstTriggerFun: Function
}
