import { setInstance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { CommonProto } from "../ws/proto/CommonProto";
import BaseServiceErrorCenter from "./base/BaseServiceErrorCenter";
import { ServiceErrorCoinLock } from "./handler/ServiceErrorCoinLock";
import { ServiceErrorShowToast } from "./handler/ServiceErrorShowToast";
import { ServiceErrorToHallScene } from "./handler/ServiceErrorToHallScene";
import { ServiceErrorToLoginScene } from "./handler/ServiceErrorToLoginScene";
import ServiceErrorTryOpenTopup from "./handler/ServiceErrorTryOpenTopup";

@setInstance
export default class SocketServiceErrorCenter extends BaseServiceErrorCenter {
    protected onInitialize(): void {
        //
        //
        // 
        //
        // 
        //  
        // 
        // 
        this.applyDeflaultHandler(new ServiceErrorShowToast());

        /**  */
        this.applyHandler(CommonProto.STATUS.NOT_AUTH, new ServiceErrorToLoginScene());
        /** token */
        this.applyHandler(CommonProto.STATUS.TOKEN_INVALID, new ServiceErrorToLoginScene());
        /** */
        this.applyHandler(CommonProto.STATUS.DUP_LOGIN, new ServiceErrorToHallScene());
        /**  */
        this.applyHandler(CommonProto.STATUS.SERVER_CLOSED, new ServiceErrorToHallScene());
        /**  */
        this.applyHandler(CommonProto.STATUS.REDIRECT, new ServiceErrorToHallScene());
        //
        this.applyHandler(CommonProto.STATUS.ROOM_IS_FULL, new ServiceErrorToHallScene());
        //
        this.applyHandler(CommonProto.STATUS.CUBE_ROOM_NOT_EXIST, new ServiceErrorToHallScene());
        //
        this.applyHandler(CommonProto.STATUS.GOLDLOCK_CONFLICT, new ServiceErrorCoinLock());
        // 
        //this.applyHandler(CommonProto.STATUS.INSUFFICIENT, new ServiceErrorTryOpenTopup());
    }
}
