export default class DynamicMounNodeUtil {

    _spSkeleton = null;

    constructor(spSkeletonNode) {
        let self = this;

        if (!spSkeletonNode) {
        }

        let spine = spSkeletonNode.getComponent(sp.Skeleton);

        if (!spine) {
            return;
        }

        self._spSkeleton = spine;
    }

    /**
     * Node
     * NodeSpine/Spine
     *
     * @param mountNode
     * @param boneName
     */
    public updateMountNode(mountNode, boneName, updateScale = true, callFun = null) {
        let self = this;
        if (!self._spSkeleton) {
            return;
        }

        if (!cc.isValid(self._spSkeleton) || !cc.isValid(self._spSkeleton.node.parent)) {
            return;
        }

        if (!mountNode) {
            return;
        }

        if (!boneName) {
            return;
        }

        if (!cc.isValid(mountNode) || !cc.isValid(mountNode.parent)) {
            return;
        }

        let currBone = self._spSkeleton.findBone(boneName);

        if (!currBone) {
            return;
        }

        let worldPos = self._spSkeleton.node.parent.convertToWorldSpaceAR(self._spSkeleton.node.getPosition());
        let pos = mountNode.parent.convertToNodeSpaceAR(worldPos);

        mountNode.setPosition(pos.x + currBone.worldX, pos.y + currBone.worldY);
        if (updateScale) {
            mountNode.scaleX = currBone.scaleX;
            mountNode.scaleY = currBone.scaleY;
        }
        if (callFun) {
            callFun();
        }
    }

    /**
     *
     * @param mountArr = {
     *     mountNode : xx,
     *     boneName : xx,
     * }
     */
    public updateMultipleMountNode(mounArr) {
        let self = this;
        if (!mounArr) {
            return;
        }

        for (let index = 0; index < mounArr.length; index++) {
            let mountObj = mounArr[index];
            if (mountObj && mountObj.mountNode && mountObj.boneName) {
                let callFun = null;
                if (mountObj.isFirstTrigger) {
                    mountObj.isFirstTrigger = false;
                    callFun = mountObj.firstTriggerFun;
                }
                let updateScale = true;
                if (mountObj.updateScale != undefined) {
                    updateScale = mountObj.updateScale;
                }
                self.updateMountNode(mountObj.mountNode, mountObj.boneName, updateScale, callFun);
            }
        }
    }

}
