import { SpriteManager } from "../../../../../../main/core/ui/base/SpriteManager";
import CommonLanguage from "../../../../../plaza/common/Script/conf/lang/CommonLanguage";
import BrcGraphicsMgr from "../../Component/brc/BrcGraphicsMgr";
import CommonSkin from "../../conf/skin/CommonSkin";

const { ccclass, property } = cc._decorator;

@ccclass
export default class BrcRedBlackDLTrendFrame extends cc.Component {
    @property(cc.Node)
    protected dlTrend: cc.Node = null;

    @property(cc.ScrollView)
    private mainScrollView: cc.ScrollView = null;

    @property(cc.Node)
    private mainContent: cc.Node = null;

    @property(cc.Node)
    private dlBall: cc.Node = null;

    @property({ type: cc.Node, tooltip: "Loading" })
    private loadingNode: cc.Node = null;

    @property
    _itemSize: number = 20;
    @property
    _maxLine: number = 6;
    @property
    _maxItem: number = 32;
    @property
    _trendData: any[] = [];
    @property
    _dataInfoArr: any[] = [];
    @property
    _maxLineMinusArr: any[] = [];

    private _clickCallback = null;
    private _miniItem: number = 32;

    protected onLoad(): void {
        let self = this;
        if (cc.isValid(self.mainContent)) {
            self.mainContent.on("touchend", function () {
                if (typeof self._clickCallback == "function") {
                    self._clickCallback();
                }
            });
        }
    }

    /**
     * 
     * @param row
     * @param column
     * @param itemSize
     * @param clickCallback
     */
    public init(row: number, column: number, itemSize: number, clickCallback?: Function): void {
        let self = this;
        self._maxLine = row;
        self._maxItem = self._miniItem = column;
        self._itemSize = itemSize;
        self._clickCallback = clickCallback;
    }

    protected drawTrendTableView(): void {
        let self = this;
        if (cc.isValid(self.dlTrend)) {
            let graphics = self.dlTrend.getComponent(cc.Graphics);
            BrcGraphicsMgr.drawTrendTableView(
                graphics,
                self._itemSize,
                self._maxLine,
                self._maxItem
            );
        }
    }

    public initWithTrendData(trendData: any[], isUpdate: boolean): void {
        let self = this;
        self._trendData = trendData || [];
        if (self._trendData && self._trendData.length > 0) {
            let trendDataLength = self._trendData.length;
            let dataColumn = trendDataLength + 1;
            if (dataColumn < self._miniItem) {
                dataColumn = self._miniItem;
            }
            self._maxItem = dataColumn;
            let maxCount = self._trendData[0].length;
            let maxIndex = 0;
            let bigCountIndexArr = [];
            for (let i = 1; i < self._trendData.length; i++) {
                let trendDatas = self._trendData[i];
                let jLen = trendDatas.length;
                if (jLen >= maxCount) {
                    maxIndex = i;
                    maxCount = jLen;
                }
                if (jLen > self._maxLine) {
                    bigCountIndexArr.push({
                        index: i,
                        count: jLen,
                    });
                }
            }
            let diffValue = maxCount - self._maxLine;
            if (diffValue > 0) {
                let maxItem = maxIndex + 1 + diffValue;
                if (maxItem >= self._maxItem) {
                    self._maxItem = maxItem;
                }
            }
            let maxWidth = self._maxItem * self._itemSize;
            if (cc.isValid(self.mainContent)) {
                self.mainContent.width = maxWidth;
            }
            if (cc.isValid(self.dlTrend)) {
                self.dlTrend.width = maxWidth;
                self.dlTrend.x = 0;
                self.dlTrend.y = 0;
            }
            for (let i = 0, len = bigCountIndexArr.length; i < len; i++) {
                let bigCountInfo = bigCountIndexArr[i];
                let index = bigCountInfo.index;
                let count = bigCountInfo.count;
                let diffValue = count - self._maxLine;
                for (let j = 0; j < diffValue; j++) {
                    self._maxLineMinusArr.push(index + j + 1);
                }
            }
        }
        self.drawTrendTableView();
        self.disposeTrendDataAction();

        if (isUpdate) {
            self.updateUIWithTrendData();
        } else {
            self.drawCircleWithData();
        }
        self.scheduleOnce(function () {
            if (cc.isValid(self.mainScrollView)) {
                self.mainScrollView.scrollToRight(0);
            }
        }, 0);

        let isLoading = !self._trendData || self._trendData.length == 0;
        self.updateViewState(isLoading);
    }

    private disposeTrendDataAction() {
        let self = this;
        self._dataInfoArr = [];
        if (!self._trendData) {
            return;
        }
        /**
         * 61
         * 6
         * */
        for (let i = 0, len = self._trendData.length; i < len; i++) {
            let trendDatas = self._trendData[i];
            let jLen = trendDatas.length;
            let maxLine = self._maxLine;
            if (self._maxLineMinusArr.indexOf(i) >= 0) {
                maxLine -= 1;
            }
            for (let j = 0; j < jLen; j++) {
                let trendData = trendDatas[j];
                let winIndex = trendData.win || 0;
                let lineIndex = i;
                let rowIndex = j;
                if (j >= maxLine) {
                    lineIndex = j - maxLine + i + 1;
                    rowIndex = maxLine - 1;
                }
                let y = self._itemSize * self._maxLine;
                let ballX = (self._itemSize / 2) * (lineIndex * 2 + 1);
                let ballY = y - (self._itemSize / 2) * (rowIndex * 2 + 1);
                let ballInfo = {
                    x: ballX,
                    y: ballY,
                    win: winIndex,
                };
                self._dataInfoArr.push(ballInfo);
            }
        }
    }

    private drawCircleWithData() {
        let self = this;
        if (!cc.isValid(self.mainContent)) {
            return;
        }
        for (let i = 0, len = self._dataInfoArr.length; i < len; i++) {
            let ballInfo = self._dataInfoArr[i];
            let trendBall = cc.instantiate(self.dlBall);
            if (cc.isValid(trendBall)) {
                self.mainContent.addChild(trendBall);
            }
            self.updateTrendBall(trendBall, ballInfo, false);
        }
    }

    private updateUIWithTrendData(): void {
        let self = this;
        if (!cc.isValid(self.mainContent)) {
            return;
        }
        let childrenArr = self.mainContent.children;
        let childrenCount = childrenArr.length;
        let dataInfoCount = self._dataInfoArr.length;
        if (childrenCount - 1 > dataInfoCount) {
            let diffValue = childrenCount - dataInfoCount - 1;
            for (let i = 0; i < diffValue; i++) {
                let childNode = childrenArr[1];
                if (childNode) {
                    self.mainContent.removeChild(childNode);
                }
            }
        }
        for (let i = 0; i < dataInfoCount; i++) {
            let ballInfo = self._dataInfoArr[i];
            let ballNode = childrenArr[i + 1];
            if (ballNode == null) {
                ballNode = cc.instantiate(self.dlBall);
                self.mainContent.addChild(ballNode);
            }
            let shouldBlink = i == dataInfoCount - 1;
            self.updateTrendBall(ballNode, ballInfo, shouldBlink);
        }
    }

    private updateTrendBall(ballNode, ballInfo, shouldBlink) {
        ballNode.opacity = 255;
        SpriteManager.getInstance()
            .loadSpriteBundle(
                [
                    CommonSkin.LoadOnDemand.tk_room_quan_black,
                    CommonSkin.LoadOnDemand.tk_room_quan_red,
                ][ballInfo.win]
            )
            .then((res) => {
                ballNode.getComponent(cc.Sprite).spriteFrame = res;
            });
        ballNode.x = ballInfo.x;
        ballNode.y = ballInfo.y;
        if (shouldBlink) {
            let action = cc.blink(3, 5);
            ballNode.runAction(action);
        }
    }

    private updateViewState(isLoading: boolean): void {
        let self = this;
        if (isLoading) {
            if (cc.isValid(self.mainScrollView)) {
                self.mainScrollView.node.active = false;
            }
            if (cc.isValid(self.loadingNode)) {
                self.loadingNode.active = true;
                UIDepend.LabelManager.setLabelString(self.loadingNode, CommonLanguage.loading);
            }
        } else {
            if (cc.isValid(self.mainScrollView)) {
                self.mainScrollView.node.active = true;
            }
            if (cc.isValid(self.loadingNode)) {
                self.loadingNode.active = false;
                UIDepend.LabelManager.setLabelString(self.loadingNode, "");
            }
        }
    }
}
