    public getUserInfo(auto_login: boolean) {
        let req_header = new ApiProto.ReqHeader();
        let reqHeader: ReqHeader = HttpRequestUtil.getInstance().buildHeader(req_header);
        let userInfoReq = ApiProto.UserInfoReq.fromObject({
            header: reqHeader,
            auto_login: auto_login,
        });
        let req_plain = userInfoReq.serializeBinary();
        return req_plain;
    }

    public login(type: number, identity: string, password?: string, vcode?: string) {
        let req_header = new ApiProto.ReqHeader();
        let reqHeader: ReqHeader = HttpRequestUtil.getInstance().buildHeader(req_header);
        let userInfoReq = ApiProto.LoginReq.fromObject({
            header: reqHeader,
            identity_info: ApiProto.IdentityInfo.fromObject({
                type: type,
                identity: identity,
                cert: ApiProto.Cert.fromObject({
                    password: password,
                    vcode: vcode,
                }),
            }),
        });
        let req_plain = userInfoReq.serializeBinary();
        return req_plain;
    }

    public register(identity: string, password: string) {
        let req_header = new ApiProto.ReqHeader();
        let reqHeader: ReqHeader = HttpRequestUtil.getInstance().buildHeader(req_header);
        let appName = CommonDepend.AppParamManager.getAppName();
        let userInfoReq = ApiProto.RegisterReq.fromObject({
            header: reqHeader,
            identity_info: ApiProto.IdentityInfo.fromObject({
                type: ApiProto.IdentityType.AT_ACCOUNT_NO,
                identity: identity,
                cert: ApiProto.Cert.fromObject({
                    password: password,
                }),
            }),
            app_name: appName,
        });
        let req_plain = userInfoReq.serializeBinary();
        return req_plain;
    }

