import PopUpViewBase from "../../../../../../main/core/popUpViewUtils/PopUpViewBase";
import EnumActive from "../../../../../../main/core/ui/component/EnumActive";
import { UserManager } from "../../../../../../main/core/user/UserManager";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import UIMgr from "../../Component/base/UIMgr";
import CommonLanguage from "../../conf/lang/CommonLanguage";
import ApiMgr from "../../service/api/ApiMgr";
import { ApiProto } from "../../service/api/ApiProto";
import { FormatTestUtil } from "../../utils/FormatTestUtil";
import { InfoMosaicUtil } from "../../utils/InfoMosaicUtil";
import { CommonAccountUtil } from "../utils/CommonAccountUtil";
import { CommonUserLoginUiUtil } from "../utils/CommonUserLoginUiUtil";
import CommonSmsCode from "./CommonSmsCode";
import EnumController from "../../../../../../main/core/ui/component/EnumController";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CommonRetrievePassword extends PopUpViewBase {
    @property({ type: cc.EditBox, displayName: "" })
    accountEd: cc.EditBox = null;
    @property({ type: cc.EditBox, displayName: "" })
    phoneEd: cc.EditBox = null;
    @property({ type: cc.Label, displayName: "" })
    phone: cc.Label = null;

    @property({ type: cc.Node, displayName: "" })
    code: cc.Node = null;
    @property({ type: cc.EditBox, displayName: "" })
    newPass: cc.EditBox = null;
    @property({ type: cc.Node, displayName: "" })
    regioCodeEd: cc.Node = null;

    protected start(): void {
        let codeComp = this.code.getComponent(CommonSmsCode);
        codeComp.setGetSmsBtnClick(() => this.onGetSmsCode());
    }

    public init(): void {
        this.getComponentInChildren(EnumController).show4Name(
            CommonDepend.UserManager.phone && CommonDepend.UserManager.uid ? "phone" : "empty"
        );
        this.phone.string = InfoMosaicUtil.phone2Mosaic(UserManager.getInstance().phone);
    }

    private onGetSmsCode() {
        if (CommonDepend.UserManager.phone && UserManager.getInstance().uid) {
            const phone = UserManager.getInstance().phone;
            this.code.getComponent(CommonSmsCode).requestCode(phone);
        } else {
            this.verifyBindPhone();
        }
    }

    @UIMgr.applyLoadingUi
    private verifyBindPhone() {
        let account = lodash.toLower(this.accountEd.string);
        if (account && account.length >= 6) {
            if (FormatTestUtil.containInvaildSymbol(account)) {
                UIMgr.showToast(CommonLanguage.accountRuleError);
                return;
            }
        } else {
            UIMgr.showToast(CommonLanguage.accountRuleShort);
            return;
        }
        const phone = this.regioCodeEd.getComponent(cc.Label).string + this.phoneEd.string;
        return ApiMgr.getInstance()
            .verifyBindPhone({
                phone: phone.replace(/(\D+)/g, ""),
                account_no: lodash.toLower(this.accountEd.string),
            })
            .then(() => {
                this.code.getComponent(CommonSmsCode).requestCode(phone);
            });
    }

    @UIMgr.applyLoadingUi
    private async onRetrievePasswordBtnClicked() {
        let account = "";
        if (!CommonDepend.UserManager.phone || !UserManager.getInstance().uid) {
            account = lodash.toLower(this.accountEd.string);
            if (account && account.length >= 6) {
                if (FormatTestUtil.containInvaildSymbol(account)) {
                    UIMgr.showToast(CommonLanguage.accountRuleError);
                    return;
                }
            } else {
                UIMgr.showToast(CommonLanguage.accountRuleShort);
                return;
            }
        } else {
            account = CommonAccountUtil.getLastAccount()?.[2];
        }

        const code = this.code.getComponent(CommonSmsCode).code;

        if (!code || code.length < 6) {
            UIMgr.showToast(CommonLanguage.codeInvalid);
            return;
        }

        const phone = (
            this.code.getComponent(CommonSmsCode).phone ||
            this.regioCodeEd.getComponent(cc.Label).string + this.phoneEd.string
        ).replace(/(\D+)/g, "");
        const newPass = this.newPass.string;
        if (!newPass || !code) {
            return;
        }
        if (FormatTestUtil.containInvaildSymbol(this.newPass.string)) {
            UIMgr.showToast(CommonLanguage.passworldFormot);
            return;
        }

        if (this.newPass.string.length < 6) {
            UIMgr.showToast(CommonLanguage.passworldLenLimit);
            return;
        }

        return ApiMgr.getInstance()
            .resetPass({
                type: ApiProto.ResetPwdType.RETRIEVE_PWD,
                cert: {
                    vcode: code,
                },
                new_password: newPass,
                account_no: account,
                phone: phone,
            })
            .then(() => {
                UIMgr.showToast(CommonLanguage.findPasswordSuccess);
                this.close();
                CommonUserLoginUiUtil.launchHall();
            });
    }
}
