import ValueObserver from "../../../../../../main/core/utils/class/ValueObserver";
import { setInstance } from "../../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { ApiProto } from "../../service/api/ApiProto";

type ILoginInfo = ProtoObject<ApiProto.LoginInfo>

@setInstance
export default class CommonUserLoginInfoObserver extends ValueObserver<ILoginInfo> {
    protected onInit(): ILoginInfo {
        return {};
    }

    /**
     * 
     *  */
    public isAutoLogin() {
        return !!lodash.isEmpty(this.get());
    }
}
