import ValueObserver from "../../../../../main/core/utils/class/ValueObserver";
import { setInstance } from "../../../../../main/core/utils/decorators/InstanceClassDecorator";
import { UniverseProto } from "../universe/proto/UniverseProto";
import { GameType } from "../../../../../main/core/conf/GameType";

type TData = ProtoObject<UniverseProto.FeatureConfResp.GameInfo>[];

@setInstance
export default class CommonGameListObserver extends ValueObserver<TData> {
    protected onInit(): TData {
        return [];
    }

    public hasGame(gameId: GameType) {
        return !!this.get().find((v) => gameId === v.game_id);
    }
}
