import BaseServiceErrorHandler from "./base/BaseServiceErrorHandler";
import UIMgr from "../../../Component/base/UIMgr";

export class ServiceErrorShowToast extends BaseServiceErrorHandler {
    protected onCatchError(data: ServiceError.IError): void {
        if (data.message || CC_DEBUG) {
            UIMgr.showToast(data.message || ` code:${data.code}`);
        } else {
            CC_DEBUG && cc.error(`[ServiceError] code:${data.code},message: ${data.message}.`);
        }
    }
}
