
import { DialogActionType } from "../../../../../../main/core/conf/GameEnum";
import PopUpViewBase from "../../../../../../main/core/popUpViewUtils/PopUpViewBase";
import CommonLanguage from "../../conf/lang/CommonLanguage";
import GetAwardItem from "./GetAwardItem";
import { AwardItem } from "./GetAwardType";

const { ccclass, property } = cc._decorator;

@ccclass
export default class GetAwardDialog extends PopUpViewBase {
    @property(cc.Node)
    awardListNode: cc.Node = null;

    @property(cc.Layout)
    awardListLayout: cc.Layout = null;

    @property(cc.Node)
    confirmeBtn: cc.Node = null;

    @property(cc.Prefab)
    awardItemPerfab: cc.Prefab = null;

    @property(cc.Label)
    confirmeBtnLabel: cc.Label = null;

    @property(cc.Node)
    coinAnim: cc.Node = null;

    private dialogData;
    private isShowCoinAnim: boolean;

    public init(...args: any[]): void {
        let [awardList] = args;
        this.dialogData = awardList;
        this.refreshUI();
    }

    onEnable() { }

    refreshUI() {
        if (!this.dialogData) return;
        this.awardListNode.removeAllChildren();
        (this.dialogData as AwardItem[]).forEach((vo) => {
            let awardItemNode = cc.instantiate(this.awardItemPerfab);
            let awardItemComp = awardItemNode.getComponent(GetAwardItem);
            awardItemComp.setUIByVO(vo);
            this.awardListNode.addChild(awardItemNode);
            this.isShowCoinAnim = vo.isShowCoinAnim;
        });

        this.showCoinAnim();

        //i18n
        this.confirmeBtnLabel.string = CommonLanguage.confirm;
    }

    protected async onClose(): Promise<void> {
        this.executeDoneCallBack(DialogActionType.Close);
        this.hideCoinAnim();
    }

    executeDoneCallBack(actType: DialogActionType) {
        this.close(actType);
    }

    onConfirme() {
        this.executeDoneCallBack(DialogActionType.Confirm);
    }

    showCoinAnim() {
        if (this.isShowCoinAnim) {
            this.coinAnim.active = true;
        }
    }

    hideCoinAnim() {
        if (this.isShowCoinAnim) {
            this.coinAnim.active = false;
        }
    }

}
