import PopUpViewBase from "../../../../../../main/core/popUpViewUtils/PopUpViewBase";
import { EnumActiveType } from "../../../../../../main/core/ui/component/BaseEnumComp";
import EnumActive from "../../../../../../main/core/ui/component/EnumActive";
import { Injector } from "../../../../../../main/core/utils/decorators/Injector";
import { lodash } from "../../../../../../main/core/utils/NpmExport";
import { Tools } from "../../../../../../main/core/utils/Tools";
import UIMgr from "../../Component/base/UIMgr";
import CommonLanguage from "../../conf/lang/CommonLanguage";
import ApiMgr from "../../service/api/ApiMgr";
import FormatConstUtil from "../../utils/FormatConstUtil";
import { FormatTemplateUtil } from "../../utils/FormatTemplateUtil";
import { FormatTestUtil } from "../../utils/FormatTestUtil";
import CommonKycInfoObserver from "../obs/CommonKycInfoObserver";

const { ccclass, property } = cc._decorator;

@ccclass
export default class CommonKycInfo extends PopUpViewBase {
    @property({ type: cc.Node, displayName: "" })
    cpf: cc.Node = null;

    @property({ type: cc.Node, displayName: "" })
    email: cc.Node = null;

    @property({ type: cc.Node, displayName: "" })
    cep: cc.Node = null;

    @property({ type: cc.Node, displayName: "" })
    phone: cc.Node = null;

    @property({ type: cc.Node, displayName: "" })
    nameItem: cc.Node = null;

    @Injector.Inject(CommonKycInfoObserver)
    private kycObs: CommonKycInfoObserver = null;

    start() {
        this.kycObs.listenChange(this.onRefreshUi, this);
    }

    protected onDestroy(): void {
        try {
            this.kycObs.unListenChange(this.onRefreshUi, this);
        } catch {}
    }

    protected onRefreshUi() {
        let data = this.kycObs.get();
        if (lodash.isEmpty(data)) {
            return;
        }
        this.getComponentsInChildren(cc.EditBox).forEach((edit) => {
            let errorComp = edit.node.parent.getComponent(EnumActive);
            let nodeName = edit.node.parent.name;

            edit.node.targetOff(this);
            edit.node.on(
                "editing-did-ended",
                () => {
                    this[`on${lodash.capitalize(nodeName)}EditEnded`]?.(errorComp, edit);
                    this[`on${lodash.capitalize(nodeName)}EditSet`]?.(edit);
                    this[`onCheck${lodash.capitalize(nodeName)}Edit`]?.(errorComp, edit);
                },
                this
            );

            edit.node.on(
                "editing-did-began",
                () => {
                    this[`on${lodash.capitalize(nodeName)}EditBegin`]?.(edit);
                },
                this
            );
            this[`on${lodash.capitalize(nodeName)}EditSet`]?.(edit);
        });
    }

    init() {
        this.kycObs.query();
        this.getComponentsInChildren(EnumActive).forEach((c) => {
            c.activeIndex = EnumActiveType.HideAll;
        });
    }
    private onCpfEmptyToast() {
        UIMgr.showToast(CommonLanguage.cpfEmpty);
    }
    private onCpfEditSet(edit: cc.EditBox) {
        let str = this.kycObs.get("kyc_extra_info", {})?.in_kyc_extra?.CPF_OR_CNPJ ?? "";
        let format = FormatTemplateUtil.cpf(str);
        if (format == str) {
            format = FormatTemplateUtil.cnpj(str);
        }
        edit.maxLength = format.length;
        edit.string = format;
    }

    private onCpfEditBegin(edit: cc.EditBox) {
        let str = this.kycObs.get("kyc_extra_info", {})?.in_kyc_extra?.CPF_OR_CNPJ ?? "";
        edit.string = str;
        edit.maxLength = 14;
    }

    private onCheckCpfEdit(errorComp: EnumActive, edit: cc.EditBox) {
        let str = this.kycObs.get("kyc_extra_info", {})?.in_kyc_extra?.CPF_OR_CNPJ ?? "";
        if (str.length !== 14 && str.length !== 11 && edit.string) {
            errorComp.activeIndex = EnumActiveType.ShowFirstChild;
        } else {
            errorComp.activeIndex = EnumActiveType.HideAll;
        }
    }
    private onCpfEditEnded(errorComp: EnumActive, edit: cc.EditBox) {
        this.kycObs.modify({
            kyc_extra_info: {
                in_kyc_extra: {
                    CPF_OR_CNPJ: edit.string,
                },
            },
        });
    }
    private onEmailEditSet(edit: cc.EditBox) {
        let str = this.kycObs.get("email", "");
        edit.string = str;
    }
    private onEmailEmptyToast() {
        UIMgr.showToast(CommonLanguage.emailEmpty);
    }
    private onCheckEmailEdit(errorComp: EnumActive, edit: cc.EditBox) {
        if (!FormatTestUtil.testEmail(edit.string) && edit.string) {
            errorComp.activeIndex = EnumActiveType.ShowFirstChild;
        } else {
            errorComp.activeIndex = EnumActiveType.HideAll;
        }
    }

    private onEmailEditEnded(errorComp: EnumActive, edit: cc.EditBox) {
        this.kycObs.modify({
            email: edit.string,
        });
    }

    private onCepEditSet(edit: cc.EditBox) {
        let str = this.kycObs.get("kyc_extra_info", {})?.in_kyc_extra?.CEP ?? "";
        let format = FormatTemplateUtil.cep(str);
        edit.maxLength = format.length;
        edit.string = format;
    }

    private onCepEditBegin(edit: cc.EditBox) {
        let str = this.kycObs.get("kyc_extra_info", {})?.in_kyc_extra?.CEP ?? "";
        edit.string = str;
        edit.maxLength = 8;
    }

    private onCheckCepEdit(errorComp: EnumActive, edit: cc.EditBox) {
        let str = this.kycObs.get("kyc_extra_info", {})?.in_kyc_extra?.CEP ?? "";
        if (str.length != 8 && edit.string) {
            errorComp.activeIndex = EnumActiveType.ShowFirstChild;
        } else {
            errorComp.activeIndex = EnumActiveType.HideAll;
        }
    }

    private onCepEditEnded(errorComp: EnumActive, edit: cc.EditBox) {
        this.kycObs.modify({
            kyc_extra_info: {
                in_kyc_extra: {
                    CEP: edit.string,
                },
            },
        });
    }

    private onPhoneEmptyToast() {
        UIMgr.showToast(CommonLanguage.phoneEmpty);
    }
    private onPhoneEditSet(edit: cc.EditBox) {
        let str = this.kycObs.get("phone", "");
        let format = FormatTemplateUtil.phone(str);
        edit.maxLength = format.length;
        edit.string = format;
    }

    private onPhoneEditBegin(edit: cc.EditBox) {
        let str = this.kycObs.get("phone", "");
        edit.string = str;
        edit.maxLength = FormatConstUtil.MAX_PHONE_LEN;
    }
    private onCheckPhoneEdit(errorComp: EnumActive, edit: cc.EditBox) {
        let str = this.kycObs.get("phone", "");
        if (str.length < FormatConstUtil.MIN_PHONE_LEN && edit.string) {
            errorComp.activeIndex = EnumActiveType.ShowFirstChild;
        } else {
            errorComp.activeIndex = EnumActiveType.HideAll;
        }
    }
    private onPhoneEditEnded(errorComp: EnumActive, edit: cc.EditBox) {
        this.kycObs.modify({
            phone: edit.string,
        });
    }

    private onNameitemEmptyToast() {
        UIMgr.showToast(CommonLanguage.nameEmpty);
    }
    private onCheckNameitemEdit(errorComp: EnumActive, edit: cc.EditBox) {
        if (!Tools.isLimit(edit.string.length, 2, 200) && edit.string) {
            errorComp.activeIndex = EnumActiveType.ShowFirstChild;
        } else {
            errorComp.activeIndex = EnumActiveType.HideAll;
        }
    }
    private onNameitemEditSet(edit: cc.EditBox) {
        let str = this.kycObs.get("name", "");
        edit.string = str;
    }
    private onNameitemEditEnded(errorComp: EnumActive, edit: cc.EditBox) {
        this.kycObs.modify({
            name: edit.string,
        });
    }

    private getString(node: cc.Node) {
        return node.getComponentInChildren(cc.EditBox).string;
    }

    @UIMgr.applyLoadingUi
    private onComfirmeClicked(errorComp: EnumActive) {
        let result = this.getComponentsInChildren(cc.EditBox).every((edit, index) => {
            let errorComp = edit.node.parent.getComponent(EnumActive);
            let nodeName = edit.node.parent.name;
            this[`onCheck${lodash.capitalize(nodeName)}Edit`]?.(errorComp, edit);
            if (errorComp.activeIndex == EnumActiveType.ShowFirstChild || !edit.string) {
                this[`on${lodash.capitalize(nodeName)}EmptyToast`]?.();
                return false;
            }
            return true;
        });

        let comp = this.getComponentsInChildren(EnumActive).find((item) => {
            return item.activeIndex == EnumActiveType.ShowFirstChild;
        });
        if (!result) {
            // UIMgr.showToast(comp.curentNode.getComponent(cc.Label).string);
            return;
        }
        return ApiMgr.getInstance()
            .setKyc(
                this.kycObs.translate((data) => {
                    data.phone = CommonLanguage.phoneHead + data.phone;
                    return data;
                })
            )
            .then(() => {
                this.close();
            });
    }
}
