import ObjectLabel from "../../../../../../main/core/ui/component/ObjectLabel";
import { Injector } from "../../../../../../main/core/utils/decorators/Injector";
import Listener from "../../../../../../main/core/utils/Listener";
import { UniverseProto } from "../../universe/proto/UniverseProto";
import { CurrencyUtil } from "../../utils/CurrencyUtil";
import { CommonBroadCastEvents } from "../define/CommonBroadCastEvents";
import CommonBroadCastLogic from "../logic/CommonBroadCastLogic";
import SkeletonHelper from "../../../../../../main/core/utils/class/SkeletonHelper";
import { CommonBroadCastType } from "../define/CommonBroadCastType";
import { Tools } from "../../../../../../main/core/utils/Tools";
const { ccclass, property } = cc._decorator;

type TData = ProtoObject<UniverseProto.BroadCastNT> & { vipLevel?: number };

@ccclass
export default class CommonBroadCast extends cc.Component {
    @property(cc.Node)
    content: cc.Node = null;

    @property(ObjectLabel)
    protected label: ObjectLabel = null;

    @property(cc.Node)
    protected mask: cc.Node = null;

    @property(sp.Skeleton)
    protected anim: sp.Skeleton = null;

    @Injector.Inject(CommonBroadCastLogic)
    private logic: CommonBroadCastLogic = null;

    private pos: cc.Vec3 = null;

    /** Labelx */
    private _endMovePosX!: number;

    /** Label */
    private _isMoveIng!: boolean;

    start() {
        this._isMoveIng = false;

        cc.game.addPersistRootNode(this.node);
        this.content.active = false;
        this.anim.node.active = false;
        this.pos = this.content.position;
        Listener.listen(CommonBroadCastEvents.show, this.onShow, this);
        Listener.listen(CommonBroadCastEvents.position, this.onSetPosition, this);
        Listener.listen(CommonBroadCastEvents.reset, this.onResetPos, this);
        if (this.logic.getCurrent()) {
            this.onShow(this.logic.getCurrent());
        }
    }

    onResetPos() {
        this.content.position = this.pos;
    }

    public onSetPosition(position: cc.Vec3) {
        this.content.position = this.content.parent.convertToNodeSpaceAR(position);
    }

    protected onDestroy(): void {
        Listener.listenOffTarget(this);
        this.logic.destroy();
        this.unschedule(this._updateNoticeMove);
    }

    private onShow(data: TData) {
        if (this._isMoveIng) return;

        this.label.text = data.Template;
        const element = data.Element;
        let result: { [index: string]: string } = {};

        for (let key in element) {
            let value = element[key].Value;
            switch (key) {
                case `Amount`:
                case `GoldCount`: {
                    value = `<color=#ffcd60><b>${CurrencyUtil.formatMoney(
                        Number(value)
                    )}</b></color>`;
                    break;
                }
                default: {
                    value = `<color=#36af2d>${value}</color>`;
                    break;
                }
            }
            result[key] = value;
        }
        this.content.active = true;
        this.playAnim(data);
        this.label.object = result;

        // cc.tween(this.label.node)
        //     .set({
        //         x: -this.mask.width / 2,
        //     })
        //     .delay(0.5 + Math.random() * 0.5)
        //     .to((this.label.node.width + this.mask.width) / (this.mask.width / 4.5), {
        //         x: -this.mask.width / 2 - this.label.node.width,
        //     })
        //     .delay(0.1)
        //     .call(() => {
        //         this.logic.showFinish();
        //         this.content.active = false;
        //         this.anim.node.active = false;
        //     })
        //     .start();

        cc.tween(this.label.node)
            .set({ x: -this.mask.width / 2 })
            .delay(0.5 + Math.random() * 0.5)
            .call(() => {
                this._isMoveIng = true;
                this._endMovePosX = -this.mask.width / 2 - this.label.node.width;
                this.schedule(this._updateNoticeMove, 0);
            })
            .start();
    }

    /**
     * @zh 
     * @param dt
     */
    private _updateNoticeMove(dt: number): void {
        if (!this.label || !cc.isValid(this.label)) {
            this.unschedule(this._updateNoticeMove);
            return;
        }

        this.label.node.x -= 1;
        if (Math.floor(this.label.node.x) <= this._endMovePosX) {
            this.unschedule(this._updateNoticeMove);

            this.content.active = false;
            this.anim.node.active = false;
            this._isMoveIng = false;

            this.logic.showFinish();
        }
    }

    private playAnim(data: TData) {
        switch (data.BroadCastType) {
            case CommonBroadCastType.VipLevelUp: {
                this.playVipAnim(data);
                break;
            }
        }
    }

    /**
     * vip 
     * @param data
     * @returns
     */
    private playVipAnim(data: TData) {
        const level = data?.vipLevel ?? 0;
        if (!level) {
            return;
        }
        const help = new SkeletonHelper(this.anim);
        let animName = "";

        if (level <= 4) {
            return;
        } else if (level <= 7) {
            animName = "ordinary";
        } else {
            animName = "advanced";
        }
        this.anim.node.active = true;
        help.play(animName, {
            loop: true,
        }).then(() => {});
    }
    // update (dt) {}
}
