import { MoneyUtil } from "../../../../../../main/core/utils/MoneyUtil";

/**
 * 
 */
export namespace CurrencyINR {
    /**
     * 999999M
     * @param money
     */
    export function formatMoney(money: number): string {
        if (money >= 10000000) {
            money = money / 10000000;
            return MoneyUtil.getInstance().keepDigitPrecision(money) + "Cr";
        } else {
            return MoneyUtil.getInstance().keepDigitPrecision(money);
        }
    }

    export function formatChipMoney(money: number): string {
        if (money >= 10000000) {
            money = money / 10000000;
            return MoneyUtil.getInstance().keepDigitPrecision(money) + "Cr";
        } else if (money >= 10000) {
            money = money / 1000;
            return MoneyUtil.getInstance().keepDigitPrecision(money) + "K";
        } else {
            return MoneyUtil.getInstance().keepDigitPrecision(money).toString();
        }
    }
}
