export class CommonSocketUtil {
    private static _instance: CommonSocketUtil = null;

    public static getInstance() {
        if (!this._instance) {
            this._instance = new CommonSocketUtil();
        }

        return this._instance;
    }

    private constructor() {}

    /**
     * @protobufData websocketonmessageprotobuf
     * @desc  protobufData  messageId
     */
    public getMessageId(protobufData: ArrayBuffer) {
        let dv = new DataView(protobufData);
        let messageId = dv.getUint16(0, false);
        return messageId;
    }

    /**
     * @buffer proto
     * @desc  messageId 2 totalBuffer
     */
    public addMessageId(messageId: number, buffer: Uint8Array) {
        return this.add2byteToBuffer(messageId, buffer);
    }

    /**
     * @buffer proto
     * @desc  key 2 totalBuffer
     */
    public addCryptId(key, buffer) {
        return this.add2byteToBuffer(key, buffer);
    }

    private add2byteToBuffer(id: number, buffer: Uint8Array) {
        let totalBuffer = new Uint8Array(2 + buffer.byteLength);
        let indexBuffer = new ArrayBuffer(2);
        let dv = new DataView(indexBuffer);
        dv.setUint16(0, id, false);
        totalBuffer.set(new Uint8Array(indexBuffer), 0);
        totalBuffer.set(new Uint8Array(buffer), 2);
        return totalBuffer;
    }
}
